import React, { useState, useEffect } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import adminService from '../../services/adminService';
import DataTable from '../../components/admin/DataTable/DataTable';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import Button from '../../components/common/Button/Button';
import Modal from '../../components/common/Modal/Modal';
import Input from '../../components/common/Input/Input';
import { downloadInvoice } from '../../utils/invoiceGenerator';
import './OrdersManagementPage.css';

function OrdersManagementPage() {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [showOrderModal, setShowOrderModal] = useState(false);
  const [statusFilter, setStatusFilter] = useState('all');
  const [typeFilter, setTypeFilter] = useState('all');
  const [updating, setUpdating] = useState(false);

  const { user } = useAuth();

  // Transform backend status to frontend status
  const transformBackendStatus = (backendStatus) => {
    const statusMap = {
      'pending': 'Pending',
      'order placed': 'Order Placed',
      'cooking in progress': 'Cooking in Progress',
      'ready for pickup': 'Ready for Pickup',
      'out for delivery': 'Out for Delivery',
      'delivered': 'Delivered',
      'cancelled': 'Cancelled'
    };
    return statusMap[backendStatus] || backendStatus;
  };

  // Transform frontend status to backend status
  const transformFrontendStatus = (frontendStatus) => {
    const statusMap = {
      'Pending': 'pending',
      'Order Placed': 'order placed',
      'Cooking in Progress': 'cooking in progress',
      'Ready for Pickup': 'ready for pickup',
      'Out for Delivery': 'out for delivery',
      'Delivered': 'delivered',
      'Completed': 'delivered',
      'Cancelled': 'cancelled'
    };
    return statusMap[frontendStatus] || frontendStatus.toLowerCase();
  };

  useEffect(() => {
    if (user) {
      loadOrders();
    }
  }, [user]);

  // Reload orders when filters change
  useEffect(() => {
    if (user) {
      loadOrders();
    }
  }, [statusFilter, typeFilter]);

  const loadOrders = async (filters = {}) => {
    try {
      setLoading(true);
      setError('');

      // Always request all orders and filter on frontend for better UX
      // This avoids status mapping issues between frontend and backend
      const finalFilters = { ...filters, all: 'true' };

      console.log('Loading orders with filters:', finalFilters);
      const result = await adminService.getOrders(finalFilters);

      if (result.success) {
        // Transform the orders to match the expected format
        const transformedOrders = result.data.orders.map(order => ({
          id: order._id,
          customerName: order.customer?.name || order.customerInfo?.name || 'Guest',
          customerPhone: order.customer?.phone || order.customerInfo?.phone || 'N/A',
          type: order.orderType || 'pickup', // Keep the original orderType (delivery, pickup, dine in)
          items: order.items.map(item => ({
            name: item.menuItem?.name || 'Unknown Item',
            quantity: item.quantity,
            price: item.price
          })),
          total: order.total,
          status: transformBackendStatus(order.status),
          timestamp: order.createdAt,
          tableNumber: order.tableNumber,
          deliveryAddress: order.deliveryAddress ?
            `${order.deliveryAddress.street}, ${order.deliveryAddress.city}` : null,
          deliveryInstructions: order.specialInstructions,
          paymentMethod: order.paymentMethod || 'card'
        }));

        setOrders(transformedOrders);
        console.log('Orders loaded successfully:', transformedOrders);

        // Reset status filter if the selected status is no longer available
        if (statusFilter !== 'all') {
          const availableStatuses = [...new Set(transformedOrders.map(order => order.status))];
          if (!availableStatuses.includes(statusFilter)) {
            console.log(`Status filter '${statusFilter}' not found in orders, resetting to 'all'`);
            setStatusFilter('all');
          }
        }
      } else {
        setError('Failed to load orders');
      }
    } catch (err) {
      setError('Failed to load orders. Please try again.');
      console.error('Orders loading error:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleStatusUpdate = async (orderId, newStatus) => {
    try {
      setUpdating(true);
      const backendStatus = transformFrontendStatus(newStatus);
      console.log(`Updating order ${orderId} from ${newStatus} to backend status: ${backendStatus}`);

      const result = await adminService.updateOrderStatus(orderId, backendStatus);
      if (result.success) {
        setOrders(prevOrders =>
          prevOrders.map(order =>
            order.id === orderId ? { ...order, status: newStatus } : order
          )
        );
        if (selectedOrder && selectedOrder.id === orderId) {
          setSelectedOrder({ ...selectedOrder, status: newStatus });
        }
        console.log(`Order ${orderId} status updated successfully`);

        // Show inventory deduction results if order was delivered
        if (backendStatus === 'delivered' && result.data?.inventoryDeduction) {
          const { inventoryDeduction } = result.data;
          if (inventoryDeduction.deductions && inventoryDeduction.deductions.length > 0) {
            const deductionSummary = inventoryDeduction.deductions
              .map(d => `${d.ingredient}: -${d.quantityDeducted} ${d.unit}`)
              .join('\n');
            alert(`Order delivered successfully!\n\nInventory automatically updated:\n${deductionSummary}`);
          }
          if (inventoryDeduction.errors && inventoryDeduction.errors.length > 0) {
            const errorSummary = inventoryDeduction.errors
              .map(e => `${e.ingredient}: ${e.error}`)
              .join('\n');
            console.warn('Inventory deduction warnings:', errorSummary);
          }
        }
      }
    } catch (err) {
      console.error('Status update error:', err);
      setError('Failed to update order status. Please try again.');
    } finally {
      setUpdating(false);
    }
  };

  const getStatusOptions = (currentStatus, orderType) => {
    let allStatuses = [
      'Pending',
      'Order Placed',
      'Cooking in Progress'
    ];

    // Add type-specific status based on order type
    if (orderType === 'delivery') {
      allStatuses.push('Out for Delivery');
    } else if (orderType === 'pickup') {
      allStatuses.push('Ready for Pickup');
    } else if (orderType === 'dine in') {
      allStatuses.push('Ready for Pickup'); // For dine in, food is ready to be served
    } else if (orderType === 'online') { // Legacy support
      allStatuses.push('Out for Delivery');
    } else {
      allStatuses.push('Ready for Pickup'); // Default fallback
    }

    allStatuses.push('Delivered');
    allStatuses.push('Cancelled');

    const currentIndex = allStatuses.indexOf(currentStatus);
    return allStatuses.slice(currentIndex);
  };

  const formatTime = (timestamp) => {
    return new Date(timestamp).toLocaleString();
  };

  const getFilteredOrders = () => {
    const filtered = orders.filter(order => {
      const statusMatch = statusFilter === 'all' || order.status === statusFilter;
      const typeMatch = typeFilter === 'all' || order.type === typeFilter;
      return statusMatch && typeMatch;
    });

    console.log('Filtering orders:', {
      totalOrders: orders.length,
      statusFilter,
      typeFilter,
      filteredCount: filtered.length,
      availableStatuses: [...new Set(orders.map(o => o.status))],
      availableTypes: [...new Set(orders.map(o => o.type))]
    });

    return filtered;
  };

  // Get unique statuses from actual orders data with counts
  const getAvailableStatuses = () => {
    const statuses = [...new Set(orders.map(order => order.status))];
    return statuses.sort();
  };

  // Get status count for display
  const getStatusCount = (status) => {
    return orders.filter(order => order.status === status).length;
  };

  const handleDownloadInvoice = async (order) => {
    try {
      const invoiceData = {
        id: order.id,
        customerName: order.customerName,
        customerPhone: order.customerPhone || 'N/A',
        items: order.items,
        orderType: order.type, // Use the actual order type (delivery, pickup, dine in)
        tableNumber: order.tableNumber,
        paymentMethod: order.paymentMethod || 'card',
        total: order.total,
        timestamp: order.timestamp
      };

      await downloadInvoice(invoiceData);
    } catch (error) {
      console.error('Error downloading invoice:', error);
      setError('Failed to download invoice');
    }
  };

  const ordersColumns = [
    {
      key: 'id',
      title: 'Order ID',
      width: '120px'
    },
    {
      key: 'customerName',
      title: 'Customer',
      width: '150px'
    },
    {
      key: 'type',
      title: 'Type',
      render: (type) => {
        const typeDisplay = {
          'delivery': ' Delivery',
          'pickup': ' Take Away',
          'dine in': ' Dine In',
          'online': ' Delivery' // Legacy support
        };
        return (
          <span className={`order-type-badge order-type-badge--${type.replace(' ', '-')}`}>
            {typeDisplay[type] || ' Take Away'}
          </span>
        );
      },
      width: '120px'
    },
    {
      key: 'items',
      title: 'Items',
      render: (items) => `${items.length} item${items.length !== 1 ? 's' : ''}`,
      width: '80px'
    },
    {
      key: 'total',
      title: 'Total',
      type: 'currency',
      align: 'right',
      width: '100px'
    },
    {
      key: 'status',
      title: 'Status',
      type: 'status',
      width: '150px'
    },
    {
      key: 'timestamp',
      title: 'Order Time',
      render: (timestamp) => formatTime(timestamp),
      width: '150px'
    },
    {
      key: 'actions',
      title: 'Actions',
      sortable: false,
      render: (_, order) => (
        <div className="order-actions">
          <Button
            variant="outline"
            size="small"
            onClick={(e) => {
              e.stopPropagation();
              setSelectedOrder(order);
              setShowOrderModal(true);
            }}
          >
            View
          </Button>
          <Button
            variant="outline"
            size="small"
            onClick={async (e) => {
              e.stopPropagation();
              await handleDownloadInvoice(order);
            }}
            className="download-btn"
          >
            
          </Button>
          {order.status !== 'Delivered' && order.status !== 'Cancelled' && (
            <select
              className="status-select"
              value={order.status}
              onChange={(e) => {
                e.stopPropagation();
                handleStatusUpdate(order.id, e.target.value);
              }}
              disabled={updating}
              onClick={(e) => e.stopPropagation()}
            >
              {getStatusOptions(order.status, order.type).map(status => (
                <option key={status} value={status}>
                  {status}
                </option>
              ))}
            </select>
          )}
        </div>
      ),
      width: '200px'
    }
  ];

  const filteredOrders = getFilteredOrders();

  if (loading) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-loading">
            <LoadingSpinner size="large" />
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-error">
            <h1>Error Loading Orders</h1>
            <p>{error}</p>
            <Button onClick={loadOrders} variant="primary">
              Try Again
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="orders-management-page">
      <div className="container">
        <div className="orders-header">
          <div className="orders-title-section">
            <h1>Orders Management</h1>
            <p>Manage all incoming orders and update their status</p>
          </div>

          <div className="orders-stats">
            <div className="stat-item">
              <span className="stat-value">{orders.length}</span>
              <span className="stat-label">Total Orders</span>
            </div>
            <div className="stat-item">
              <span className="stat-value">
                {orders.filter(o => o.status === 'Order Placed').length}
              </span>
              <span className="stat-label">New Orders</span>
            </div>
            <div className="stat-item">
              <span className="stat-value">
                {orders.filter(o => o.status === 'Cooking in Progress').length}
              </span>
              <span className="stat-label">In Progress</span>
            </div>
          </div>
        </div>

        <div className="orders-filters">
          <div className="filter-group">
            <label>Status:</label>
            <select
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              className="filter-select"
            >
              <option value="all">All Statuses ({orders.length})</option>
              {getAvailableStatuses().map(status => (
                <option key={status} value={status}>
                  {status} ({getStatusCount(status)})
                </option>
              ))}
            </select>
          </div>

          <div className="filter-group">
            <label>Type:</label>
            <select
              value={typeFilter}
              onChange={(e) => setTypeFilter(e.target.value)}
              className="filter-select"
            >
              <option value="all">All Types</option>
              <option value="delivery">Delivery</option>
              <option value="take away">Take Away</option>
              <option value="dine in">Dine In</option>
            </select>
          </div>

          <Button
            variant="outline"
            onClick={loadOrders}
            disabled={loading}
          >
             Refresh
          </Button>
        </div>

        <DataTable
          data={filteredOrders}
          columns={ordersColumns}
          title={`Orders (${filteredOrders.length})`}
          searchable={true}
          sortable={true}
          pagination={true}
          pageSize={15}
          emptyMessage="No orders found"
          onRowClick={(order) => {
            setSelectedOrder(order);
            setShowOrderModal(true);
          }}
        />

        {/* Order Details Modal */}
        <Modal
          isOpen={showOrderModal}
          onClose={() => {
            setShowOrderModal(false);
            setSelectedOrder(null);
          }}
          title={`Order Details - ${selectedOrder?.id}`}
          size="large"
        >
          {selectedOrder && (
            <div className="order-details-modal">
              <div className="order-details-header">
                <div className="order-info-grid">
                  <div className="info-item">
                    <label>Customer:</label>
                    <span>{selectedOrder.customerName}</span>
                  </div>
                  <div className="info-item">
                    <label>Order Type:</label>
                    <span className={`order-type-badge order-type-badge--${selectedOrder.type.replace(' ', '-')}`}>
                      {(() => {
                        const typeDisplay = {
                          'delivery': ' Delivery',
                          'pickup': ' Take Away',
                          'dine in': ' Dine In', 'online': ' Delivery' // Legacy support
                        }; return typeDisplay[selectedOrder.type] || ' Take Away';
                      })()}
                    </span>
                  </div>
                  <div className="info-item">
                    <label>Status:</label>
                    <span className={`status-badge status-badge--${selectedOrder.status.toLowerCase().replace(/\s+/g, '-')}`}>
                      {selectedOrder.status}
                    </span>
                  </div>
                  <div className="info-item">
                    <label>Order Time:</label>
                    <span>{formatTime(selectedOrder.timestamp)}</span>
                  </div>
                  <div className="info-item">
                    <label>Total:</label>
                    <span className="total-amount">{selectedOrder.total.toFixed(2)}</span>
                  </div>
                </div>
              </div>

              <div className="order-items-section">
                <h3>Order Items</h3>
                <div className="order-items-list">
                  {selectedOrder.items.map((item, index) => (
                    <div key={index} className="order-item-row">
                      <div className="item-details">
                        <span className="item-name">{item.name}</span>
                        <span className="item-quantity">Qty: {item.quantity}</span>
                      </div>
                      <span className="item-price">{(item.price * item.quantity).toFixed(2)}</span>
                    </div>
                  ))}
                </div>
              </div>

              {selectedOrder.deliveryAddress && (
                <div className="delivery-section">
                  <h3>Delivery Information</h3>
                  <p>{selectedOrder.deliveryAddress}</p>
                  {selectedOrder.deliveryInstructions && (
                    <div className="delivery-instructions">
                      <strong>Instructions:</strong> {selectedOrder.deliveryInstructions}
                    </div>
                  )}
                </div>
              )}

              {selectedOrder.tableNumber && (
                <div className="pickup-section">
                  <h3>Take Away Information</h3>
                  <p>Table Number: {selectedOrder.tableNumber}</p>
                </div>
              )}

              <div className="order-actions-section">
                <h3>Update Status</h3>
                <div className="status-update-controls">
                  {getStatusOptions(selectedOrder.status, selectedOrder.type).map(status => (
                    <Button
                      key={status}
                      variant={status === selectedOrder.status ? 'primary' : 'outline'}
                      onClick={() => handleStatusUpdate(selectedOrder.id, status)}
                      disabled={updating || status === selectedOrder.status}
                      className="status-btn"
                    >
                      {status}
                    </Button>
                  ))}
                </div>
              </div>

              <div className="invoice-actions-section">
                <h3>Invoice</h3>
                <Button
                  variant="outline"
                  onClick={async () => await handleDownloadInvoice(selectedOrder)}
                >
                   Download Invoice
                </Button>
              </div>
            </div>
          )}
        </Modal>
      </div>
    </div>
  );
}

export default OrdersManagementPage;