import api from './api';

const settingsService = {
  // Get all settings or by category
  getSettings: async (category = null) => {
    try {
      const url = category ? `/settings?category=${category}` : '/settings';
      const response = await api.get(url);
      return response.data;
    } catch (error) {
      console.error('Error fetching settings:', error);
      throw error;
    }
  },

  // Get specific setting
  getSetting: async (category, key) => {
    try {
      const response = await api.get(`/settings/${category}/${key}`);
      return response.data.value;
    } catch (error) {
      console.error('Error fetching setting:', error);
      throw error;
    }
  },

  // Update settings for a category
  updateSettings: async (category, settings) => {
    try {
      const response = await api.put(`/settings/${category}`, settings);
      return response.data;
    } catch (error) {
      console.error('Error updating settings:', error);
      throw error;
    }
  },

  // Delete a specific setting
  deleteSetting: async (category, key) => {
    try {
      const response = await api.delete(`/settings/${category}/${key}`);
      return response.data;
    } catch (error) {
      console.error('Error deleting setting:', error);
      throw error;
    }
  },

  // Get business hours specifically
  getBusinessHours: async () => {
    try {
      const response = await api.get('/settings/business/openingHours');
      return response.data.value;
    } catch (error) {
      // Return default hours if not found
      return {
        monday: { open: '07:00', close: '19:00', closed: false },
        tuesday: { open: '07:00', close: '19:00', closed: false },
        wednesday: { open: '07:00', close: '19:00', closed: false },
        thursday: { open: '07:00', close: '19:00', closed: false },
        friday: { open: '07:00', close: '20:00', closed: false },
        saturday: { open: '08:00', close: '20:00', closed: false },
        sunday: { open: '08:00', close: '18:00', closed: false }
      };
    }
  },

  // Format hours for display
  formatHours: (hours) => {
    const formatTime = (time) => {
      const [hour, minute] = time.split(':');
      const hourNum = parseInt(hour);
      const ampm = hourNum >= 12 ? 'PM' : 'AM';
      const displayHour = hourNum === 0 ? 12 : hourNum > 12 ? hourNum - 12 : hourNum;
      return `${displayHour}:${minute} ${ampm}`;
    };

    const daysOrder = ['monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday', 'sunday'];
    const dayNames = {
      monday: 'Monday',
      tuesday: 'Tuesday', 
      wednesday: 'Wednesday',
      thursday: 'Thursday',
      friday: 'Friday',
      saturday: 'Saturday',
      sunday: 'Sunday'
    };

    return daysOrder.map(day => {
      const dayHours = hours[day];
      if (dayHours.closed) {
        return `${dayNames[day]}: Closed`;
      }
      return `${dayNames[day]}: ${formatTime(dayHours.open)} - ${formatTime(dayHours.close)}`;
    });
  },

  // Check if maintenance mode is enabled
  isMaintenanceMode: async () => {
    try {
      const response = await api.get('/settings/system/maintenanceMode');
      return response.data.value;
    } catch (error) {
      console.error('Error checking maintenance mode:', error);
      return false;
    }
  },

  // Get maintenance message
  getMaintenanceMessage: async () => {
    try {
      const response = await api.get('/settings/system/maintenanceMessage');
      return response.data.value;
    } catch (error) {
      console.error('Error getting maintenance message:', error);
      return 'Sorry, we are not delivering currently. Please try again later!';
    }
  },

  // Get business information specifically
  getBusinessInfo: async () => {
    try {
      const response = await api.get('/settings?category=business');
      return response.data.business || {};
    } catch (error) {
      console.error('Error fetching business info:', error);
      return {
        name: 'Sanctum Cafe',
        address: '123 Coffee Street, Brew City, BC 12345',
        phone: '(555) 123-CAFE',
        email: 'info@sanctumcafe.com',
        website: 'https://sanctumcafe.com',
        description: 'A cozy neighborhood cafe serving premium coffee and fresh food.'
      };
    }
  }
};

export default settingsService;