// src/utils/helpers.js

/**
 * Formats a given price to a currency string.
 * @param {number} price - The price to format.
 * @param {string} currency - The currency code (e.g., 'USD', 'INR').
 * @param {string} locale - The locale string (e.g., 'en-US', 'en-IN').
 * @returns {string} The formatted currency string.
 */
export const formatCurrency = (price, currency = 'INR', locale = 'en-IN') => {
  return new Intl.NumberFormat(locale, {
    style: 'currency',
    currency: currency,
  }).format(price);
};

/**
 * Simple currency formatter for quick use
 * @param {number} amount - The amount to format
 * @returns {string} The formatted currency string in INR
 */
export const formatPrice = (amount) => {
  return new Intl.NumberFormat('en-IN', {
    style: 'currency',
    currency: 'INR'
  }).format(amount);
};

/**
 * Capitalizes the first letter of a string.
 * @param {string} string - The input string.
 * @returns {string} The string with the first letter capitalized.
 */
export const capitalizeFirstLetter = (string) => {
  if (!string) return '';
  return string.charAt(0).toUpperCase() + string.slice(1);
};

/**
 * Debounces a function, so it only runs after a certain delay.
 * Useful for input fields to prevent excessive API calls.
 * @param {function} func - The function to debounce.
 * @param {number} delay - The delay in milliseconds.
 * @returns {function} The debounced function.
 */
export const debounce = (func, delay) => {
  let timeout;
  return function(...args) {
    const context = this;
    clearTimeout(timeout);
    timeout = setTimeout(() => func.apply(context, args), delay);
  };
};

/**
 * Throttles a function, so it only runs at most once per a given time period.
 * Useful for scroll events or resize events.
 * @param {function} func - The function to throttle.
 * @param {number} limit - The time limit in milliseconds.
 * @returns {function} The throttled function.
 */
export const throttle = (func, limit) => {
  let inThrottle;
  return function(...args) {
    const context = this;
    if (!inThrottle) {
      func.apply(context, args);
      inThrottle = true;
      setTimeout(() => (inThrottle = false), limit);
    }
  };
};