import React, { useState, useEffect } from 'react';
import { useCart } from '../../contexts/CartContext';
import { useSettings } from '../../contexts/SettingsContext';
import menuService from '../../services/menuService';
import MenuCard from '../../components/common/MenuCard/MenuCard';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import Button from '../../components/common/Button/Button';
import Input from '../../components/common/Input/Input';
import './MenuPage.css';

function MenuPage() {
  const [menuItems, setMenuItems] = useState([]);
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('All');
  const [searchTerm, setSearchTerm] = useState('');
  const [sortBy, setSortBy] = useState('name');
  const [quantities, setQuantities] = useState({});

  const { getTotalItems } = useCart();
  const { getSetting, loading: settingsLoading } = useSettings();

  useEffect(() => {
    loadMenuData();
  }, []);

  // Add a refresh function that can be called manually
  const refreshMenu = () => {
    loadMenuData();
  };

  const loadMenuData = async () => {
    try {
      setLoading(true);
      const [menuResponse, categoriesResponse] = await Promise.all([
        menuService.getAllMenuItems(),
        menuService.getCategories()
      ]);

      if (menuResponse.success) {
        setMenuItems(menuResponse.data);
        // Initialize quantities for each item
        const initialQuantities = {};
        menuResponse.data.forEach(item => {
          initialQuantities[item.id] = 1;
        });
        setQuantities(initialQuantities);
      }

      if (categoriesResponse.success) {
        setCategories(['All', ...categoriesResponse.data]);
      }
    } catch (err) {
      setError('Failed to load menu items. Please try again.');
      console.error('Error loading menu:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleQuantityChange = (itemId, newQuantity) => {
    setQuantities(prev => ({
      ...prev,
      [itemId]: newQuantity
    }));
  };

  const filteredAndSortedItems = menuItems
    .filter(item => {
      const matchesCategory = selectedCategory === 'All' || item.category === selectedCategory;
      const matchesSearch = item.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           item.description.toLowerCase().includes(searchTerm.toLowerCase());
      return matchesCategory && matchesSearch;
    })
    .sort((a, b) => {
      switch (sortBy) {
        case 'price-low':
          return a.price - b.price;
        case 'price-high':
          return b.price - a.price;
        case 'prep-time':
          return a.preparationTime - b.preparationTime;
        case 'name':
        default:
          return a.name.localeCompare(b.name);
      }
    });

  if (loading || settingsLoading) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-loading">
            <LoadingSpinner size="large" />
          </div>
        </div>
      </div>
    );
  }

  // Check if maintenance mode is enabled
  const isMaintenanceMode = getSetting('system', 'maintenanceMode');
  const maintenanceMessage = getSetting('system', 'maintenanceMessage') || 'Sorry, we are not delivering currently. Please try again later!';

  if (isMaintenanceMode) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="maintenance-mode">
            <div className="maintenance-content">
              <div className="maintenance-icon"></div>
              <h1>Service Temporarily Unavailable</h1>
              <p className="maintenance-message">{maintenanceMessage}</p>
              <div className="maintenance-actions">
                <Button 
                  onClick={() => window.location.reload()} 
                  variant="primary"
                >
                  Refresh Page
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-error">
            <h1>Error Loading Menu</h1>
            <p>{error}</p>
            <Button onClick={loadMenuData} variant="primary">
              Try Again
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="menu-page">
      <div className="container">
        <div className="menu-header">
          <div className="menu-title-section">
            <h1>Our Menu</h1>
            <p>Discover our carefully crafted selection of coffee, food, and treats</p>
            {getTotalItems() > 0 && (
              <div className="cart-indicator">
                <span className="cart-count">{getTotalItems()}</span>
                <span>items in cart</span>
              </div>
            )}
          </div>
          <div className="menu-actions">
            <Button 
              onClick={refreshMenu} 
              variant="outline" 
              disabled={loading}
              className="refresh-btn"
            >
              {loading ? 'Loading...' : 'Refresh Menu'}
            </Button>
          </div>
        </div>

        <div className="menu-filters">
          <div className="search-section">
            <Input
              type="text"
              placeholder="Search menu items..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="search-input"
            />
          </div>

          <div className="filter-section">
            <div className="category-filters">
              {categories.map(category => (
                <Button
                  key={category}
                  variant={selectedCategory === category ? 'primary' : 'outline'}
                  onClick={() => setSelectedCategory(category)}
                  className="category-btn"
                >
                  {category}
                </Button>
              ))}
            </div>

            <div className="sort-section">
              <select
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value)}
                className="sort-select"
              >
                <option value="name">Sort by Name</option>
                <option value="price-low">Price: Low to High</option>
                <option value="price-high">Price: High to Low</option>
                <option value="prep-time">Preparation Time</option>
              </select>
            </div>
          </div>
        </div>

        <div className="menu-results">
          <div className="results-info">
            <span>{filteredAndSortedItems.length} items found</span>
            {selectedCategory !== 'All' && (
              <span className="category-indicator">in {selectedCategory}</span>
            )}
            {searchTerm && (
              <span className="search-indicator">matching "{searchTerm}"</span>
            )}
          </div>

          {filteredAndSortedItems.length === 0 ? (
            <div className="no-results">
              <h3>No items found</h3>
              <p>Try adjusting your search or filter criteria</p>
              <Button
                onClick={() => {
                  setSearchTerm('');
                  setSelectedCategory('All');
                }}
                variant="outline"
              >
                Clear Filters
              </Button>
            </div>
          ) : (
            <div className="menu-grid">
              {filteredAndSortedItems.map(item => (
                <MenuCard
                  key={item.id}
                  item={item}
                  quantity={quantities[item.id] || 1}
                  onQuantityChange={(newQuantity) => handleQuantityChange(item.id, newQuantity)}
                />
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default MenuPage;