import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useCart } from '../../contexts/CartContext';
import { useOrder } from '../../contexts/OrderContext';
import { useAuth } from '../../contexts/AuthContext';
import Button from '../../components/common/Button/Button';
import Input from '../../components/common/Input/Input';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import { formatPrice } from '../../utils/helpers';
import './CheckoutPage.css';

function CheckoutPage() {
  const { cartItems, getSubtotal, clearCart } = useCart();
  const { placeOrder } = useOrder();
  const { user } = useAuth();
  const navigate = useNavigate();

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [orderType, setOrderType] = useState('delivery');
  
  const [deliveryInfo, setDeliveryInfo] = useState({
    address: '',
    city: '',
    zipCode: '',
    phone: '',
    instructions: ''
  });

  const [paymentMethod, setPaymentMethod] = useState('cash_on_delivery');
  
  // Commented out for future payment gateway integration
  // const [paymentInfo, setPaymentInfo] = useState({
  //   cardNumber: '',
  //   expiryDate: '',
  //   cvv: '',
  //   cardholderName: '',
  //   billingAddress: '',
  //   billingCity: '',
  //   billingZipCode: ''
  // });

  const subtotal = getSubtotal();
  const deliveryFee = orderType === 'delivery' ? (subtotal >= 25 ? 0 : 3.99) : 0;
  const tax = subtotal * 0.08;
  const total = subtotal + deliveryFee;

  const handleDeliveryInfoChange = (e) => {
    setDeliveryInfo({
      ...deliveryInfo,
      [e.target.name]: e.target.value
    });
  };

  // Commented out for future payment gateway integration
  // const handlePaymentInfoChange = (e) => {
  //   setPaymentInfo({
  //     ...paymentInfo,
  //     [e.target.name]: e.target.value
  //   });
  // };

  const validateForm = () => {
    if (orderType === 'delivery') {
      if (!deliveryInfo.address || !deliveryInfo.city || !deliveryInfo.zipCode || !deliveryInfo.phone) {
        setError('Please fill in all delivery information fields');
        return false;
      }
    }

    // Payment method validation - Cash on Delivery is always valid
    if (!paymentMethod) {
      setError('Please select a payment method');
      return false;
    }

    // Commented out for future payment gateway integration
    // if (paymentMethod === 'card') {
    //   if (!paymentInfo.cardNumber || !paymentInfo.expiryDate || !paymentInfo.cvv || !paymentInfo.cardholderName) {
    //     setError('Please fill in all payment information fields');
    //     return false;
    //   }
    // }

    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');

    if (!validateForm()) {
      return;
    }

    setLoading(true);

    try {
      const orderData = {
        customerId: user.id,
        customerName: user.name,
        items: cartItems,
        orderType,
        deliveryInfo: orderType === 'delivery' ? deliveryInfo : null,
        paymentMethod: paymentMethod,
        paymentStatus: paymentMethod === 'cash_on_delivery' ? 'pending' : 'completed',
        subtotal,
        deliveryFee,
        tax,
        total,
        estimatedDeliveryTime: orderType === 'delivery' ? '25-35 minutes' : '10-15 minutes'
      };

      const result = await placeOrder(orderData);
      
      if (result.success) {
        clearCart();
        navigate('/customer/orders', { 
          state: { 
            orderPlaced: true, 
            orderId: result.order.id 
          } 
        });
      } else {
        console.error('Order placement failed:', result.error);
        setError(result.error || 'Failed to place order. Please try again.');
      }
    } catch (err) {
      setError('An error occurred while placing your order. Please try again.');
      console.error('Checkout error:', err);
    } finally {
      setLoading(false);
    }
  };

  if (cartItems.length === 0) {
    return (
      <div className="checkout-page">
        <div className="container">
          <div className="empty-checkout">
            <h1>Your cart is empty</h1>
            <p>Add some items to your cart before checking out.</p>
            <Button onClick={() => navigate('/customer/menu')} variant="primary">
              Browse Menu
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="checkout-page">
      <div className="container">
        <div className="checkout-header">
          <h1>Checkout</h1>
          <p>Complete your order</p>
        </div>

        <form onSubmit={handleSubmit} className="checkout-form">
          <div className="checkout-content">
            <div className="checkout-main">
              {/* Order Type Selection */}
              <div className="checkout-section">
                <h2>Order Type</h2>
                <div className="order-type-selection">
                  <label className={`order-type-option ${orderType === 'delivery' ? 'selected' : ''}`}>
                    <input
                      type="radio"
                      name="orderType"
                      value="delivery"
                      checked={orderType === 'delivery'}
                      onChange={(e) => setOrderType(e.target.value)}
                    />
                    <div className="order-type-content">
                      <div className="order-type-icon"></div>
                      <div>
                        <div className="order-type-title">Delivery</div>
                        <div className="order-type-subtitle">25-35 minutes</div>
                      </div>
                    </div>
                  </label>
                  
                  <label className={`order-type-option ${orderType === 'take away' ? 'selected' : ''}`}>
                    <input
                      type="radio"
                      name="orderType"
                      value="take away"
                      checked={orderType === 'take away'}
                      onChange={(e) => setOrderType(e.target.value)}
                    />
                    <div className="order-type-content">
                      <div className="order-type-icon"></div>
                      <div>
                        <div className="order-type-title">Take Away</div>
                        <div className="order-type-subtitle">10-15 minutes</div>
                      </div>
                    </div>
                  </label>
                </div>
              </div>

              {/* Delivery Information */}
              {orderType === 'delivery' && (
                <div className="checkout-section">
                  <h2>Delivery Information</h2>
                  <div className="form-grid">
                    <Input
                      label="Street Address"
                      name="address"
                      value={deliveryInfo.address}
                      onChange={handleDeliveryInfoChange}
                      required
                      className="full-width"
                    />
                    
                    <Input
                      label="City"
                      name="city"
                      value={deliveryInfo.city}
                      onChange={handleDeliveryInfoChange}
                      required
                    />
                    
                    <Input
                      label="ZIP Code"
                      name="zipCode"
                      value={deliveryInfo.zipCode}
                      onChange={handleDeliveryInfoChange}
                      required
                    />
                    
                    <Input
                      label="Phone Number"
                      name="phone"
                      type="tel"
                      value={deliveryInfo.phone}
                      onChange={handleDeliveryInfoChange}
                      required
                      className="full-width"
                    />
                    
                    <Input
                      label="Delivery Instructions (Optional)"
                      name="instructions"
                      type="textarea"
                      value={deliveryInfo.instructions}
                      onChange={handleDeliveryInfoChange}
                      placeholder="e.g., Ring doorbell, Leave at door, etc."
                      className="full-width"
                    />
                  </div>
                </div>
              )}

              {/* Payment Method */}
              <div className="checkout-section">
                <h2>Payment Method</h2>
                <div className="payment-method-selection">
                  <label className={`payment-method-option ${paymentMethod === 'cash_on_delivery' ? 'selected' : ''}`}>
                    <input
                      type="radio"
                      name="paymentMethod"
                      value="cash_on_delivery"
                      checked={paymentMethod === 'cash_on_delivery'}
                      onChange={(e) => setPaymentMethod(e.target.value)}
                    />
                    <div className="payment-method-content">
                      <div className="payment-method-icon"></div>
                      <div>
                        <div className="payment-method-title">Cash on Delivery</div>
                        <div className="payment-method-subtitle">Pay when your order arrives</div>
                      </div>
                    </div>
                  </label>
                  
                  {/* Commented out for future payment gateway integration */}
                  {/* <label className={`payment-method-option ${paymentMethod === 'card' ? 'selected' : ''}`}>
                    <input
                      type="radio"
                      name="paymentMethod"
                      value="card"
                      checked={paymentMethod === 'card'}
                      onChange={(e) => setPaymentMethod(e.target.value)}
                    />
                    <div className="payment-method-content">
                      <div className="payment-method-icon"></div>
                      <div>
                        <div className="payment-method-title">Credit/Debit Card</div>
                        <div className="payment-method-subtitle">Pay online securely</div>
                      </div>
                    </div>
                  </label> */}
                </div>
                
                <div className="payment-notice">
                  <p> Currently, we only accept Cash on Delivery. Online payment options will be available soon!</p>
                </div>
              </div>
            </div>

            {/* Order Summary Sidebar */}
            <div className="checkout-sidebar">
              <div className="order-summary">
                <h3>Order Summary</h3>
                
                <div className="order-items">
                  {cartItems.map(item => (
                    <div key={item.id} className="order-item">
                      <div className="order-item-info">
                        <span className="order-item-name">{item.name}</span>
                        <span className="order-item-quantity">x{item.quantity}</span>
                      </div>
                      <span className="order-item-price">
                        {(item.price * item.quantity).toFixed(2)}
                      </span>
                    </div>
                  ))}
                </div>
                
                <div className="order-totals">
                  <div className="total-line">
                    <span>Subtotal</span>
                    <span>{subtotal.toFixed(2)}</span>
                  </div>
                  
                  {orderType === 'delivery' && (
                    <div className="total-line">
                      <span>Delivery Fee</span>
                      <span>
                        {deliveryFee === 0 ? 'FREE' : `${deliveryFee.toFixed(2)}`}
                      </span>
                    </div>
                  )}
                  
                  {/* <div className="total-line">
                    <span>Tax</span>
                    <span>{tax.toFixed(2)}</span>
                  </div> */}
                  
                  <div className="total-line total-final">
                    <span>Total</span>
                    <span>{total.toFixed(2)}</span>
                  </div>
                </div>
                
                {error && <div className="error-message">{error}</div>}
                
                <Button
                  type="submit"
                  variant="primary"
                  size="large"
                  fullWidth
                  disabled={loading}
                  className="place-order-btn"
                >
                  {loading ? (
                    <>
                      <LoadingSpinner size="small" />
                      Processing...
                    </>
                  ) : (
                    `Place Order - ${total.toFixed(2)}`
                  )}
                </Button>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}

export default CheckoutPage;