import React from 'react';
import Button from '../Button/Button';
import './SimpleMenuCard.css';

function SimpleMenuCard({ item, quantity = 0, onAdd, onRemove, onQuantityChange }) {
  return (
    <div className={`simple-menu-card ${!item.isAvailable ? 'simple-menu-card--unavailable' : ''}`}>
      <div className="simple-menu-card__content">
        <div className="simple-menu-card__info">
          <h4 className="simple-menu-card__name">{item.name}</h4>
          <p className="simple-menu-card__price">{item.price.toFixed(2)}</p>
          {item.description && (
            <p className="simple-menu-card__description">{item.description}</p>
          )}
        </div>
        
        <div className="simple-menu-card__actions">
          {quantity > 0 ? (
            <div className="simple-menu-card__quantity-controls">
              <button
                className="quantity-btn quantity-btn--remove"
                onClick={() => onQuantityChange(item.id, quantity - 1)}
                disabled={!item.isAvailable}
              >
                -
              </button>
              <span className="quantity-display">{quantity}</span>
              <button
                className="quantity-btn quantity-btn--add"
                onClick={() => onQuantityChange(item.id, quantity + 1)}
                disabled={!item.isAvailable}
              >
                +
              </button>
            </div>
          ) : (
            <Button
              variant="primary"
              size="small"
              onClick={() => onAdd(item)}
              disabled={!item.isAvailable}
              className="simple-add-btn"
            >
              {!item.isAvailable ? 'Unavailable' : 'Add'}
            </Button>
          )}
        </div>
      </div>
    </div>
  );
}

export default SimpleMenuCard;