import React, { useState } from 'react';
import Button from '../../common/Button/Button';
import Modal from '../../common/Modal/Modal';
import './OrderCard.css';

function OrderCard({ order, onStatusUpdate }) {
  const [showDetails, setShowDetails] = useState(false);
  const [updating, setUpdating] = useState(false);

  const handleStatusUpdate = async (newStatus) => {
    setUpdating(true);
    try {
      await onStatusUpdate(order._id, newStatus);
    } catch (error) {
      console.error('Error updating status:', error);
    } finally {
      setUpdating(false);
    }
  };

  const openGoogleMaps = () => {
    const address = encodeURIComponent(order.deliveryAddress);
    const url = `https://www.google.com/maps/search/?api=1&query=${address}`;
    window.open(url, '_blank');
  };

  const callCustomer = () => {
    window.location.href = `tel:${order.customerPhone}`;
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'Order Placed':
        return '#f39c12';
      case 'Cooking in Progress':
        return '#e67e22';
      case 'On the Way to Delivery':
        return '#3498db';
      case 'Delivered':
        return '#27ae60';
      default:
        return '#95a5a6';
    }
  };

  const canPickup = order.status === 'Cooking in Progress';
  const canDeliver = order.status === 'On the Way to Delivery';
  const isDelivered = order.status === 'Delivered';

  return (
    <>
      <div className="order-card">
        <div className="order-header">
          <div className="order-info">
            <h3>Order #{order.orderNumber || order._id.slice(-6)}</h3>
            <span 
              className="status-badge" 
              style={{ backgroundColor: getStatusColor(order.status) }}
            >
              {order.status}
            </span>
          </div>
          <div className="order-amount">
            <span className="amount">{order.totalAmount?.toFixed(2) || '0.00'}</span>
          </div>
        </div>

        <div className="customer-info">
          <div className="customer-details">
            <h4>{order.customerName}</h4>
            <p className="phone">{order.customerPhone}</p>
          </div>
          <div className="customer-actions">
            <Button 
              onClick={callCustomer}
              className="call-btn"
              title="Call Customer"
            >
               Call
            </Button>
          </div>
        </div>

        <div className="delivery-address">
          <div className="address-info">
            <h5>Delivery Address:</h5>
            <p>{order.deliveryAddress}</p>
          </div>
          <Button 
            onClick={openGoogleMaps}
            className="navigate-btn"
            title="Open in Google Maps"
          >
             Navigate
          </Button>
        </div>

        <div className="order-items-preview">
          <h5>Items ({order.items?.length || 0}):</h5>
          <div className="items-list">
            {order.items?.slice(0, 2).map((item, index) => (
              <span key={index} className="item-preview">
                {item.quantity}x {item.name}
              </span>
            ))}
            {order.items?.length > 2 && (
              <span className="more-items">
                +{order.items.length - 2} more
              </span>
            )}
          </div>
          <Button 
            onClick={() => setShowDetails(true)}
            className="details-btn"
          >
            View Details
          </Button>
        </div>

        <div className="order-actions">
          {canPickup && (
            <Button 
              onClick={() => handleStatusUpdate('On the Way to Delivery')}
              disabled={updating}
              className="pickup-btn"
            >
              {updating ? 'Updating...' : 'Mark as Picked Up'}
            </Button>
          )}
          
          {canDeliver && (
            <Button 
              onClick={() => handleStatusUpdate('Delivered')}
              disabled={updating}
              className="deliver-btn"
            >
              {updating ? 'Updating...' : 'Mark as Delivered'}
            </Button>
          )}
          
          {isDelivered && (
            <div className="delivered-status">
               Order Delivered
            </div>
          )}
        </div>

        <div className="order-meta">
          <span className="order-time">
            Ordered: {new Date(order.createdAt).toLocaleString()}
          </span>
          {order.estimatedDeliveryTime && (
            <span className="delivery-time">
              Est. Delivery: {new Date(order.estimatedDeliveryTime).toLocaleString()}
            </span>
          )}
        </div>
      </div>

      {showDetails && (
        <Modal 
          isOpen={showDetails} 
          onClose={() => setShowDetails(false)}
          title={`Order #${order.orderNumber || order._id.slice(-6)} Details`}
        >
          <div className="order-details-modal">
            <div className="customer-section">
              <h4>Customer Information</h4>
              <p><strong>Name:</strong> {order.customerName}</p>
              <p><strong>Phone:</strong> {order.customerPhone}</p>
              <p><strong>Email:</strong> {order.customerEmail || 'Not provided'}</p>
            </div>

            <div className="delivery-section">
              <h4>Delivery Information</h4>
              <p><strong>Address:</strong> {order.deliveryAddress}</p>
              <p><strong>Special Instructions:</strong> {order.specialInstructions || 'None'}</p>
            </div>

            <div className="items-section">
              <h4>Order Items</h4>
              <div className="detailed-items-list">
                {order.items?.map((item, index) => (
                  <div key={index} className="detailed-item">
                    <div className="item-info">
                      <span className="item-name">{item.name}</span>
                      <span className="item-quantity">Qty: {item.quantity}</span>
                    </div>
                    <div className="item-price">
                      {(item.price * item.quantity).toFixed(2)}
                    </div>
                  </div>
                ))}
              </div>
              <div className="order-total">
                <strong>Total: {order.totalAmount?.toFixed(2) || '0.00'}</strong>
              </div>
            </div>

            <div className="payment-section">
              <h4>Payment Information</h4>
              <p><strong>Method:</strong> {order.paymentMethod || 'Online Payment'}</p>
              <p><strong>Status:</strong> {order.paymentStatus || 'Paid'}</p>
            </div>
          </div>
        </Modal>
      )}
    </>
  );
}

export default OrderCard;