import { jsPDF } from 'jspdf';
import autoTable from 'jspdf-autotable';

// Utility: Draw emoji text to canvas and return as image data URL
const drawEmojiOnly = async (emoji, fontSize = 32) => {
  const canvas = document.createElement('canvas');
  const ctx = canvas.getContext('2d');

  ctx.font = `${fontSize}px "Segoe UI Emoji", "Noto Color Emoji", sans-serif`;
  const { width } = ctx.measureText(emoji);

  canvas.width = width;
  canvas.height = fontSize * 1.2;

  ctx.font = `${fontSize}px "Segoe UI Emoji", "Noto Color Emoji", sans-serif`;
  ctx.textBaseline = 'top';
  ctx.fillStyle = '#000';
  ctx.fillText(emoji, 0, 0);

  return canvas.toDataURL('image/png');
};

const formatPrice = (price) => `${Number(price).toFixed(2)}`;
const currencyFormat = (amount) => `Rs. ${Number(amount).toFixed(2)}`;

export const generateInvoice = async (orderData) => {
  const {
    id,
    customerName,
    customerPhone,
    items,
    orderType,
    tableNumber,
    paymentMethod,
    total,
    timestamp,
  } = orderData;

  const doc = new jsPDF({ unit: 'pt' });
  let currentY = 60;

  // Header Title
  doc.setFont('helvetica', 'bold');
  doc.setFontSize(22);
  doc.text('The Sanctum Cafe', 300, currentY, { align: 'center' });
  currentY += 50;

  // Helper to draw emoji + text
  const drawLabelWithEmoji = async (emoji, label) => {
    const imgData = await drawEmojiOnly(emoji);
    const imgSize = 16;
    doc.addImage(imgData, 'PNG', 40, currentY, imgSize, imgSize);
    doc.setFont('helvetica', 'normal');
    doc.setFontSize(12);
    doc.text(label, 40 + imgSize + 6, currentY + 12);
    currentY += 24;
  };

  const formattedDate = new Date(timestamp).toLocaleString();

  await drawLabelWithEmoji('', `Invoice #${id}`);
  await drawLabelWithEmoji('', `Date: ${formattedDate}`);
  await drawLabelWithEmoji('', `Customer: ${customerName}`);
  if (customerPhone) await drawLabelWithEmoji('', `Phone: ${customerPhone}`);
  await drawLabelWithEmoji('', `Order Type: ${orderType === 'dine in' ? 'Dine In' : 'Take Away'}`);
  if (orderType === 'dine in' && tableNumber)
    await drawLabelWithEmoji('', `Table No: ${tableNumber}`);
  await drawLabelWithEmoji('', `Payment: ${paymentMethod}`);

  currentY += 40;

  // Table of items
  const tableBody = items.map((item) => [
    item.name,
    item.quantity.toString(),
    currencyFormat(item.price),
    currencyFormat(item.price * item.quantity),
  ]);

  autoTable(doc, {
    startY: currentY,
    head: [['Item', 'Qty', 'Price', 'Total']],
    body: tableBody,
    theme: 'grid',
    styles: {
      font: 'helvetica',
      fontSize: 10,
      cellPadding: 6,
    },
    headStyles: {
       fillColor: [41, 128, 185],
      textColor: [255, 255, 255],
      fontStyle: 'bold',
    },
    columnStyles: {
      0: { cellWidth: 200 },
      1: { halign: 'center' },
      2: { halign: 'right' },
      3: { halign: 'right' },
    },
    didDrawPage: async (data) => {
      const finalY = data.cursor.y + 20;

      // Total
      doc.setFont('helvetica', 'bold');
      doc.setFontSize(12);
      doc.setTextColor(0);
      doc.text(`Grand Total: ${currencyFormat(total)}`, doc.internal.pageSize.getWidth() - 40, finalY, { align: 'right' });
      currentY += 40;
      // Footer with emoji
      const thankYouEmoji = await drawEmojiOnly('');
      doc.addImage(thankYouEmoji, 'PNG', 240, finalY + 25, 16, 16);
      doc.setFont('helvetica', 'italic');
      doc.setFontSize(10);
      doc.setTextColor(100);
      doc.text('Thank you for dining with us!', 260, finalY + 38);
    },
  });

  return doc;
};

export const downloadInvoice = async (orderData) => {
  const doc = await generateInvoice(orderData);
  doc.save(`invoice-${orderData.id}.pdf`);
};
