import React, { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useAuth } from '../../../contexts/AuthContext';
import { useCart } from '../../../contexts/CartContext';
import './Header.css';

function Header() {
  const { user, logout } = useAuth();
  const { getTotalItems } = useCart();
  const location = useLocation();
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const isActive = (path) => location.pathname === path;

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  const closeMobileMenu = () => {
    setIsMobileMenuOpen(false);
  };

  // Manage body scroll when mobile menu is open
  useEffect(() => {
    if (isMobileMenuOpen) {
      document.body.classList.add('mobile-menu-open');
    } else {
      document.body.classList.remove('mobile-menu-open');
    }

    // Cleanup on unmount
    return () => {
      document.body.classList.remove('mobile-menu-open');
    };
  }, [isMobileMenuOpen]);

  // Close mobile menu on route change
  useEffect(() => {
    closeMobileMenu();
  }, [location.pathname]);

  return (
    <header className="app-header">
      <nav>
        <Link to="/" className="logo" onClick={closeMobileMenu}>
           The Sanctum Cafe
        </Link>
        
        <button 
          className="mobile-menu-toggle"
          onClick={toggleMobileMenu}
          aria-label="Toggle mobile menu"
        >
          {isMobileMenuOpen ? '' : ''}
        </button>
        
        <div className={`mobile-menu ${isMobileMenuOpen ? 'mobile-menu--open' : ''}`}>
          <div className="nav-links">
            {user ? (
              <>
                {user.role === 'customer' && (
                  <>
                    <Link 
                      to="/customer/menu" 
                      className={isActive('/customer/menu') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Menu
                    </Link>
                    <Link 
                      to="/customer/cart" 
                      className={isActive('/customer/cart') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Cart {getTotalItems() > 0 && `(${getTotalItems()})`}
                    </Link>
                    <Link 
                      to="/customer/orders" 
                      className={isActive('/customer/orders') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      My Orders
                    </Link>
                    <Link 
                      to="/in-cafe/menu" 
                      className={isActive('/in-cafe/menu') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      In-Cafe Menu
                    </Link>
                  </>
                )}
                
                {user.role === 'delivery' && (
                  <>
                    <Link 
                      to="/delivery/dashboard" 
                      className={isActive('/delivery/dashboard') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Dashboard
                    </Link>
                    <Link 
                      to="/delivery/orders" 
                      className={isActive('/delivery/orders') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Assigned Orders
                    </Link>
                  </>
                )}
                
                {user.role === 'admin' && (
                  <>
                    <Link 
                      to="/admin/dashboard" 
                      className={isActive('/admin/dashboard') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Dashboard
                    </Link>
                    <Link 
                      to="/admin/take-order" 
                      className={isActive('/admin/take-order') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Take Order
                    </Link>
                    <Link 
                      to="/admin/orders" 
                      className={isActive('/admin/orders') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Orders
                    </Link>
                    <Link 
                      to="/admin/menu-items" 
                      className={isActive('/admin/menu-items') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Menu Items
                    </Link>
                    <Link 
                      to="/admin/inventory" 
                      className={isActive('/admin/inventory') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Inventory
                    </Link>
                    <Link 
                      to="/admin/employees" 
                      className={isActive('/admin/employees') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Employees
                    </Link>
                    <Link 
                      to="/admin/customers" 
                      className={isActive('/admin/customers') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Analytics
                    </Link>
                    <Link 
                      to="/admin/settings" 
                      className={isActive('/admin/settings') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Settings
                    </Link>
                  </>
                )}
                
                {user.role === 'employee' && (
                  <>
                    <Link 
                      to="/employee/dashboard" 
                      className={isActive('/employee/dashboard') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Dashboard
                    </Link>
                    <Link 
                      to="/employee/take-order" 
                      className={isActive('/employee/take-order') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Take Order
                    </Link>
                    <Link 
                      to="/employee/orders" 
                      className={isActive('/employee/orders') ? 'active' : ''}
                      onClick={closeMobileMenu}
                    >
                      Orders
                    </Link>
                  </>
                )}
              </>
            ) : (
              <>
                <Link 
                  to="/login" 
                  className={isActive('/login') ? 'active' : ''}
                  onClick={closeMobileMenu}
                >
                  Login
                </Link>
                <Link 
                  to="/register" 
                  className={isActive('/register') ? 'active' : ''}
                  onClick={closeMobileMenu}
                >
                  Register
                </Link>
              </>
            )}
          </div>
          
          {user && (
            <div className="user-info">
              <span className="user-welcome">
                Welcome, {user.username}
              </span>
              <span className="user-role">{user.role}</span>
              <button 
                onClick={() => {
                  logout();
                  closeMobileMenu();
                }} 
                className="logout-button"
              >
                Logout
              </button>
            </div>
          )}
        </div>
        
        {/* Desktop nav-links and user-info */}
        <div className="desktop-nav">
          <div className="nav-links">
            {user ? (
              <>
                {user.role === 'customer' && (
                  <>
                    <Link 
                      to="/customer/menu" 
                      className={isActive('/customer/menu') ? 'active' : ''}
                    >
                      Menu
                    </Link>
                    <Link 
                      to="/customer/cart" 
                      className={isActive('/customer/cart') ? 'active' : ''}
                    >
                      Cart {getTotalItems() > 0 && `(${getTotalItems()})`}
                    </Link>
                    <Link 
                      to="/customer/orders" 
                      className={isActive('/customer/orders') ? 'active' : ''}
                    >
                      My Orders
                    </Link>
                    <Link 
                      to="/in-cafe/menu" 
                      className={isActive('/in-cafe/menu') ? 'active' : ''}
                    >
                      In-Cafe Menu
                    </Link>
                  </>
                )}
                
                {user.role === 'delivery' && (
                  <>
                    <Link 
                      to="/delivery/dashboard" 
                      className={isActive('/delivery/dashboard') ? 'active' : ''}
                    >
                      Dashboard
                    </Link>
                    <Link 
                      to="/delivery/orders" 
                      className={isActive('/delivery/orders') ? 'active' : ''}
                    >
                      Assigned Orders
                    </Link>
                  </>
                )}
                
                {user.role === 'admin' && (
                  <>
                    <Link 
                      to="/admin/dashboard" 
                      className={isActive('/admin/dashboard') ? 'active' : ''}
                    >
                      Dashboard
                    </Link>
                    <Link 
                      to="/admin/take-order" 
                      className={isActive('/admin/take-order') ? 'active' : ''}
                    >
                      Take Order
                    </Link>
                    <Link 
                      to="/admin/orders" 
                      className={isActive('/admin/orders') ? 'active' : ''}
                    >
                      Orders
                    </Link>
                    <Link 
                      to="/admin/menu-items" 
                      className={isActive('/admin/menu-items') ? 'active' : ''}
                    >
                      Menu Items
                    </Link>
                    <Link 
                      to="/admin/inventory" 
                      className={isActive('/admin/inventory') ? 'active' : ''}
                    >
                      Inventory
                    </Link>
                    <Link 
                      to="/admin/employees" 
                      className={isActive('/admin/employees') ? 'active' : ''}
                    >
                      Employees
                    </Link>
                    <Link 
                      to="/admin/customers" 
                      className={isActive('/admin/customers') ? 'active' : ''}
                    >
                      Analytics
                    </Link>
                    <Link 
                      to="/admin/settings" 
                      className={isActive('/admin/settings') ? 'active' : ''}
                    >
                      Settings
                    </Link>
                  </>
                )}
                
                {user.role === 'employee' && (
                  <>
                    <Link 
                      to="/employee/dashboard" 
                      className={isActive('/employee/dashboard') ? 'active' : ''}
                    >
                      Dashboard
                    </Link>
                    <Link 
                      to="/employee/take-order" 
                      className={isActive('/employee/take-order') ? 'active' : ''}
                    >
                      Take Order
                    </Link>
                    <Link 
                      to="/employee/orders" 
                      className={isActive('/employee/orders') ? 'active' : ''}
                    >
                      Orders
                    </Link>
                  </>
                )}
              </>
            ) : (
              <>
                <Link 
                  to="/login" 
                  className={isActive('/login') ? 'active' : ''}
                >
                  Login
                </Link>
                <Link 
                  to="/register" 
                  className={isActive('/register') ? 'active' : ''}
                >
                  Register
                </Link>
              </>
            )}
          </div>
          
          {user && (
            <div className="user-info">
              <span className="user-welcome">
                Welcome, {user.username}
              </span>
              <span className="user-role">{user.role}</span>
              <button 
                onClick={logout} 
                className="logout-button"
              >
                Logout
              </button>
            </div>
          )}
        </div>
      </nav>
    </header>
  );
}

export default Header;