import React, { createContext, useState, useContext } from 'react';
import orderService from '../services/orderService';
import { useAuth } from './AuthContext';
import notificationService from '../services/notificationService';

const OrderContext = createContext(null);

// Helper function to get user-friendly status messages
const getStatusMessage = (status) => {
  const messages = {
    'pending': 'Your order has been received and is being prepared.',
    'confirmed': 'Your order has been confirmed and is being prepared.',
    'preparing': 'Your order is being prepared by our kitchen staff.',
    'ready': 'Your order is ready for pickup or delivery.',
    'out_for_delivery': 'Your order is on its way to you.',
    'delivered': 'Your order has been delivered. Enjoy your meal!',
    'completed': 'Your order has been completed. Thank you!',
    'cancelled': 'Your order has been cancelled. Please contact us if you have questions.'
  };
  return messages[status] || `Your order status has been updated to ${status}.`;
};

export const OrderProvider = ({ children }) => {
  const [orders, setOrders] = useState([]);
  const [currentOrder, setCurrentOrder] = useState(null); // For tracking a single active order
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Place a new order
  const placeOrder = async (orderDetails) => {
    try {
      setLoading(true);
      setError(null);
      console.log('Placing order:', orderDetails);
      
      const result = await orderService.placeOrder(orderDetails);
      
      if (result.success) {
        const newOrder = result.data;
        setOrders((prevOrders) => [...prevOrders, newOrder]);
        setCurrentOrder(newOrder); // Set the newly placed order as the current active order
        
        // Send notification to admin and employees
        try {
          await notificationService.sendOrderNotification(newOrder, ['admin', 'employee']);
          console.log('Order notification sent to admin/employees');
        } catch (notificationError) {
          console.error('Failed to send order notification:', notificationError);
          // Don't fail the order placement if notification fails
        }
        
        console.log('Order placed successfully:', newOrder);
        return { success: true, order: newOrder };
      } else {
        setError(result.error);
        return { success: false, error: result.error };
      }
    } catch (err) {
      const errorMessage = err.response?.data?.message || err.message || 'Failed to place order';
      setError(errorMessage);
      console.error('Order placement error:', err);
      return { success: false, error: errorMessage };
    } finally {
      setLoading(false);
    }
  };

  // Fetch orders (for customer tracking or admin view)
  const fetchOrders = async (userId, role, filters = {}) => {
    try {
      setLoading(true);
      setError(null);
      console.log(`Fetching orders for ${role} (ID: ${userId})`);
      
      let result;
      if (role === 'admin' || role === 'employee') {
        // Admin/employee can see all orders
        result = await orderService.getAllOrders(filters);
      } else {
        // Regular users see only their orders
        result = await orderService.getMyOrders(filters);
      }
      
      if (result.success) {
        const fetchedOrders = result.data.orders;
        setOrders(fetchedOrders);
        console.log('Orders fetched:', fetchedOrders);
        return { success: true, orders: fetchedOrders, pagination: result.data.pagination };
      } else {
        setError(result.error);
        return { success: false, error: result.error };
      }
    } catch (err) {
      const errorMessage = err.response?.data?.message || err.message || 'Failed to fetch orders';
      setError(errorMessage);
      console.error('Orders fetch error:', err);
      return { success: false, error: errorMessage };
    } finally {
      setLoading(false);
    }
  };

  // Update order status (primarily for admin/delivery)
  const updateOrderStatus = async (orderId, newStatus, notes = '') => {
    try {
      setLoading(true);
      setError(null);
      console.log(`Updating order ${orderId} status to: ${newStatus}`);
      
      const result = await orderService.updateOrderStatus(orderId, newStatus, notes);
      
      if (result.success) {
        const updatedOrder = result.data;
        setOrders((prevOrders) =>
          prevOrders.map((order) =>
            order.id === orderId ? updatedOrder : order
          )
        );
        if (currentOrder && currentOrder.id === orderId) {
          setCurrentOrder(updatedOrder);
        }
        
        // Send status update notification to customer
        try {
          const statusMessage = getStatusMessage(newStatus);
          await notificationService.sendStatusUpdateNotification(
            orderId, 
            updatedOrder.customerId, 
            newStatus, 
            statusMessage
          );
          console.log('Status update notification sent to customer');
        } catch (notificationError) {
          console.error('Failed to send status update notification:', notificationError);
        }
        
        console.log(`Order ${orderId} status updated to ${newStatus}`);
        return { success: true, orderId, newStatus, order: updatedOrder };
      } else {
        setError(result.error);
        return { success: false, error: result.error };
      }
    } catch (err) {
      const errorMessage = err.response?.data?.message || err.message || 'Failed to update order status';
      setError(errorMessage);
      console.error('Order status update error:', err);
      return { success: false, error: errorMessage };
    } finally {
      setLoading(false);
    }
  };

  // Placeholder for assigning delivery person
  const assignDeliveryPerson = async (orderId, deliveryPersonId) => {
    console.log(`Assigning order ${orderId} to delivery person ${deliveryPersonId}`);
    // Simulate API call
    return new Promise((resolve) => {
      setTimeout(() => {
        setOrders((prevOrders) =>
          prevOrders.map((order) =>
            order.id === orderId ? { ...order, assignedTo: deliveryPersonId } : order
          )
        );
        console.log(`Order ${orderId} assigned to ${deliveryPersonId}`);
        resolve({ success: true, orderId, deliveryPersonId });
      }, 500);
    });
  };

  return (
    <OrderContext.Provider
      value={{
        orders,
        currentOrder,
        placeOrder,
        fetchOrders,
        updateOrderStatus,
        assignDeliveryPerson,
      }}
    >
      {children}
    </OrderContext.Provider>
  );
};

export const useOrder = () => {
  return useContext(OrderContext);
};