import React, { useState } from 'react';
import Button from '../Button/Button';
import './PWAGuide.css';

function PWAGuide({ onClose }) {
  const [currentStep, setCurrentStep] = useState(0);

  const installSteps = {
    chrome: [
      {
        title: "Open Chrome Menu",
        description: "Click the three dots menu in the top right corner",
        icon: ""
      },
      {
        title: "Install App",
        description: "Look for 'Install Sanctum Cafe' or 'Add to Home Screen'",
        icon: ""
      },
      {
        title: "Confirm Installation",
        description: "Click 'Install' in the popup dialog",
        icon: ""
      }
    ],
    safari: [
      {
        title: "Open Share Menu",
        description: "Tap the share button at the bottom of the screen",
        icon: ""
      },
      {
        title: "Add to Home Screen",
        description: "Scroll down and tap 'Add to Home Screen'",
        icon: ""
      },
      {
        title: "Customize and Add",
        description: "Edit the name if needed and tap 'Add'",
        icon: ""
      }
    ],
    edge: [
      {
        title: "Open Edge Menu",
        description: "Click the three dots menu in the top right corner",
        icon: ""
      },
      {
        title: "Apps Menu",
        description: "Click on 'Apps' in the menu",
        icon: ""
      },
      {
        title: "Install App",
        description: "Click 'Install this site as an app'",
        icon: ""
      }
    ]
  };

  const [selectedBrowser, setSelectedBrowser] = useState('chrome');
  const steps = installSteps[selectedBrowser];

  const features = [
    {
      icon: "",
      title: "Faster Loading",
      description: "App loads instantly from your device"
    },
    {
      icon: "",
      title: "Works Offline",
      description: "Browse menu and place orders without internet"
    },
    {
      icon: "",
      title: "Push Notifications",
      description: "Get notified about order status and promotions"
    },
    {
      icon: "",
      title: "Home Screen Access",
      description: "Launch directly from your home screen"
    },
    {
      icon: "",
      title: "Less Storage",
      description: "Uses less space than traditional apps"
    },
    {
      icon: "",
      title: "Auto Updates",
      description: "Always get the latest features automatically"
    }
  ];

  return (
    <div className="pwa-guide-overlay">
      <div className="pwa-guide">
        <div className="pwa-guide-header">
          <h2> Install Sanctum Cafe App</h2>
          <button className="close-btn" onClick={onClose}></button>
        </div>

        <div className="pwa-guide-content">
          <div className="guide-section">
            <h3>Why Install Our App?</h3>
            <div className="features-grid">
              {features.map((feature, index) => (
                <div key={index} className="feature-card">
                  <span className="feature-icon">{feature.icon}</span>
                  <div className="feature-text">
                    <h4>{feature.title}</h4>
                    <p>{feature.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>

          <div className="guide-section">
            <h3>Installation Instructions</h3>
            
            <div className="browser-selector">
              <button
                className={`browser-btn ${selectedBrowser === 'chrome' ? 'active' : ''}`}
                onClick={() => setSelectedBrowser('chrome')}
              >
                Chrome
              </button>
              <button
                className={`browser-btn ${selectedBrowser === 'safari' ? 'active' : ''}`}
                onClick={() => setSelectedBrowser('safari')}
              >
                Safari
              </button>
              <button
                className={`browser-btn ${selectedBrowser === 'edge' ? 'active' : ''}`}
                onClick={() => setSelectedBrowser('edge')}
              >
                Edge
              </button>
            </div>

            <div className="installation-steps">
              {steps.map((step, index) => (
                <div 
                  key={index} 
                  className={`step-card ${index === currentStep ? 'active' : ''}`}
                  onClick={() => setCurrentStep(index)}
                >
                  <div className="step-number">{index + 1}</div>
                  <div className="step-icon">{step.icon}</div>
                  <div className="step-content">
                    <h4>{step.title}</h4>
                    <p>{step.description}</p>
                  </div>
                </div>
              ))}
            </div>

            <div className="step-navigation">
              <Button
                variant="outline"
                onClick={() => setCurrentStep(Math.max(0, currentStep - 1))}
                disabled={currentStep === 0}
              >
                Previous
              </Button>
              <span className="step-indicator">
                {currentStep + 1} of {steps.length}
              </span>
              <Button
                variant="outline"
                onClick={() => setCurrentStep(Math.min(steps.length - 1, currentStep + 1))}
                disabled={currentStep === steps.length - 1}
              >
                Next
              </Button>
            </div>
          </div>

          <div className="guide-section">
            <h3>Need Help?</h3>
            <div className="help-content">
              <p>
                If you're having trouble installing the app, try refreshing the page or 
                contact our support team for assistance.
              </p>
              <div className="help-actions">
                <Button variant="outline" onClick={() => window.location.reload()}>
                   Refresh Page
                </Button>
                <Button variant="outline" onClick={() => window.open('mailto:support@sanctumcafe.com')}>
                   Contact Support
                </Button>
              </div>
            </div>
          </div>
        </div>

        <div className="pwa-guide-footer">
          <Button variant="primary" onClick={onClose}>
            Got It!
          </Button>
        </div>
      </div>
    </div>
  );
}

export default PWAGuide;