import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import adminService from '../../services/adminService';
import StatCard from '../../components/admin/StatCard/StatCard';
import DataTable from '../../components/admin/DataTable/DataTable';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import Button from '../../components/common/Button/Button';
import InventoryAlerts from '../../components/admin/InventoryAlerts/InventoryAlerts';
import { useNotifications } from '../../contexts/NotificationContext';
import notificationService from '../../services/notificationService';
import './DashboardPage.css';

function DashboardPage() {
  const [dashboardData, setDashboardData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedPeriod, setSelectedPeriod] = useState('today');
  const [testingNotifications, setTestingNotifications] = useState(false);
  
  const navigate = useNavigate();
  const { showLocalNotification } = useNotifications();

  useEffect(() => {
    loadDashboardData();
  }, []);

  const loadDashboardData = async () => {
    try {
      setLoading(true);
      const result = await adminService.getDashboardData();
      if (result.success) {
        setDashboardData(result.data);
      }
    } catch (err) {
      setError('Failed to load dashboard data. Please try again.');
      console.error('Dashboard error:', err);
    } finally {
      setLoading(false);
    }
  };

  const getStatsForPeriod = () => {
    const defaultStats = {
      totalOrders: 0,
      totalRevenue: 0,
      averageOrderValue: 0,
      onlineOrders: 0,
      inCafeOrders: 0,
      pendingOrders: 0
    };

    if (!dashboardData) return defaultStats;
    
    switch (selectedPeriod) {
      case 'week':
        return { ...defaultStats, ...dashboardData.weeklyStats };
      case 'month':
        return { ...defaultStats, ...dashboardData.monthlyStats };
      default:
        return { ...defaultStats, ...dashboardData.todayStats };
    }
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('en-IN', {
      style: 'currency',
      currency: 'INR'
    }).format(amount);
  };

  const formatTime = (timestamp) => {
    const now = new Date();
    const orderTime = new Date(timestamp);
    const diffMinutes = Math.floor((now - orderTime) / (1000 * 60));
    
    if (diffMinutes < 1) return 'Just now';
    if (diffMinutes < 60) return `${diffMinutes}m ago`;
    if (diffMinutes < 1440) return `${Math.floor(diffMinutes / 60)}h ago`;
    return orderTime.toLocaleDateString();
  };

  // Test notification functionality
  const testOrderNotification = async () => {
    setTestingNotifications(true);
    try {
      const mockOrderData = {
        id: 'TEST-' + Date.now(),
        customerName: 'John Doe',
        total: 25.99,
        items: [
          { name: 'Cappuccino', quantity: 2 },
          { name: 'Blueberry Muffin', quantity: 1 }
        ]
      };

      await notificationService.sendOrderNotification(mockOrderData, ['admin', 'employee']);
      showLocalNotification('Test Notification Sent!', {
        body: 'Check if you received the order notification',
        icon: '/icons/icon-192x192.png'
      });
    } catch (error) {
      console.error('Failed to send test notification:', error);
      showLocalNotification('Test Failed', {
        body: 'Failed to send test notification',
        icon: '/icons/icon-192x192.png'
      });
    } finally {
      setTestingNotifications(false);
    }
  };

  const recentOrdersColumns = [
    {
      key: 'id',
      title: 'Order ID',
      width: '120px'
    },
    {
      key: 'customerName',
      title: 'Customer',
      width: '150px'
    },
    {
      key: 'items',
      title: 'Items',
      render: (items) => items.join(', ')
    },
    {
      key: 'total',
      title: 'Total',
      type: 'currency',
      align: 'right',
      width: '100px'
    },
    {
      key: 'status',
      title: 'Status',
      type: 'status',
      width: '150px'
    },
    {
      key: 'timestamp',
      title: 'Time',
      render: (timestamp) => formatTime(timestamp),
      width: '100px'
    }
  ];

  const popularItemsColumns = [
    {
      key: 'name',
      title: 'Item Name'
    },
    {
      key: 'orders',
      title: 'Orders',
      align: 'center',
      width: '100px'
    },
    {
      key: 'revenue',
      title: 'Revenue',
      type: 'currency',
      align: 'right',
      width: '120px'
    }
  ];

  const lowStockColumns = [
    {
      key: 'name',
      title: 'Item Name'
    },
    {
      key: 'currentStock',
      title: 'Current Stock',
      align: 'center',
      render: (stock, row) => `${stock} ${row.unit}`
    },
    {
      key: 'minStock',
      title: 'Min Stock',
      align: 'center',
      render: (stock, row) => `${stock} ${row.unit}`
    },
    {
      key: 'status',
      title: 'Status',
      render: (_, row) => {
        const percentage = (row.currentStock / row.minStock) * 100;
        if (percentage <= 50) return 'Critical';
        if (percentage <= 100) return 'Low';
        return 'OK';
      },
      type: 'status'
    }
  ];

  if (loading) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-loading">
            <LoadingSpinner size="large" />
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-error">
            <h1>Error Loading Dashboard</h1>
            <p>{error}</p>
            <Button onClick={loadDashboardData} variant="primary">
              Try Again
            </Button>
          </div>
        </div>
      </div>
    );
  }

  const stats = getStatsForPeriod();

  return (
    <div className="dashboard-page">
      <div className="container">
        <div className="dashboard-header">
          <div className="dashboard-title-section">
            <h1>Admin Dashboard</h1>
            <p>Overview of your cafe's performance</p>
          </div>
          
          <div className="dashboard-controls">
            <div className="period-selector">
              {['today', 'week', 'month'].map(period => (
                <button
                  key={period}
                  className={`period-btn ${selectedPeriod === period ? 'active' : ''}`}
                  onClick={() => setSelectedPeriod(period)}
                >
                  {period.charAt(0).toUpperCase() + period.slice(1)}
                </button>
              ))}
            </div>
            
            <div className="dashboard-action-buttons">
              <Button
                variant="outline"
                size="small"
                onClick={testOrderNotification}
                disabled={testingNotifications}
              >
                {testingNotifications ? ' Testing...' : ' Test Notification'}
              </Button>
              
              <Button
                variant="primary"
                onClick={() => navigate('/admin/orders')}
              >
                View All Orders
              </Button>
            </div>
          </div>
        </div>

        <div className="dashboard-stats">
          <StatCard
            title="Total Orders"
            value={stats.totalOrders}
            icon=""
            color="primary"
            trend="up"
            trendValue="+12%"
            onClick={() => navigate('/admin/orders')}
          />
          
          <StatCard
            title="Revenue"
            value={formatCurrency(stats.totalRevenue)}
            icon=""
            color="success"
            trend="up"
            trendValue="+8%"
            onClick={() => navigate('/admin/analytics')}
          />
          
          <StatCard
            title="Avg Order Value"
            value={formatCurrency(stats.averageOrderValue)}
            icon=""
            color="info"
            trend="up"
            trendValue="+3%"
          />
          
          {selectedPeriod === 'today' && (
            <>
              <StatCard
                title="Online Orders"
                value={stats.onlineOrders}
                icon=""
                color="secondary"
              />
              
              <StatCard
                title="In-Cafe Orders"
                value={stats.inCafeOrders}
                icon=""
                color="warning"
              />
              
              <StatCard
                title="Pending Orders"
                value={stats.pendingOrders}
                icon=""
                color={stats.pendingOrders > 10 ? "error" : "warning"}
                onClick={() => navigate('/admin/orders?status=pending')}
              />
            </>
          )}
        </div>

        <div className="dashboard-content">
          <div className="dashboard-main">
            <div className="dashboard-section">
              <DataTable
                title="Recent Orders"
                data={dashboardData?.recentOrders || []}
                columns={recentOrdersColumns}
                pagination={false}
                searchable={false}
                actions={[
                  {
                    label: 'View All',
                    variant: 'outline',
                    onClick: () => navigate('/admin/orders')
                  }
                ]}
                onRowClick={(order) => navigate(`/admin/orders/${order.id}`)}
              />
            </div>

            <div className="dashboard-section">
              <DataTable
                title="Popular Items"
                data={dashboardData?.popularItems || []}
                columns={popularItemsColumns}
                pagination={false}
                searchable={false}
                actions={[
                  {
                    label: 'Manage Menu',
                    variant: 'outline',
                    onClick: () => navigate('/admin/menu-items')
                  }
                ]}
              />
            </div>
          </div>

          <div className="dashboard-sidebar">
            <div className="dashboard-section">
              <InventoryAlerts />
            </div>

            <div className="quick-actions">
              <h3>Quick Actions</h3>
              <div className="action-buttons">
                <Button
                  variant="primary"
                  fullWidth
                  onClick={() => navigate('/admin/take-order')}
                >
                   Take Order
                </Button>
                <Button
                  variant="outline"
                  fullWidth
                  onClick={() => navigate('/admin/menu-items/new')}
                >
                   Add Menu Item
                </Button>
                <Button
                  variant="outline"
                  fullWidth
                  onClick={() => navigate('/admin/inventory/restock')}
                >
                   Restock Items
                </Button>
                <Button
                  variant="outline"
                  fullWidth
                  onClick={() => navigate('/admin/employees/new')}
                >
                   Add Employee
                </Button>
                <Button
                  variant="outline"
                  fullWidth
                  onClick={() => navigate('/admin/analytics')}
                >
                   View Analytics
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default DashboardPage;