import api from './api';

const inventoryService = {
  // Check inventory availability for order items
  checkInventoryAvailability: async (items) => {
    try {
      const response = await api.post('/orders/check-inventory', { items });
      return {
        success: true,
        data: response.data
      };
    } catch (error) {
      console.error('Inventory check error:', error);
      return {
        success: false,
        message: error.response?.data?.message || 'Failed to check inventory availability',
        error: error.response?.data
      };
    }
  },

  // Get low stock alerts
  getLowStockAlerts: async () => {
    try {
      const response = await api.get('/orders/inventory/low-stock');
      return {
        success: true,
        data: response.data
      };
    } catch (error) {
      console.error('Low stock alerts error:', error);
      return {
        success: false,
        message: error.response?.data?.message || 'Failed to get low stock alerts',
        error: error.response?.data
      };
    }
  }
};

export default inventoryService;