import api from './api';

const orderService = {
  placeOrder: async (orderDetails) => {
    try {
      // Transform frontend order data to backend format
      const backendOrderData = orderService.transformOrderForBackend(orderDetails);
      console.log('Frontend order data:', orderDetails);
      console.log('Transformed backend order data:', backendOrderData);
      
      const response = await api.post('/orders', backendOrderData);
      return { success: true, data: orderService.transformOrderForFrontend(response.data.order) };
    } catch (error) {
      console.error('Error placing order:', error.response?.data || error.message);
      console.error('Full error response:', error.response);
      
      // Return more detailed error information
      const errorMessage = error.response?.data?.message || error.message;
      const errors = error.response?.data?.errors || [];
      const details = error.response?.data?.details || [];
      
      let fullErrorMessage = errorMessage;
      if (errors.length > 0) {
        fullErrorMessage += ': ' + errors.map(err => err.msg || err.message || err).join(', ');
      } else if (details.length > 0) {
        fullErrorMessage += ': ' + details.join(', ');
      }
      
      return { success: false, error: fullErrorMessage };
    }
  },

  getAllOrders: async (filters = {}) => {
    try {
      const queryParams = new URLSearchParams();
      Object.keys(filters).forEach(key => {
        if (filters[key] !== undefined && filters[key] !== null && filters[key] !== '') {
          queryParams.append(key, filters[key]);
        }
      });

      console.log('Fetching all orders with URL:', `/orders?${queryParams.toString()}`);
      const response = await api.get(`/orders?${queryParams.toString()}`);
      console.log('Raw API response:', response.data);
      
      const orders = response.data.orders.map(order => orderService.transformOrderForFrontend(order));
      console.log('Transformed orders:', orders);
      
      return { success: true, data: { orders, pagination: response.data.pagination } };
    } catch (error) {
      console.error('Error fetching all orders:', error.response?.data || error.message);
      return { success: false, error: error.response?.data?.message || error.message };
    }
  },

  getMyOrders: async (filters = {}) => {
    try {
      const queryParams = new URLSearchParams();
      Object.keys(filters).forEach(key => {
        if (filters[key] !== undefined && filters[key] !== null && filters[key] !== '') {
          queryParams.append(key, filters[key]);
        }
      });

      console.log('Fetching my orders with URL:', `/orders/my-orders?${queryParams.toString()}`);
      const response = await api.get(`/orders/my-orders?${queryParams.toString()}`);
      console.log('Raw API response for my orders:', response.data);
      
      const orders = response.data.orders.map(order => orderService.transformOrderForFrontend(order));
      console.log('Transformed my orders:', orders);
      
      return { success: true, data: { orders, pagination: response.data.pagination } };
    } catch (error) {
      console.error('Error fetching user orders:', error.response?.data || error.message);
      return { success: false, error: error.response?.data?.message || error.message };
    }
  },

  getOrderById: async (orderId) => {
    try {
      const response = await api.get(`/orders/${orderId}`);
      return { success: true, data: orderService.transformOrderForFrontend(response.data) };
    } catch (error) {
      console.error(`Error fetching order ${orderId}:`, error.response?.data || error.message);
      return { success: false, error: error.response?.data?.message || error.message };
    }
  },

  getAssignedOrders: async (filters = {}) => {
    try {
      const queryParams = new URLSearchParams();
      Object.keys(filters).forEach(key => {
        if (filters[key] !== undefined && filters[key] !== null && filters[key] !== '') {
          queryParams.append(key, filters[key]);
        }
      });

      const response = await api.get(`/admin/orders/assigned?${queryParams.toString()}`);
      return response.data;
    } catch (error) {
      console.error('Error fetching assigned orders:', error.response?.data || error.message);
      throw error;
    }
  },

  updateOrderStatus: async (orderId, newStatus, notes = '') => {
    try {
      const backendStatus = orderService.mapFrontendStatusToBackend(newStatus);
      const response = await api.patch(`/orders/${orderId}/status`, { status: backendStatus, notes });
      return { success: true, data: orderService.transformOrderForFrontend(response.data.order) };
    } catch (error) {
      console.error(`Error updating order ${orderId} status:`, error.response?.data || error.message);
      return { success: false, error: error.response?.data?.message || error.message };
    }
  },

  assignDeliveryPerson: async (orderId, deliveryPersonId) => {
    try {
      const response = await api.patch(`/admin/orders/${orderId}/assign`, { deliveryPersonId });
      return response.data;
    } catch (error) {
      console.error(`Error assigning delivery person to order ${orderId}:`, error.response?.data || error.message);
      throw error;
    }
  },

  markInCafeOrderServed: async (orderId) => {
    try {
      const response = await api.patch(`/admin/orders/${orderId}/serve`);
      return response.data;
    } catch (error) {
      console.error(`Error marking in-cafe order ${orderId} as served:`, error.response?.data || error.message);
      throw error;
    }
  },

  // Delivery person specific methods
  markOrderPickedUp: async (orderId) => {
    try {
      const response = await api.patch(`/delivery/orders/${orderId}/pickup`);
      return response.data;
    } catch (error) {
      console.error(`Error marking order ${orderId} as picked up:`, error.response?.data || error.message);
      throw error;
    }
  },

  markOrderDelivered: async (orderId) => {
    try {
      const response = await api.patch(`/delivery/orders/${orderId}/deliver`);
      return response.data;
    } catch (error) {
      console.error(`Error marking order ${orderId} as delivered:`, error.response?.data || error.message);
      throw error;
    }
  },

  getDeliveryPersonStats: async () => {
    try {
      const response = await api.get('/admin/delivery-stats');
      return response.data;
    } catch (error) {
      console.error('Error fetching delivery person stats:', error.response?.data || error.message);
      throw error;
    }
  },

  // Helper methods for data transformation
  transformOrderForFrontend: (backendOrder) => {
    return {
      id: backendOrder._id,
      customerName: backendOrder.customer?.name || backendOrder.customerInfo?.name || 'Unknown Customer',
      customerEmail: backendOrder.customer?.email || backendOrder.customerInfo?.email || '',
      customerPhone: backendOrder.customer?.phone || backendOrder.customerInfo?.phone || '',
      items: backendOrder.items.map(item => ({
        id: item.menuItem?._id || item.menuItem,
        name: item.menuItem?.name || 'Unknown Item',
        price: item.price,
        quantity: item.quantity,
        specialInstructions: item.specialInstructions || '',
        image: item.menuItem?.image || ''
      })),
      orderType: backendOrder.orderType, // Use the standardized order type directly
      type: backendOrder.orderType === 'delivery' ? 'online' : 'in-cafe', // For backward compatibility
      tableNumber: backendOrder.tableNumber || null,
      deliveryAddress: backendOrder.deliveryAddress ? 
        `${backendOrder.deliveryAddress.street || ''}, ${backendOrder.deliveryAddress.city || ''}, ${backendOrder.deliveryAddress.zipCode || ''}`.trim() : 
        null,
      deliveryInstructions: backendOrder.deliveryAddress?.instructions || '',
      paymentMethod: backendOrder.paymentMethod,
      subtotal: backendOrder.subtotal,
      tax: backendOrder.tax,
      deliveryFee: backendOrder.deliveryFee,
      total: backendOrder.total,
      status: orderService.mapBackendStatusToFrontend(backendOrder.status),
      timestamp: backendOrder.createdAt,
      estimatedDeliveryTime: backendOrder.estimatedDeliveryTime,
      actualDeliveryTime: backendOrder.actualDeliveryTime,
      assignedTo: backendOrder.assignedTo,
      notes: backendOrder.notes || ''
    };
  },

  transformOrderForBackend: (frontendOrder) => {
    // Map frontend order types to backend order types
    let backendOrderType = frontendOrder.orderType;
    if (frontendOrder.type === 'online') {
      backendOrderType = 'delivery';
    } else if (frontendOrder.orderType === 'pickup') {
      backendOrderType = 'take away'; // Map pickup to take away for backend
    }

    return {
      items: frontendOrder.items.map(item => ({
        menuItem: item.id || item.menuItem,
        quantity: item.quantity,
        specialInstructions: item.specialInstructions || ''
      })),
      orderType: backendOrderType,
      tableNumber: frontendOrder.tableNumber || undefined, // Only include if provided
      deliveryAddress: frontendOrder.deliveryInfo ? {
        street: frontendOrder.deliveryInfo.address,
        city: frontendOrder.deliveryInfo.city,
        zipCode: frontendOrder.deliveryInfo.zipCode,
        instructions: frontendOrder.deliveryInfo.instructions
      } : undefined,
      paymentMethod: frontendOrder.paymentMethod === 'cash_on_delivery' ? 'cash' : (frontendOrder.paymentMethod || frontendOrder.paymentInfo?.method || 'cash')
    };
  },

  // Status mapping between backend and frontend
  mapBackendStatusToFrontend: (backendStatus) => {
    const statusMap = {
      'pending': 'Pending',
      'order placed': 'Order Placed',
      'cooking in progress': 'Cooking in Progress',
      'ready for pickup': 'Ready for Pickup',
      'out for delivery': 'Out for Delivery',
      'delivered': 'Delivered',
      'cancelled': 'Cancelled'
    };
    return statusMap[backendStatus] || backendStatus;
  },

  mapFrontendStatusToBackend: (frontendStatus) => {
    const statusMap = {
      'Pending': 'pending',
      'Order Placed': 'order placed',
      'Cooking in Progress': 'cooking in progress',
      'Ready for Pickup': 'ready for pickup',
      'Out for Delivery': 'out for delivery',
      'Delivered': 'delivered',
      'Completed': 'delivered',
      'Cancelled': 'cancelled'
    };
    return statusMap[frontendStatus] || frontendStatus.toLowerCase();
  },

  // Admin-specific method to create orders on behalf of customers
  placeAdminOrder: async (orderDetails) => {
    try {
      const adminOrderData = {
        items: orderDetails.items.map(item => {
          const menuItemId = item.id || item.menuItem;
          console.log('Processing menu item:', { 
            itemId: menuItemId, 
            itemName: item.name,
            quantity: item.quantity 
          });
          
          // Validate that we have a valid-looking ObjectId (24 hex characters)
          if (!menuItemId || !/^[0-9a-fA-F]{24}$/.test(menuItemId)) {
            console.error(`Invalid menu item ID: ${menuItemId} for item: ${item.name}`);
            throw new Error(`Invalid menu item ID: ${menuItemId}`);
          }
          return {
            menuItem: menuItemId,
            quantity: item.quantity,
            specialInstructions: item.specialInstructions || ''
          };
        }),
        orderType: orderDetails.orderType,
        tableNumber: orderDetails.tableNumber ? parseInt(orderDetails.tableNumber) : null,
        paymentMethod: orderDetails.paymentMethod || 'cash',
        customerInfo: {
          name: orderDetails.customerName || orderDetails.customerInfo?.name || '',
          phone: orderDetails.customerPhone || orderDetails.customerInfo?.phone || '',
          email: orderDetails.customerEmail || orderDetails.customerInfo?.email || ''
        },
        specialInstructions: orderDetails.specialInstructions || ''
      };

      // Validate required fields
      if (!adminOrderData.customerInfo.name) {
        throw new Error('Customer name is required');
      }
      
      if (!adminOrderData.items || adminOrderData.items.length === 0) {
        throw new Error('Order must contain at least one item');
      }

      if (adminOrderData.orderType === 'dine in' && !adminOrderData.tableNumber) {
        throw new Error('Table number is required for dine in orders');
      }

      console.log('Creating admin order with data:', adminOrderData);
      
      // Use admin endpoint directly for admin orders
      const response = await api.post('/admin/orders', adminOrderData);
      
      return { success: true, data: orderService.transformOrderForFrontend(response.data.order) };
    } catch (error) {
      console.error('Error placing admin order:', error.response?.data || error.message);
      
      // Handle specific validation errors
      if (error.response?.status === 400) {
        const errorMessage = error.response?.data?.message || 'Validation failed';
        const errors = error.response?.data?.errors || [];
        const details = error.response?.data?.details || [];
        
        console.log('Validation error details:', {
          message: errorMessage,
          errors: errors,
          details: details,
          fullResponse: error.response?.data
        });
        
        let fullError = errorMessage;
        if (errors.length > 0) {
          fullError += ': ' + errors.map(err => err.msg || err.message || err).join(', ');
        } else if (details.length > 0) {
          fullError += ': ' + details.join(', ');
        }
        
        return { success: false, error: fullError };
      }
      
      return { success: false, error: error.response?.data?.message || error.message };
    }
  }
};

export default orderService;