import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import Button from '../../components/common/Button/Button';
import Input from '../../components/common/Input/Input';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import './Auth.css';

function DeliveryLogin() {
  const [formData, setFormData] = useState({
    identifier: '',
    password: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  
  const { login } = useAuth();
  const navigate = useNavigate();

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      const result = await login(formData.identifier, formData.password);
      if (result.success) {
        // Verify user is delivery person
        if (result.user.role === 'delivery') {
          navigate('/delivery/orders');
        } else {
          setError('Access denied. Delivery person credentials required.');
        }
      } else {
        setError(result.message || 'Login failed');
      }
    } catch (err) {
      setError('An error occurred during login');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="login-container">
      <div className="login-form">
        <h1>Delivery Login - The Sanctum Cafe</h1>
        
        <div className="demo-credentials">
          <h3>Delivery Person Access:</h3>
          <p><strong>Email:</strong> delivery@sanctumcafe.com</p>
          <p><strong>Password:</strong> delivery123</p>
          <p className="login-note">
            <em>Use your delivery person email address to login</em>
          </p>
        </div>

        {error && <div className="error-message">{error}</div>}
        
        <form onSubmit={handleSubmit}>
          <Input
            type="email"
            name="identifier"
            placeholder="Delivery Person Email Address"
            value={formData.identifier}
            onChange={handleChange}
            required
          />
          
          <Input
            type="password"
            name="password"
            placeholder="Password"
            value={formData.password}
            onChange={handleChange}
            required
          />
          
          <Button type="submit" disabled={loading}>
            {loading ? <LoadingSpinner /> : 'Login as Delivery Person'}
          </Button>
        </form>
        
        <p>
          Need different access? <Link to="/">Go to Home</Link>
        </p>
      </div>
    </div>
  );
}

export default DeliveryLogin;