import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import Button from '../../components/common/Button/Button';
import Input from '../../components/common/Input/Input';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import './Auth.css';

function Login() {
  const [formData, setFormData] = useState({
    identifier: '',
    password: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  
  const { login } = useAuth();
  const navigate = useNavigate();

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      const result = await login(formData.identifier, formData.password);
      if (result.success) {
        // Redirect based on user role
        switch (result.user.role) {
          case 'admin':
            navigate('/admin/dashboard');
            break;
          case 'customer':
            navigate('/customer/menu');
            break;
          case 'delivery':
            navigate('/delivery/orders');
            break;
          case 'employee':
            navigate('/employee/dashboard');
            break;
          default:
            navigate('/');
        }
      } else {
        setError(result.message || 'Login failed');
      }
    } catch (err) {
      setError('An error occurred during login');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="login-container">
      <div className="login-form">
        <h1>Login to The Sanctum Cafe</h1>
        
        <div className="demo-credentials">
          <h3>Demo Credentials:</h3>
          <p><strong>Customer:</strong> 9876543210 / customer123</p>
          <p><strong>Admin:</strong> admin@sanctumcafe.com / admin123</p>
          <p><strong>Employee:</strong> employee@sanctumcafe.com / employee123</p>
          <p><strong>Delivery:</strong> delivery@sanctumcafe.com / delivery123</p>
          <p className="login-note">
            <em>Customers: Use mobile number | Staff: Use email address</em>
          </p>
        </div>

        {error && <div className="error-message">{error}</div>}
        
        <form onSubmit={handleSubmit}>
          <Input
            type="text"
            name="identifier"
            placeholder="Mobile Number (customers) or Email (staff)"
            value={formData.identifier}
            onChange={handleChange}
            required
          />
          
          <Input
            type="password"
            name="password"
            placeholder="Password"
            value={formData.password}
            onChange={handleChange}
            required
          />
          
          <Button type="submit" disabled={loading}>
            {loading ? <LoadingSpinner /> : 'Login'}
          </Button>
        </form>
        
        <p>
          Don't have an account? <Link to="/register">Register here</Link>
        </p>
      </div>
    </div>
  );
}

export default Login;