import React, { useState, useEffect } from 'react';
import adminService from '../../services/adminService';
import DataTable from '../../components/admin/DataTable/DataTable';
import StatCard from '../../components/admin/StatCard/StatCard';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import Button from '../../components/common/Button/Button';
import Modal from '../../components/common/Modal/Modal';
import Input from '../../components/common/Input/Input';
import './InventoryManagementPage.css';

function InventoryManagementPage() {
  const [inventory, setInventory] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [showAddModal, setShowAddModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [selectedItem, setSelectedItem] = useState(null);
  const [categoryFilter, setCategoryFilter] = useState('all');
  const [stockFilter, setStockFilter] = useState('all');
  const [submitting, setSubmitting] = useState(false);

  const [formData, setFormData] = useState({
    name: '',
    category: '',
    currentStock: '',
    minStock: '',
    maxStock: '',
    unit: '',
    costPerUnit: '',
    supplier: '',
    expiryDate: ''
  });

  useEffect(() => {
    loadInventory();
  }, []);

  const loadInventory = async () => {
    try {
      setLoading(true);
      setError('');
      const result = await adminService.getInventoryItems();
      if (result.success) {
        setInventory(result.data.items || result.data);
      }
    } catch (err) {
      setError('Failed to load inventory. Please try again.');
      console.error('Inventory loading error:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleFormChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const resetForm = () => {
    setFormData({
      name: '',
      category: '',
      currentStock: '',
      minStock: '',
      maxStock: '',
      unit: '',
      costPerUnit: '',
      supplier: '',
      expiryDate: ''
    });
  };

  const handleAddItem = async (e) => {
    e.preventDefault();
    
    // Declare variables outside try block so they're accessible in catch block
    let currentStock, minStock, maxStock, costPerUnit;
    
    try {
      setSubmitting(true);
      setError('');
      
      // Basic validation
      if (!formData.name.trim()) {
        setError('Item name is required');
        return;
      }
      
      if (!formData.category) {
        setError('Category is required');
        return;
      }
      
      const validCategories = ['ingredients', 'beverages', 'dairy', 'produce', 'meat', 'grains', 'spices', 'packaging', 'cleaning', 'other'];
      if (!validCategories.includes(formData.category.toLowerCase())) {
        setError('Please select a valid category');
        return;
      }
      
      currentStock = Number(formData.currentStock);
      minStock = Number(formData.minStock);
      maxStock = Number(formData.maxStock);
      costPerUnit = Number(formData.costPerUnit);
      
      if (isNaN(currentStock) || currentStock < 0) {
        setError('Current stock must be a valid non-negative number');
        return;
      }
      
      if (isNaN(minStock) || minStock < 0) {
        setError('Minimum stock must be a valid non-negative number');
        return;
      }
      
      if (isNaN(maxStock) || maxStock < minStock) {
        setError('Maximum stock must be greater than or equal to minimum stock');
        return;
      }
      
      if (isNaN(costPerUnit) || costPerUnit < 0) {
        setError('Cost per unit must be a valid non-negative number');
        return;
      }
      
      const result = await adminService.addInventoryItem({
        ...formData,
        currentStock,
        minStock,
        maxStock,
        costPerUnit,
        expiryDate: formData.expiryDate ? formData.expiryDate : undefined
      });
      
      if (result.success) {
        const newItem = result.data.item || result.data;
        setInventory([...inventory, newItem]);
        setShowAddModal(false);
        resetForm();
        alert('Inventory item added successfully!');
      } else {
        setError(result.message || 'Failed to add inventory item');
      }
    } catch (err) {
      console.error('Add item error:', err);
      console.error('Error response data:', err.response?.data);
      console.error('Form data being sent:', {
        ...formData,
        currentStock,
        minStock,
        maxStock,
        costPerUnit,
        expiryDate: formData.expiryDate ? formData.expiryDate : undefined
      });
      
      const errorMessage = err.response?.data?.message || err.message || 'Failed to add inventory item. Please try again.';
      setError(errorMessage);
      
      // If there are validation errors, show them
      if (err.response?.data?.errors) {
        const validationErrors = err.response.data.errors.join(', ');
        setError(`Validation errors: ${validationErrors}`);
      }
    } finally {
      setSubmitting(false);
    }
  };

  const handleEditItem = async (e) => {
    e.preventDefault();
    
    // Declare variables outside try block so they're accessible in catch block
    let currentStock, minStock, maxStock, costPerUnit;
    
    try {
      setSubmitting(true);
      setError('');
      
      // Basic validation
      if (!formData.name.trim()) {
        setError('Item name is required');
        return;
      }
      
      if (!formData.category) {
        setError('Category is required');
        return;
      }
      
      const validCategories = ['ingredients', 'beverages', 'dairy', 'produce', 'meat', 'grains', 'spices', 'packaging', 'cleaning', 'other'];
      if (!validCategories.includes(formData.category.toLowerCase())) {
        setError('Please select a valid category');
        return;
      }
      
      currentStock = Number(formData.currentStock);
      minStock = Number(formData.minStock);
      maxStock = Number(formData.maxStock);
      costPerUnit = Number(formData.costPerUnit);
      
      if (isNaN(currentStock) || currentStock < 0) {
        setError('Current stock must be a valid non-negative number');
        return;
      }
      
      if (isNaN(minStock) || minStock < 0) {
        setError('Minimum stock must be a valid non-negative number');
        return;
      }
      
      if (isNaN(maxStock) || maxStock < minStock) {
        setError('Maximum stock must be greater than or equal to minimum stock');
        return;
      }
      
      if (isNaN(costPerUnit) || costPerUnit < 0) {
        setError('Cost per unit must be a valid non-negative number');
        return;
      }
      
      const result = await adminService.updateInventoryItem(selectedItem._id || selectedItem.id, {
        ...formData,
        currentStock,
        minStock,
        maxStock,
        costPerUnit,
        expiryDate: formData.expiryDate ? formData.expiryDate : undefined
      });
      
      if (result.success) {
        const updatedItem = result.data.item || result.data;
        setInventory(inventory.map(item => 
          (item._id || item.id) === (selectedItem._id || selectedItem.id) ? updatedItem : item
        ));
        setShowEditModal(false);
        setSelectedItem(null);
        resetForm();
        alert('Inventory item updated successfully!');
      } else {
        setError(result.message || 'Failed to update inventory item');
      }
    } catch (err) {
      console.error('Edit item error:', err);
      console.error('Error response data:', err.response?.data);
      console.error('Form data being sent:', {
        ...formData,
        currentStock,
        minStock,
        maxStock,
        costPerUnit,
        expiryDate: formData.expiryDate ? formData.expiryDate : undefined
      });
      
      const errorMessage = err.response?.data?.message || err.message || 'Failed to update inventory item. Please try again.';
      setError(errorMessage);
      
      // If there are validation errors, show them
      if (err.response?.data?.errors) {
        const validationErrors = err.response.data.errors.join(', ');
        setError(`Validation errors: ${validationErrors}`);
      }
    } finally {
      setSubmitting(false);
    }
  };

  const handleDeleteItem = async (itemId) => {
    if (window.confirm('Are you sure you want to delete this inventory item?')) {
      try {
        setError('');
        const result = await adminService.deleteInventoryItem(itemId);
        if (result.success) {
          setInventory(inventory.filter(item => (item._id || item.id) !== itemId));
          alert('Inventory item deleted successfully!');
        }
      } catch (err) {
        console.error('Delete item error:', err);
        setError('Failed to delete inventory item. Please try again.');
      }
    }
  };

  const openEditModal = (item) => {
    setSelectedItem(item);
    setError(''); // Clear any previous errors
    
    // Format expiry date for HTML date input (YYYY-MM-DD)
    let formattedExpiryDate = '';
    if (item.expiryDate) {
      const date = new Date(item.expiryDate);
      if (!isNaN(date.getTime())) {
        formattedExpiryDate = date.toISOString().split('T')[0];
      }
    }
    
    setFormData({
      name: item.name || '',
      category: item.category || '',
      currentStock: item.currentStock?.toString() || '0',
      minStock: item.minStock?.toString() || '0',
      maxStock: item.maxStock?.toString() || '0',
      unit: item.unit || '',
      costPerUnit: item.costPerUnit?.toString() || '0',
      supplier: item.supplier || '',
      expiryDate: formattedExpiryDate
    });
    setShowEditModal(true);
  };

  const getStockStatus = (item) => {
    const percentage = (item.currentStock / item.minStock) * 100;
    if (percentage <= 50) return 'critical';
    if (percentage <= 100) return 'low';
    return 'good';
  };

  const getFilteredInventory = () => {
    return inventory.filter(item => {
      const categoryMatch = categoryFilter === 'all' || item.category === categoryFilter;
      const stockMatch = stockFilter === 'all' || 
        (stockFilter === 'low' && getStockStatus(item) !== 'good') ||
        (stockFilter === 'critical' && getStockStatus(item) === 'critical') ||
        (stockFilter === 'good' && getStockStatus(item) === 'good');
      return categoryMatch && stockMatch;
    });
  };

  const getInventoryStats = () => {
    const totalItems = inventory.length;
    const lowStockItems = inventory.filter(item => getStockStatus(item) === 'low').length;
    const criticalStockItems = inventory.filter(item => getStockStatus(item) === 'critical').length;
    const totalValue = inventory.reduce((sum, item) => sum + (item.currentStock * item.costPerUnit), 0);
    
    return { totalItems, lowStockItems, criticalStockItems, totalValue };
  };

  const categories = [...new Set(inventory.map(item => item.category))];

  const inventoryColumns = [
    {
      key: 'name',
      title: 'Item Name',
      width: '200px'
    },
    {
      key: 'category',
      title: 'Category',
      width: '120px'
    },
    {
      key: 'currentStock',
      title: 'Current Stock',
      render: (stock, item) => (
        <span className={`stock-value stock-value--${getStockStatus(item)}`}>
          {stock} {item.unit}
        </span>
      ),
      align: 'center',
      width: '120px'
    },
    {
      key: 'minStock',
      title: 'Min Stock',
      render: (stock, item) => `${stock} ${item.unit}`,
      align: 'center',
      width: '100px'
    },
    {
      key: 'supplier',
      title: 'Supplier',
      width: '150px'
    },
    {
      key: 'costPerUnit',
      title: 'Cost/Unit',
      type: 'currency',
      align: 'right',
      width: '100px'
    },
    {
      key: 'expiryDate',
      title: 'Expiry Date',
      type: 'date',
      width: '120px'
    },
    {
      key: 'actions',
      title: 'Actions',
      sortable: false,
      render: (_, item) => (
        <div className="inventory-actions">
          <Button
            variant="outline"
            size="small"
            onClick={() => openEditModal(item)}
          >
            Edit
          </Button>
          <Button
            variant="outline"
            size="small"
            onClick={() => handleDeleteItem(item._id || item.id)}
            className="delete-btn"
          >
            Delete
          </Button>
        </div>
      ),
      width: '150px'
    }
  ];

  const stats = getInventoryStats();
  const filteredInventory = getFilteredInventory();

  if (loading) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-loading">
            <LoadingSpinner size="large" />
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-error">
            <h1>Error Loading Inventory</h1>
            <p>{error}</p>
            <Button onClick={loadInventory} variant="primary">
              Try Again
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="inventory-management-page">
      <div className="container">
        <div className="inventory-header">
          <div className="inventory-title-section">
            <h1>Inventory Management</h1>
            <p>Manage ingredients and stock levels</p>
          </div>
          
          <Button
            variant="primary"
            onClick={() => {
              setShowAddModal(true);
              setError('');
            }}
          >
             Add New Item
          </Button>
        </div>

        <div className="inventory-stats">
          <StatCard
            title="Total Items"
            value={stats.totalItems}
            icon=""
            color="primary"
          />
          
          <StatCard
            title="Low Stock Items"
            value={stats.lowStockItems}
            icon=""
            color="warning"
            onClick={() => setStockFilter('low')}
          />
          
          <StatCard
            title="Critical Stock"
            value={stats.criticalStockItems}
            icon=""
            color="error"
            onClick={() => setStockFilter('critical')}
          />
          
          <StatCard
            title="Total Value"
            value={`${stats.totalValue.toFixed(2)}`}
            icon=""
            color="success"
          />
        </div>

        <div className="inventory-filters">
          <div className="filter-group">
            <label>Category:</label>
            <select
              value={categoryFilter}
              onChange={(e) => setCategoryFilter(e.target.value)}
              className="filter-select"
            >
              <option value="all">All Categories</option>
              {categories.map(category => (
                <option key={category} value={category}>{category}</option>
              ))}
            </select>
          </div>
          
          <div className="filter-group">
            <label>Stock Status:</label>
            <select
              value={stockFilter}
              onChange={(e) => setStockFilter(e.target.value)}
              className="filter-select"
            >
              <option value="all">All Items</option>
              <option value="good">Good Stock</option>
              <option value="low">Low Stock</option>
              <option value="critical">Critical Stock</option>
            </select>
          </div>
          
          <Button
            variant="outline"
            onClick={loadInventory}
            disabled={loading}
          >
             Refresh
          </Button>
        </div>

        <DataTable
          data={filteredInventory}
          columns={inventoryColumns}
          title={`Inventory Items (${filteredInventory.length})`}
          searchable={true}
          sortable={true}
          pagination={true}
          pageSize={15}
          emptyMessage="No inventory items found"
        />

        {/* Add Item Modal */}
        <Modal
          isOpen={showAddModal}
          onClose={() => {
            setShowAddModal(false);
            resetForm();
            setError('');
          }}
          title="Add New Inventory Item"
          size="medium"
        >
          <form onSubmit={handleAddItem} className="inventory-form">
            {error && (
              <div className="error-message" style={{ 
                color: '#dc3545', 
                backgroundColor: '#f8d7da', 
                border: '1px solid #f5c6cb', 
                padding: '10px', 
                borderRadius: '4px', 
                marginBottom: '15px' 
              }}>
                {error}
              </div>
            )}
            <div className="form-grid">
              <Input
                label="Item Name"
                name="name"
                value={formData.name}
                onChange={handleFormChange}
                required
              />
              
              <div className="form-field">
                <label htmlFor="add-category">Category *</label>
                <select
                  id="add-category"
                  name="category"
                  value={formData.category}
                  onChange={handleFormChange}
                  required
                  className="form-select"
                  style={{ 
                    width: '100%', 
                    padding: '8px 12px', 
                    border: '1px solid #ddd', 
                    borderRadius: '4px',
                    fontSize: '14px'
                  }}
                >
                  <option value="">Select a category</option>
                  <option value="ingredients">Ingredients</option>
                  <option value="beverages">Beverages</option>
                  <option value="dairy">Dairy</option>
                  <option value="produce">Produce</option>
                  <option value="meat">Meat</option>
                  <option value="grains">Grains</option>
                  <option value="spices">Spices</option>
                  <option value="packaging">Packaging</option>
                  <option value="cleaning">Cleaning</option>
                  <option value="other">Other</option>
                </select>
              </div>
              
              <Input
                label="Current Stock"
                name="currentStock"
                type="number"
                value={formData.currentStock}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Minimum Stock"
                name="minStock"
                type="number"
                value={formData.minStock}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Maximum Stock"
                name="maxStock"
                type="number"
                value={formData.maxStock}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Unit"
                name="unit"
                value={formData.unit}
                onChange={handleFormChange}
                placeholder="e.g., kg, liters, pieces"
                required
              />
              
              <Input
                label="Cost per Unit"
                name="costPerUnit"
                type="number"
                step="0.01"
                value={formData.costPerUnit}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Supplier"
                name="supplier"
                value={formData.supplier}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Expiry Date"
                name="expiryDate"
                type="date"
                value={formData.expiryDate}
                onChange={handleFormChange}
                className="full-width"
              />
            </div>
            
            <div className="form-actions">
              <Button
                type="button"
                variant="outline"
                onClick={() => {
                  setShowAddModal(false);
                  resetForm();
                }}
              >
                Cancel
              </Button>
              <Button
                type="submit"
                variant="primary"
                disabled={submitting}
              >
                {submitting ? 'Adding...' : 'Add Item'}
              </Button>
            </div>
          </form>
        </Modal>

        {/* Edit Item Modal */}
        <Modal
          isOpen={showEditModal}
          onClose={() => {
            setShowEditModal(false);
            setSelectedItem(null);
            resetForm();
            setError('');
          }}
          title={`Edit ${selectedItem?.name}`}
          size="medium"
        >
          <form onSubmit={handleEditItem} className="inventory-form">
            {error && (
              <div className="error-message" style={{ 
                color: '#dc3545', 
                backgroundColor: '#f8d7da', 
                border: '1px solid #f5c6cb', 
                padding: '10px', 
                borderRadius: '4px', 
                marginBottom: '15px' 
              }}>
                {error}
              </div>
            )}
            <div className="form-grid">
              <Input
                label="Item Name"
                name="name"
                value={formData.name}
                onChange={handleFormChange}
                required
              />
              
              <div className="form-field">
                <label htmlFor="edit-category">Category *</label>
                <select
                  id="edit-category"
                  name="category"
                  value={formData.category}
                  onChange={handleFormChange}
                  required
                  className="form-select"
                  style={{ 
                    width: '100%', 
                    padding: '8px 12px', 
                    border: '1px solid #ddd', 
                    borderRadius: '4px',
                    fontSize: '14px'
                  }}
                >
                  <option value="">Select a category</option>
                  <option value="ingredients">Ingredients</option>
                  <option value="beverages">Beverages</option>
                  <option value="dairy">Dairy</option>
                  <option value="produce">Produce</option>
                  <option value="meat">Meat</option>
                  <option value="grains">Grains</option>
                  <option value="spices">Spices</option>
                  <option value="packaging">Packaging</option>
                  <option value="cleaning">Cleaning</option>
                  <option value="other">Other</option>
                </select>
              </div>
              
              <Input
                label="Current Stock"
                name="currentStock"
                type="number"
                value={formData.currentStock}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Minimum Stock"
                name="minStock"
                type="number"
                value={formData.minStock}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Maximum Stock"
                name="maxStock"
                type="number"
                value={formData.maxStock}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Unit"
                name="unit"
                value={formData.unit}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Cost per Unit"
                name="costPerUnit"
                type="number"
                step="0.01"
                value={formData.costPerUnit}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Supplier"
                name="supplier"
                value={formData.supplier}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Expiry Date"
                name="expiryDate"
                type="date"
                value={formData.expiryDate}
                onChange={handleFormChange}
                className="full-width"
              />
            </div>
            
            <div className="form-actions">
              <Button
                type="button"
                variant="outline"
                onClick={() => {
                  setShowEditModal(false);
                  setSelectedItem(null);
                  resetForm();
                }}
              >
                Cancel
              </Button>
              <Button
                type="submit"
                variant="primary"
                disabled={submitting}
              >
                {submitting ? 'Updating...' : 'Update Item'}
              </Button>
            </div>
          </form>
        </Modal>
      </div>
    </div>
  );
}

export default InventoryManagementPage;