import React, { createContext, useState, useContext, useEffect } from 'react';
import settingsService from '../services/settingsService';

const SettingsContext = createContext(null);

export const SettingsProvider = ({ children }) => {
  const [settings, setSettings] = useState({
    business: {
      name: 'Sanctum Cafe',
      address: '123 Coffee Street, Brew City, BC 12345',
      phone: '(555) 123-CAFE',
      email: 'info@sanctumcafe.com',
      website: 'https://sanctumcafe.com',
      description: 'A cozy neighborhood cafe serving premium coffee and fresh food.',
      openingHours: {
        monday: { open: '07:00', close: '19:00', closed: false },
        tuesday: { open: '07:00', close: '19:00', closed: false },
        wednesday: { open: '07:00', close: '19:00', closed: false },
        thursday: { open: '07:00', close: '19:00', closed: false },
        friday: { open: '07:00', close: '20:00', closed: false },
        saturday: { open: '08:00', close: '20:00', closed: false },
        sunday: { open: '08:00', close: '18:00', closed: false }
      }
    },
    delivery: {
      enabled: true,
      freeDeliveryThreshold: 25.00,
      deliveryFee: 3.99,
      maxDeliveryDistance: 10,
      estimatedDeliveryTime: 30
    },
    payment: {
      acceptCash: true,
      acceptCard: true,
      acceptDigitalWallet: true,
      taxRate: 8.5,
      tipSuggestions: [15, 18, 20, 25]
    },
    notifications: {
      emailNotifications: true,
      smsNotifications: false,
      pushNotifications: true,
      orderNotifications: true,
      inventoryAlerts: true,
      customerFeedback: true
    },
    system: {
      timezone: 'Asia/Kolkata',
      dateFormat: 'DD/MM/YYYY',
      currency: 'INR',
      language: 'en',
      autoBackup: true,
      backupFrequency: 'daily',
      maintenanceMode: false,
      maintenanceMessage: 'Sorry, we are not delivering currently. Please try again later!'
    }
  });
  
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Load settings on mount
  useEffect(() => {
    loadSettings();
  }, []);

  const loadSettings = async () => {
    try {
      setLoading(true);
      setError(null);
      console.log(' Loading settings from server...');
      
      const fetchedSettings = await settingsService.getSettings();
      console.log(' Fetched settings from server:', fetchedSettings);
      
      // Merge with defaults to ensure all required fields exist
      setSettings(prevSettings => {
        const mergedSettings = {
          ...prevSettings,
          ...fetchedSettings
        };
        console.log(' Merged settings:', mergedSettings);
        return mergedSettings;
      });
    } catch (err) {
      console.error('Error loading settings:', err);
      setError('Failed to load settings');
      // Keep default settings if loading fails
    } finally {
      setLoading(false);
    }
  };

  const updateSettings = async (category, newSettings) => {
    try {
      console.log(` Updating ${category} settings:`, newSettings);
      
      await settingsService.updateSettings(category, newSettings);
      
      // Update local state immediately
      setSettings(prevSettings => {
        const updatedSettings = {
          ...prevSettings,
          [category]: {
            ...prevSettings[category],
            ...newSettings
          }
        };
        console.log(` Settings updated locally for ${category}:`, updatedSettings[category]);
        return updatedSettings;
      });
      
      // Force a refresh from the server to ensure consistency
      setTimeout(() => {
        loadSettings();
      }, 500);
      
      return { success: true };
    } catch (err) {
      console.error('Error updating settings:', err);
      return { 
        success: false, 
        message: err.response?.data?.message || 'Failed to update settings' 
      };
    }
  };

  const getSetting = (category, key) => {
    return settings[category]?.[key];
  };

  const getBusinessHours = () => {
    return settings.business?.openingHours || {};
  };

  const formatBusinessHours = () => {
    const hours = getBusinessHours();
    return settingsService.formatHours(hours);
  };

  const refreshSettings = () => {
    console.log(' Manually refreshing settings...');
    loadSettings();
  };

  // Add a method to get fresh business info
  const getBusinessInfo = () => {
    return settings.business || {};
  };

  return (
    <SettingsContext.Provider value={{
      settings,
      loading,
      error,
      updateSettings,
      getSetting,
      getBusinessHours,
      getBusinessInfo,
      formatBusinessHours,
      refreshSettings
    }}>
      {children}
    </SettingsContext.Provider>
  );
};

export const useSettings = () => {
  const context = useContext(SettingsContext);
  if (!context) {
    throw new Error('useSettings must be used within a SettingsProvider');
  }
  return context;
};