import React, { useState, useEffect } from 'react';
import DataTable from '../../components/admin/DataTable/DataTable';
import Button from '../../components/common/Button/Button';
import Modal from '../../components/common/Modal/Modal';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import Toast from '../../components/common/Toast/Toast';
import { useAuth } from '../../contexts/AuthContext';
import orderService from '../../services/orderService';
import { downloadInvoice } from '../../utils/invoiceGenerator';
import './EmployeeOrdersPage.css';

function EmployeeOrdersPage() {
  const { user } = useAuth();
  const [loading, setLoading] = useState(true);
  const [orders, setOrders] = useState([]);
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [showOrderModal, setShowOrderModal] = useState(false);
  const [statusFilter, setStatusFilter] = useState('all');
  const [typeFilter, setTypeFilter] = useState('all');
  const [toast, setToast] = useState({ show: false, message: '', type: 'success' });
  const [error, setError] = useState('');
  const [updating, setUpdating] = useState(false);

  // Transform backend status to frontend status
  const transformBackendStatus = (backendStatus) => {
    const statusMap = {
      'pending': 'Pending',
      'order placed': 'Order Placed',
      'cooking in progress': 'Cooking in Progress',
      'ready for pickup': 'Ready for Pickup',
      'out for delivery': 'Out for Delivery',
      'delivered': 'Delivered',
      'cancelled': 'Cancelled'
    };
    return statusMap[backendStatus] || backendStatus;
  };

  // Transform frontend status to backend status
  const transformFrontendStatus = (frontendStatus) => {
    const statusMap = {
      'Pending': 'pending',
      'Order Placed': 'order placed',
      'Cooking in Progress': 'cooking in progress',
      'Ready for Pickup': 'ready for pickup',
      'Out for Delivery': 'out for delivery',
      'Delivered': 'delivered',
      'Completed': 'delivered',
      'Cancelled': 'cancelled'
    };
    return statusMap[frontendStatus] || frontendStatus.toLowerCase();
  };

  useEffect(() => {
    fetchOrders();
    // Set up auto-refresh every 30 seconds
    const interval = setInterval(fetchOrders, 30000);
    return () => clearInterval(interval);
  }, []);

  const fetchOrders = async () => {
    try {
      setLoading(true);
      setError('');

      // Use orderService as employees access /orders endpoint, not /admin/orders
      const result = await orderService.getAllOrders({ all: 'true' });

      if (result.success) {
        console.log('Employee page: Orders fetched successfully:', result.data.orders.length, 'orders');

        // The orderService already transforms the data, but we need to adjust the type field
        const adjustedOrders = result.data.orders.map(order => ({
          ...order,
          type: order.orderType === 'delivery' ? 'online' : 'pickup' // Convert order types for display consistency
        }));

        console.log('Employee page: Sample order after adjustment:', adjustedOrders[0]);
        setOrders(adjustedOrders);
      } else {
        console.error('Employee page: Failed to fetch orders:', result.error);
        setError('Failed to fetch orders');
        showToast('Failed to fetch orders', 'error');
      }
    } catch (error) {
      console.error('Error fetching orders:', error);
      setError('Failed to fetch orders');
      showToast('Failed to fetch orders', 'error');
    } finally {
      setLoading(false);
    }
  };



  const showToast = (message, type = 'success') => {
    setToast({ show: true, message, type });
  };

  const hideToast = () => {
    setToast({ show: false, message: '', type: 'success' });
  };

  const handleDownloadInvoice = async (order) => {
    try {
      const invoiceData = {
        id: order.id,
        customerName: order.customerName,
        customerPhone: order.customerPhone,
        items: order.items,
        orderType: order.type === 'online' ? 'delivery' : 'dine in',
        tableNumber: order.tableNumber,
        paymentMethod: order.paymentMethod,
        total: order.total,
        timestamp: order.timestamp
      };

      await downloadInvoice(invoiceData);
      showToast('Invoice downloaded successfully!', 'success');
    } catch (error) {
      console.error('Error downloading invoice:', error);
      showToast('Failed to download invoice', 'error');
    }
  };

  const handleStatusUpdate = async (orderId, newStatus) => {
    try {
      setUpdating(true);
      const backendStatus = transformFrontendStatus(newStatus);
      console.log(`Updating order ${orderId} from ${newStatus} to backend status: ${backendStatus}`);

      // Use orderService to update status
      const result = await orderService.updateOrderStatus(orderId, backendStatus);
      if (result.success) {
        setOrders(prevOrders =>
          prevOrders.map(order =>
            order.id === orderId ? { ...order, status: newStatus } : order
          )
        );
        if (selectedOrder && selectedOrder.id === orderId) {
          setSelectedOrder({ ...selectedOrder, status: newStatus });
        }
        showToast(`Order status updated to ${newStatus}`, 'success');
        console.log(`Order ${orderId} status updated successfully`);
      } else {
        showToast('Failed to update order status', 'error');
      }
    } catch (err) {
      console.error('Status update error:', err);
      showToast('Failed to update order status. Please try again.', 'error');
    } finally {
      setUpdating(false);
    }
  };

  const getStatusOptions = (currentStatus, orderType) => {
    const allStatuses = [
      'Pending',
      'Order Placed',
      'Cooking in Progress',
      orderType === 'online' ? 'Out for Delivery' : 'Ready for Pickup',
      'Delivered', // Use 'Delivered' for both online and pickup orders for consistency
      'Cancelled'
    ];

    const currentIndex = allStatuses.indexOf(currentStatus);
    return allStatuses.slice(currentIndex);
  };



  const orderColumns = [
    {
      key: 'id',
      title: 'Order ID',
      width: '120px'
    },
    {
      key: 'customerName',
      title: 'Customer',
      width: '150px'
    },
    {
      key: 'type',
      title: 'Type',
      render: (type) => (
        <span className={`order-type-badge order-type-badge--${type}`}>
          {type === 'online' ? ' Delivery' : ' Take Away'}
        </span>
      ),
      width: '120px'
    },
    {
      key: 'items',
      title: 'Items',
      render: (items) => `${items.length} item${items.length !== 1 ? 's' : ''}`,
      width: '80px'
    },
    {
      key: 'total',
      title: 'Total',
      type: 'currency',
      align: 'right',
      width: '100px'
    },
    {
      key: 'status',
      title: 'Status',
      type: 'status',
      width: '150px'
    },
    {
      key: 'timestamp',
      title: 'Order Time',
      render: (timestamp) => new Date(timestamp).toLocaleString(),
      width: '150px'
    },
    {
      key: 'actions',
      title: 'Actions',
      sortable: false,
      render: (_, order) => (
        <div className="order-actions">
          <Button
            variant="outline"
            size="small"
            onClick={(e) => {
              e.stopPropagation();
              setSelectedOrder(order);
              setShowOrderModal(true);
            }}
          >
            View
          </Button>
          <Button
            variant="outline"
            size="small"
            onClick={async (e) => {
              e.stopPropagation();
              await handleDownloadInvoice(order);
            }}
            className="download-btn"
          >
            
          </Button>
          {order.status !== 'Delivered' && order.status !== 'Cancelled' && (
            <select
              className="status-select"
              value={order.status}
              onChange={(e) => {
                e.stopPropagation();
                handleStatusUpdate(order.id, e.target.value);
              }}
              disabled={updating}
              onClick={(e) => e.stopPropagation()}
            >
              {getStatusOptions(order.status, order.type).map(status => (
                <option key={status} value={status}>
                  {status}
                </option>
              ))}
            </select>
          )}
        </div>
      ),
      width: '250px'
    }
  ];

  const formatTime = (timestamp) => {
    return new Date(timestamp).toLocaleString();
  };

  const getFilteredOrders = () => {
    const filtered = orders.filter(order => {
      const statusMatch = statusFilter === 'all' || order.status === statusFilter;
      const typeMatch = typeFilter === 'all' || order.type === typeFilter;
      return statusMatch && typeMatch;
    });

    return filtered;
  };

  // Get unique statuses from actual orders data with counts
  const getAvailableStatuses = () => {
    const statuses = [...new Set(orders.map(order => order.status))];
    return statuses.sort();
  };

  // Get status count for display
  const getStatusCount = (status) => {
    return orders.filter(order => order.status === status).length;
  };

  if (loading) {
    return (
      <div className="employee-orders">
        <LoadingSpinner />
      </div>
    );
  }

  return (
    <div className="employee-orders">
      <div className="orders-header">
        <h1>Order Management</h1>
        <p className="orders-subtitle">
          View and manage incoming orders with limited employee access.
        </p>
      </div>

      {/* Filters */}
      <div className="orders-filters">
        <div className="filter-group">
          <label>Status:</label>
          <select
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value)}
            className="filter-select"
          >
            <option value="all">All Statuses ({orders.length})</option>
            {getAvailableStatuses().map(status => (
              <option key={status} value={status}>
                {status} ({getStatusCount(status)})
              </option>
            ))}
          </select>
        </div>

        <div className="filter-group">
          <label>Type:</label>
          <select
            value={typeFilter}
            onChange={(e) => setTypeFilter(e.target.value)}
            className="filter-select"
          >
            <option value="all">All Types</option>
            <option value="online">Delivery</option>
            <option value="pickup">Take Away</option>
          </select>
        </div>

        <Button
          variant="outline"
          onClick={fetchOrders}
          disabled={loading}
        >
           Refresh
        </Button>
      </div>

      {/* Orders Table */}
      <DataTable
        title={`Orders (${getFilteredOrders().length})`}
        data={getFilteredOrders()}
        columns={orderColumns}
        pageSize={10}
        emptyMessage="No orders found matching the current filters"
        onRowClick={(order) => {
          setSelectedOrder(order);
          setShowOrderModal(true);
        }}
      />

      {/* Order Details Modal */}
      {showOrderModal && selectedOrder && (
        <Modal
          isOpen={showOrderModal}
          onClose={() => {
            setShowOrderModal(false);
            setSelectedOrder(null);
          }}
          title={`Order Details - ${selectedOrder.id}`}
        >
          <div className="order-details">
            <div className="order-info-grid">
              <div className="info-section">
                <h4>Customer Information</h4>
                <p><strong>Name:</strong> {selectedOrder.customerName}</p>
                <p><strong>Phone:</strong> {selectedOrder.customerPhone}</p>
              </div>

              <div className="info-section">
                <h4>Order Information</h4>
                <p><strong>Order ID:</strong> {selectedOrder.id}</p>
                <p><strong>Type:</strong>
                  <span className={`order-type-badge order-type-badge--${selectedOrder.type.replace(' ', '-')}`}>
                    {(() => {
                      const typeDisplay = {
                        'delivery': ' Delivery',
                        'pickup': ' Take Away',
                        'dine in': ' Dine In',
                        'online': ' Delivery' // Legacy support
                      };
                      return typeDisplay[selectedOrder.type] || ' Take Away';
                    })()}
                  </span>
                </p>
                <p><strong>Status:</strong>
                  <span className={`status-badge status-badge--${selectedOrder.status.toLowerCase().replace(/\s+/g, '-')}`}>
                    {selectedOrder.status}
                  </span>
                </p>
                <p><strong>Order Time:</strong> {new Date(selectedOrder.timestamp).toLocaleString()}</p>
                <p><strong>Payment Method:</strong> {selectedOrder.paymentMethod}</p>
              </div>
            </div>

            {/* Delivery/Pickup Information */}
            {(selectedOrder.type === 'online' || selectedOrder.type === 'delivery') && selectedOrder.deliveryAddress && (
              <div className="delivery-section">
                <h3>Delivery Information</h3>
                <p>Address: {selectedOrder.deliveryAddress}</p>
                {selectedOrder.deliveryInstructions && (
                  <div className="delivery-instructions">
                    <strong>Instructions:</strong> {selectedOrder.deliveryInstructions}
                  </div>
                )}
              </div>
            )}

            {selectedOrder.type === 'pickup' && selectedOrder.tableNumber && (
              <div className="pickup-section">
                <h3>Take Away Information</h3>
                <p>Table Number: {selectedOrder.tableNumber}</p>
              </div>
            )}

            <div className="info-section">
              <h4>Items Ordered</h4>
              <div className="items-list">
                {selectedOrder.items.map((item, index) => (
                  <div key={index} className="item-row">
                    <span className="item-name">{item.name}</span>
                    <span className="item-quantity">Qty: {item.quantity}</span>
                    <span className="item-price">{(item.price * item.quantity).toFixed(2)}</span>
                  </div>
                ))}
                <div className="total-row">
                  <strong>Total: {selectedOrder.total.toFixed(2)}</strong>
                </div>
              </div>
            </div>



            {/* Status Update Actions */}
            <div className="status-actions">
              <h4>Update Status</h4>
              <div className="status-buttons">
                {getStatusOptions(selectedOrder.status, selectedOrder.type).map(status => (
                  <Button
                    key={status}
                    variant={status === selectedOrder.status ? 'primary' : 'outline'}
                    onClick={() => handleStatusUpdate(selectedOrder.id, status)}
                    disabled={updating || status === selectedOrder.status}
                  >
                    {status}
                  </Button>
                ))}
              </div>
            </div>

            {/* Download Invoice */}
            <div className="invoice-actions">
              <h4>Invoice</h4>
              <Button
                variant="outline"
                onClick={async () => await handleDownloadInvoice(selectedOrder)}
              >
                 Download Invoice
              </Button>
            </div>
          </div>
        </Modal>
      )}

      {/* Toast Notification */}
      <Toast
        message={toast.message}
        type={toast.type}
        isVisible={toast.show}
        onClose={hideToast}
      />
    </div>
  );
}

export default EmployeeOrdersPage;