// PWA Service for handling service worker and PWA features
class PWAService {
  constructor() {
    this.swRegistration = null;
    this.isOnline = navigator.onLine;
    this.installPrompt = null;
    this.isInstalled = false;
    
    this.init();
  }

  async init() {
    // Register service worker
    await this.registerServiceWorker();
    
    // Setup online/offline listeners
    this.setupNetworkListeners();
    
    // Setup install prompt
    this.setupInstallPrompt();
    
    // Check if app is already installed
    this.checkInstallStatus();
  }

  async registerServiceWorker() {
    if ('serviceWorker' in navigator) {
      try {
        console.log('PWA: Registering service worker...');
        
        this.swRegistration = await navigator.serviceWorker.register('/sw.js', {
          scope: '/'
        });

        console.log('PWA: Service worker registered successfully');

        // Handle service worker updates
        this.swRegistration.addEventListener('updatefound', () => {
          const newWorker = this.swRegistration.installing;
          
          newWorker.addEventListener('statechange', () => {
            if (newWorker.state === 'installed' && navigator.serviceWorker.controller) {
              // New service worker is available
              this.showUpdateAvailable();
            }
          });
        });

        // Listen for messages from service worker
        navigator.serviceWorker.addEventListener('message', (event) => {
          console.log('PWA: Message from service worker:', event.data);
        });

      } catch (error) {
        console.error('PWA: Service worker registration failed:', error);
      }
    } else {
      console.warn('PWA: Service workers not supported');
    }
  }

  setupNetworkListeners() {
    window.addEventListener('online', () => {
      console.log('PWA: Back online');
      this.isOnline = true;
      this.handleOnline();
    });

    window.addEventListener('offline', () => {
      console.log('PWA: Gone offline');
      this.isOnline = false;
      this.handleOffline();
    });
  }

  setupInstallPrompt() {
    window.addEventListener('beforeinstallprompt', (event) => {
      console.log('PWA: Install prompt available');
      event.preventDefault();
      this.installPrompt = event;
      
      // Show custom install button
      this.showInstallButton();
    });

    window.addEventListener('appinstalled', () => {
      console.log('PWA: App installed successfully');
      this.isInstalled = true;
      this.hideInstallButton();
      this.showInstallSuccess();
    });
  }

  checkInstallStatus() {
    // Check if app is running in standalone mode (installed)
    if (window.matchMedia('(display-mode: standalone)').matches || 
        window.navigator.standalone === true) {
      this.isInstalled = true;
      console.log('PWA: App is running in installed mode');
    }
  }

  async promptInstall() {
    if (!this.installPrompt) {
      console.log('PWA: Install prompt not available');
      return false;
    }

    try {
      const result = await this.installPrompt.prompt();
      console.log('PWA: Install prompt result:', result.outcome);
      
      if (result.outcome === 'accepted') {
        this.installPrompt = null;
        return true;
      }
      
      return false;
    } catch (error) {
      console.error('PWA: Install prompt failed:', error);
      return false;
    }
  }

  showInstallButton() {
    // Create and show install button
    const installButton = document.createElement('button');
    installButton.id = 'pwa-install-button';
    installButton.innerHTML = ' Install App';
    installButton.className = 'pwa-install-button';
    installButton.onclick = () => this.promptInstall();
    
    // Add styles
    installButton.style.cssText = `
      position: fixed;
      bottom: 20px;
      right: 20px;
      background: #8B4513;
      color: white;
      border: none;
      padding: 12px 20px;
      border-radius: 25px;
      font-size: 14px;
      font-weight: 600;
      cursor: pointer;
      box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
      z-index: 1000;
      transition: all 0.3s ease;
      display: flex;
      align-items: center;
      gap: 8px;
    `;

    installButton.onmouseover = () => {
      installButton.style.background = '#D2691E';
      installButton.style.transform = 'translateY(-2px)';
    };

    installButton.onmouseout = () => {
      installButton.style.background = '#8B4513';
      installButton.style.transform = 'translateY(0)';
    };

    document.body.appendChild(installButton);
  }

  hideInstallButton() {
    const installButton = document.getElementById('pwa-install-button');
    if (installButton) {
      installButton.remove();
    }
  }

  showInstallSuccess() {
    this.showNotification('App Installed Successfully! ', 'success');
  }

  showUpdateAvailable() {
    const updateButton = document.createElement('div');
    updateButton.innerHTML = `
      <div style="
        position: fixed;
        top: 20px;
        right: 20px;
        background: #4CAF50;
        color: white;
        padding: 15px 20px;
        border-radius: 8px;
        box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
        z-index: 1001;
        max-width: 300px;
      ">
        <div style="font-weight: 600; margin-bottom: 8px;">
           Update Available
        </div>
        <div style="font-size: 14px; margin-bottom: 12px;">
          A new version of the app is available.
        </div>
        <button onclick="pwaService.applyUpdate()" style="
          background: white;
          color: #4CAF50;
          border: none;
          padding: 8px 16px;
          border-radius: 4px;
          cursor: pointer;
          font-weight: 600;
          margin-right: 8px;
        ">Update Now</button>
        <button onclick="this.parentElement.parentElement.remove()" style="
          background: transparent;
          color: white;
          border: 1px solid white;
          padding: 8px 16px;
          border-radius: 4px;
          cursor: pointer;
        ">Later</button>
      </div>
    `;
    
    document.body.appendChild(updateButton);
  }

  async applyUpdate() {
    if (this.swRegistration && this.swRegistration.waiting) {
      this.swRegistration.waiting.postMessage({ type: 'SKIP_WAITING' });
      window.location.reload();
    }
  }

  handleOnline() {
    this.showNotification('Back Online! ', 'success');
    
    // Trigger background sync if available
    if (this.swRegistration && this.swRegistration.sync) {
      this.swRegistration.sync.register('background-sync-orders');
    }
  }

  handleOffline() {
    this.showNotification('You\'re Offline ', 'warning');
  }

  showNotification(message, type = 'info') {
    const notification = document.createElement('div');
    const colors = {
      success: '#4CAF50',
      warning: '#FF9800',
      error: '#F44336',
      info: '#2196F3'
    };

    notification.style.cssText = `
      position: fixed;
      top: 20px;
      left: 50%;
      transform: translateX(-50%);
      background: ${colors[type]};
      color: white;
      padding: 12px 20px;
      border-radius: 8px;
      box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
      z-index: 1002;
      font-weight: 500;
      animation: slideDown 0.3s ease;
    `;

    notification.textContent = message;
    document.body.appendChild(notification);

    // Add animation styles
    const style = document.createElement('style');
    style.textContent = `
      @keyframes slideDown {
        from {
          opacity: 0;
          transform: translateX(-50%) translateY(-20px);
        }
        to {
          opacity: 1;
          transform: translateX(-50%) translateY(0);
        }
      }
    `;
    document.head.appendChild(style);

    // Remove after 3 seconds
    setTimeout(() => {
      notification.style.animation = 'slideDown 0.3s ease reverse';
      setTimeout(() => {
        if (notification.parentElement) {
          notification.remove();
        }
      }, 300);
    }, 3000);
  }

  // Cache management
  async clearCache() {
    if ('caches' in window) {
      const cacheNames = await caches.keys();
      await Promise.all(
        cacheNames.map(cacheName => caches.delete(cacheName))
      );
      console.log('PWA: Cache cleared');
    }
  }

  async getCacheSize() {
    if ('caches' in window && 'storage' in navigator && 'estimate' in navigator.storage) {
      const estimate = await navigator.storage.estimate();
      return {
        used: estimate.usage,
        available: estimate.quota,
        percentage: Math.round((estimate.usage / estimate.quota) * 100)
      };
    }
    return null;
  }

  // Offline order management
  async saveOfflineOrder(order) {
    if ('caches' in window) {
      const cache = await caches.open('sanctum-cafe-v1.0.0');
      const existingOrders = await this.getOfflineOrders();
      const updatedOrders = [...existingOrders, { ...order, timestamp: Date.now() }];
      
      await cache.put('/offline-orders', new Response(JSON.stringify(updatedOrders)));
      console.log('PWA: Order saved offline');
    }
  }

  async getOfflineOrders() {
    if ('caches' in window) {
      const cache = await caches.open('sanctum-cafe-v1.0.0');
      const response = await cache.match('/offline-orders');
      
      if (response) {
        return await response.json();
      }
    }
    return [];
  }

  // Push notification subscription
  async subscribeToPushNotifications() {
    if (!this.swRegistration) {
      console.error('PWA: Service worker not registered');
      return null;
    }

    try {
      const subscription = await this.swRegistration.pushManager.subscribe({
        userVisibleOnly: true,
        applicationServerKey: this.urlBase64ToUint8Array(process.env.REACT_APP_VAPID_PUBLIC_KEY)
      });

      console.log('PWA: Push notification subscription created');
      return subscription;
    } catch (error) {
      console.error('PWA: Push notification subscription failed:', error);
      return null;
    }
  }

  urlBase64ToUint8Array(base64String) {
    const padding = '='.repeat((4 - base64String.length % 4) % 4);
    const base64 = (base64String + padding)
      .replace(/-/g, '+')
      .replace(/_/g, '/');

    const rawData = window.atob(base64);
    const outputArray = new Uint8Array(rawData.length);

    for (let i = 0; i < rawData.length; ++i) {
      outputArray[i] = rawData.charCodeAt(i);
    }
    return outputArray;
  }

  // Get app info
  getAppInfo() {
    return {
      isOnline: this.isOnline,
      isInstalled: this.isInstalled,
      hasServiceWorker: !!this.swRegistration,
      canInstall: !!this.installPrompt
    };
  }
}

// Create global instance
const pwaService = new PWAService();

// Make it available globally for debugging
window.pwaService = pwaService;

export default pwaService;