import React, { useState, useEffect } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import orderService from '../../services/orderService';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import Button from '../../components/common/Button/Button';
import OrderCard from '../../components/delivery/OrderCard/OrderCard';
import DeliveryNavigation from '../../components/delivery/DeliveryNavigation/DeliveryNavigation';
import './AssignedOrdersPage.css';

function AssignedOrdersPage() {
  const { user } = useAuth();
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [refreshing, setRefreshing] = useState(false);

  const fetchAssignedOrders = async () => {
    try {
      setError(null);
      const assignedOrders = await orderService.getAssignedOrders(user.id);
      setOrders(assignedOrders);
    } catch (err) {
      // For development, we'll show a warning but still display sample data
      console.warn('API not available, sample data will be returned by orderService:', err);
      
      // The orderService will return sample data when API fails, so we can still try to get it
      try {
        const sampleOrders = await orderService.getAssignedOrders(user.id);
        setOrders(sampleOrders);
      } catch (sampleErr) {
        setError('Failed to fetch assigned orders. Please try again.');
        console.error('Error fetching sample orders:', sampleErr);
      }
    } finally {
      setLoading(false);
      setRefreshing(false);
    }
  };

  useEffect(() => {
    if (user && user.role === 'delivery') {
      fetchAssignedOrders();
    }
  }, [user]);

  const handleRefresh = async () => {
    setRefreshing(true);
    await fetchAssignedOrders();
  };

  const handleStatusUpdate = async (orderId, newStatus) => {
    try {
      await orderService.updateOrderStatus(orderId, newStatus);
      // Refresh the orders list to reflect the updated status
      await fetchAssignedOrders();
    } catch (err) {
      console.error('Error updating order status:', err);
      setError('Failed to update order status. Please try again.');
    }
  };

  if (loading) {
    return (
      <>
        <DeliveryNavigation />
        <div className="assigned-orders-page">
          <div className="loading-container">
            <LoadingSpinner />
            <p>Loading your assigned orders...</p>
          </div>
        </div>
      </>
    );
  }

  return (
    <>
      <DeliveryNavigation />
      <div className="assigned-orders-page">
      <div className="page-header">
        <h1>My Assigned Orders</h1>
        <Button 
          onClick={handleRefresh} 
          disabled={refreshing}
          className="refresh-btn"
        >
          {refreshing ? 'Refreshing...' : 'Refresh'}
        </Button>
      </div>

      {error && (
        <div className="error-message">
          <p>{error}</p>
          <Button onClick={handleRefresh}>Try Again</Button>
        </div>
      )}

      <div className="orders-summary">
        <div className="summary-card">
          <h3>Total Orders</h3>
          <span className="count">{orders.length}</span>
        </div>
        <div className="summary-card">
          <h3>Pending Pickup</h3>
          <span className="count">
            {orders.filter(order => order.status === 'Cooking in Progress').length}
          </span>
        </div>
        <div className="summary-card">
          <h3>Out for Delivery</h3>
          <span className="count">
            {orders.filter(order => order.status === 'On the Way to Delivery').length}
          </span>
        </div>
      </div>

      <div className="orders-container">
        {orders.length === 0 ? (
          <div className="no-orders">
            <h3>No orders assigned</h3>
            <p>You don't have any orders assigned for delivery at the moment.</p>
            <Button onClick={handleRefresh}>Check for New Orders</Button>
          </div>
        ) : (
          <div className="orders-list">
            {orders.map(order => (
              <OrderCard 
                key={order._id} 
                order={order} 
                onStatusUpdate={handleStatusUpdate}
              />
            ))}
          </div>
        )}
      </div>
    </div>
    </>
  );
}

export default AssignedOrdersPage;