import api from './api';
import mockNotificationBackend from './mockNotificationBackend';

class NotificationService {
  constructor() {
    this.subscriptions = new Map(); // Store user subscriptions
    this.isSupported = 'Notification' in window && 'serviceWorker' in navigator && 'PushManager' in window;
  }

  // Check if notifications are supported
  isNotificationSupported() {
    return this.isSupported;
  }

  // Request notification permission
  async requestPermission() {
    if (!this.isSupported) {
      throw new Error('Notifications not supported');
    }

    const permission = await Notification.requestPermission();
    console.log('Notification permission:', permission);
    return permission === 'granted';
  }

  // Subscribe user to push notifications
  async subscribeToPushNotifications(userId, userRole) {
    try {
      if (!this.isSupported) {
        throw new Error('Push notifications not supported');
      }

      // Request permission first
      const hasPermission = await this.requestPermission();
      if (!hasPermission) {
        throw new Error('Notification permission denied');
      }

      // Get service worker registration
      const registration = await navigator.serviceWorker.ready;
      
      // Check if VAPID public key is configured
      const vapidPublicKey = process.env.REACT_APP_VAPID_PUBLIC_KEY || 'BKrYy4FhUVDePMDCEDZOixUKjOPnYG7aAo4s6iCT1hUPLlYmZupJjJQDKZhFAs-i8XrRYsnwdoz9pMNJYEotvxI';
      console.log('VAPID Public Key from env:', process.env.REACT_APP_VAPID_PUBLIC_KEY);
      console.log('VAPID Public Key being used:', vapidPublicKey);
      if (!vapidPublicKey) {
        throw new Error('VAPID public key not configured. Please set REACT_APP_VAPID_PUBLIC_KEY in your .env file.');
      }

      // Subscribe to push notifications
      const applicationServerKey = this.urlBase64ToUint8Array(vapidPublicKey);
      console.log('Attempting to subscribe with applicationServerKey:', applicationServerKey);
      
      const subscription = await registration.pushManager.subscribe({
        userVisibleOnly: true,
        applicationServerKey: applicationServerKey
      });

      // Send subscription to server
      await this.sendSubscriptionToServer(subscription, userId, userRole);
      
      // Store locally
      this.subscriptions.set(userId, subscription);
      localStorage.setItem(`push_subscription_${userId}`, JSON.stringify(subscription));

      console.log('Push notification subscription successful');
      return subscription;
    } catch (error) {
      console.error('Push notification subscription failed:', error);
      throw error;
    }
  }

  // Send subscription to server
  async sendSubscriptionToServer(subscription, userId, userRole) {
    try {
      // Try real API first, fall back to mock if it fails
      try {
        await api.post('/notifications/subscribe', {
          subscription,
          userId,
          userRole,
          timestamp: new Date().toISOString()
        });
      } catch (apiError) {
        console.log('Real API not available, using mock backend');
        await mockNotificationBackend.storeSubscription(userId, userRole, subscription);
      }
    } catch (error) {
      console.error('Failed to send subscription to server:', error);
      throw error;
    }
  }

  // Unsubscribe from push notifications
  async unsubscribeFromPushNotifications(userId) {
    try {
      const registration = await navigator.serviceWorker.ready;
      const subscription = await registration.pushManager.getSubscription();
      
      if (subscription) {
        await subscription.unsubscribe();
        
        // Try real API first, fall back to mock
        try {
          await api.post('/notifications/unsubscribe', { userId });
        } catch (apiError) {
          console.log('Real API not available, using mock backend');
          await mockNotificationBackend.removeSubscription(userId);
        }
        
        this.subscriptions.delete(userId);
        localStorage.removeItem(`push_subscription_${userId}`);
        
        console.log('Push notification unsubscription successful');
        return true;
      }
      return false;
    } catch (error) {
      console.error('Push notification unsubscription failed:', error);
      return false;
    }
  }

  // Send notification to specific users (admin/employees)
  async sendOrderNotification(orderData, targetRoles = ['admin', 'employee']) {
    try {
      const notificationData = {
        type: 'new_order',
        title: `New Order #${orderData.id}`,
        body: `${orderData.customerName} placed an order for $${orderData.total}`,
        orderData,
        timestamp: new Date().toISOString()
      };

      // Try real API first, fall back to mock
      try {
        await api.post('/notifications/send-order-notification', {
          ...notificationData,
          targetRoles
        });
      } catch (apiError) {
        console.log('Real API not available, using mock backend');
        await mockNotificationBackend.sendNotificationToRoles(notificationData, targetRoles);
      }
      
      console.log('Order notification sent');
    } catch (error) {
      console.error('Failed to send order notification:', error);
    }
  }

  // Send status update notification to customer
  async sendStatusUpdateNotification(orderId, customerId, status, message) {
    try {
      const notificationData = {
        type: 'order_status',
        title: `Order Update #${orderId}`,
        body: message,
        orderData: { id: orderId, status },
        timestamp: new Date().toISOString()
      };

      // Try real API first, fall back to mock
      try {
        await api.post('/notifications/send-status-notification', {
          ...notificationData,
          customerId
        });
      } catch (apiError) {
        console.log('Real API not available, using mock backend');
        await mockNotificationBackend.sendNotificationToUser(customerId, notificationData);
      }
      
      console.log('Status update notification sent');
    } catch (error) {
      console.error('Failed to send status update notification:', error);
    }
  }

  // Show local notification (fallback)
  showLocalNotification(title, options = {}) {
    if (!this.isSupported) {
      console.warn('Notifications not supported');
      return;
    }

    if (Notification.permission === 'granted') {
      const notification = new Notification(title, {
        icon: '/icons/icon-192x192.png',
        badge: '/icons/icon-72x72.png',
        ...options
      });

      // Auto close after 5 seconds
      setTimeout(() => {
        notification.close();
      }, 5000);

      return notification;
    }
  }

  // Get stored subscription for user
  getStoredSubscription(userId) {
    const stored = localStorage.getItem(`push_subscription_${userId}`);
    return stored ? JSON.parse(stored) : null;
  }

  // Check if user is subscribed
  isUserSubscribed(userId) {
    return this.subscriptions.has(userId) || !!this.getStoredSubscription(userId);
  }

  // Utility function to convert VAPID key
  urlBase64ToUint8Array(base64String) {
    console.log('Converting VAPID key:', base64String);
    try {
      const padding = '='.repeat((4 - base64String.length % 4) % 4);
      const base64 = (base64String + padding)
        .replace(/-/g, '+')
        .replace(/_/g, '/');

      const rawData = window.atob(base64);
      const outputArray = new Uint8Array(rawData.length);

      for (let i = 0; i < rawData.length; ++i) {
        outputArray[i] = rawData.charCodeAt(i);
      }
      console.log('Converted VAPID key to Uint8Array:', outputArray);
      return outputArray;
    } catch (error) {
      console.error('Error converting VAPID key:', error);
      throw error;
    }
  }

  // Handle incoming push messages (called from service worker)
  handlePushMessage(data) {
    const { type, title, body, orderData } = data;
    
    switch (type) {
      case 'new_order':
        this.showLocalNotification(` New Order #${orderData.id}`, {
          body: `${orderData.customerName} placed an order for $${orderData.total}`,
          tag: `order-${orderData.id}`,
          requireInteraction: true,
          actions: [
            { action: 'view', title: 'View Order' },
            { action: 'dismiss', title: 'Dismiss' }
          ]
        });
        break;
        
      case 'order_status':
        this.showLocalNotification(` Order Update #${orderData.id}`, {
          body: `Your order is now ${orderData.status}`,
          tag: `status-${orderData.id}`,
          actions: [
            { action: 'track', title: 'Track Order' },
            { action: 'dismiss', title: 'OK' }
          ]
        });
        break;
        
      default:
        this.showLocalNotification(title, { body });
    }
  }
}

// Create singleton instance
const notificationService = new NotificationService();

// Make available globally for service worker
window.notificationService = notificationService;

export default notificationService;