import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useCart } from '../../contexts/CartContext';
import Button from '../../components/common/Button/Button';
import { formatPrice } from '../../utils/helpers';
import './CartPage.css';

function CartPage() {
  const { 
    cartItems, 
    removeFromCart, 
    updateQuantity, 
    clearCart, 
    getTotalItems, 
    getSubtotal 
  } = useCart();
  
  const navigate = useNavigate();

  const handleQuantityChange = (itemId, newQuantity) => {
    if (newQuantity <= 0) {
      removeFromCart(itemId);
    } else {
      updateQuantity(itemId, newQuantity);
    }
  };

  const handleProceedToCheckout = () => {
    navigate('/customer/checkout');
  };

  const calculateTax = (subtotal) => {
    return subtotal * 0.08; // 8% tax rate
  };

  const calculateTotal = (subtotal) => {
    return subtotal;
  };

  const subtotal = getSubtotal();
  const tax = calculateTax(subtotal);
  const total = calculateTotal(subtotal);

  if (cartItems.length === 0) {
    return (
      <div className="cart-page">
        <div className="container">
          <div className="cart-header">
            <h1>Your Cart</h1>
          </div>
          
          <div className="empty-cart">
            <div className="empty-cart-icon"></div>
            <h2>Your cart is empty</h2>
            <p>Looks like you haven't added any items to your cart yet.</p>
            <Link to="/customer/menu">
              <Button variant="primary" size="large">
                Browse Menu
              </Button>
            </Link>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="cart-page">
      <div className="container">
        <div className="cart-header">
          <h1>Your Cart</h1>
          <div className="cart-summary-header">
            <span className="item-count">{getTotalItems()} items</span>
            <Button 
              variant="outline" 
              size="small" 
              onClick={clearCart}
              className="clear-cart-btn"
            >
              Clear Cart
            </Button>
          </div>
        </div>

        <div className="cart-content">
          <div className="cart-items">
            {cartItems.map(item => (
              <div key={item.id} className="cart-item">
                <div className="cart-item-image">
                  <img 
                    src={item.image || `https://via.placeholder.com/100x100/8B4513/FFFFFF?text=${encodeURIComponent(item.name)}`} 
                    alt={item.name}
                    onError={(e) => {
                      e.target.src = `https://via.placeholder.com/100x100/8B4513/FFFFFF?text=${encodeURIComponent(item.name)}`;
                    }}
                  />
                </div>
                
                <div className="cart-item-details">
                  <div className="cart-item-info">
                    <h3 className="cart-item-name">{item.name}</h3>
                    <p className="cart-item-description">{item.description}</p>
                    <div className="cart-item-meta">
                      <span className="cart-item-category">{item.category}</span>
                      <span className="cart-item-prep-time"> {item.preparationTime} min</span>
                    </div>
                  </div>
                  
                  <div className="cart-item-actions">
                    <div className="quantity-controls">
                      <button 
                        className="quantity-btn"
                        onClick={() => handleQuantityChange(item.id, item.quantity - 1)}
                        aria-label="Decrease quantity"
                      >
                        -
                      </button>
                      <span className="quantity-display">{item.quantity}</span>
                      <button 
                        className="quantity-btn"
                        onClick={() => handleQuantityChange(item.id, item.quantity + 1)}
                        aria-label="Increase quantity"
                      >
                        +
                      </button>
                    </div>
                    
                    <div className="cart-item-pricing">
                      <div className="unit-price">{formatPrice(item.price)} each</div>
                      <div className="total-price">{formatPrice(item.price * item.quantity)}</div>
                    </div>
                    
                    <button 
                      className="remove-btn"
                      onClick={() => removeFromCart(item.id)}
                      aria-label={`Remove ${item.name} from cart`}
                    >
                      
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>

          <div className="cart-sidebar">
            <div className="order-summary">
              <h3>Order Summary</h3>
              
              <div className="summary-line">
                <span>Subtotal ({getTotalItems()} items)</span>
                <span>{formatPrice(subtotal)}</span>
              </div>
              
              {/* <div className="summary-line">
                <span>Tax</span>
                <span>{formatPrice(tax)}</span>
              </div> */}
              
              <div className="summary-line summary-total">
                <span>Total</span>
                <span>{formatPrice(total)}</span>
              </div>
              
              <div className="checkout-actions">
                <Button 
                  variant="primary" 
                  size="large" 
                  fullWidth
                  onClick={handleProceedToCheckout}
                  className="checkout-btn"
                >
                  Proceed to Checkout
                </Button>
                
                <Link to="/customer/menu">
                  <Button variant="outline" size="medium" fullWidth>
                    Continue Shopping
                  </Button>
                </Link>
              </div>
            </div>
            
            <div className="delivery-info">
              <h4>Delivery Information</h4>
              <div className="delivery-details">
                <div className="delivery-item">
                  <span className="delivery-icon"></span>
                  <div>
                    <div className="delivery-title">Free Delivery</div>
                    <div className="delivery-subtitle">On orders over 25</div>
                  </div>
                </div>
                
                <div className="delivery-item">
                  <span className="delivery-icon"></span>
                  <div>
                    <div className="delivery-title">Estimated Time</div>
                    <div className="delivery-subtitle">25-35 minutes</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CartPage;