import React from 'react';
import './StatCard.css';

function StatCard({ 
  title, 
  value, 
  icon, 
  trend, 
  trendValue, 
  color = 'primary',
  size = 'medium',
  onClick 
}) {
  const getTrendIcon = () => {
    if (trend === 'up') return '';
    if (trend === 'down') return '';
    return '';
  };

  const getTrendColor = () => {
    if (trend === 'up') return 'var(--success-color)';
    if (trend === 'down') return 'var(--error-color)';
    return 'var(--text-muted)';
  };

  return (
    <div 
      className={`stat-card stat-card--${color} stat-card--${size} ${onClick ? 'stat-card--clickable' : ''}`}
      onClick={onClick}
    >
      <div className="stat-card__header">
        <div className="stat-card__icon">
          {icon}
        </div>
        {trendValue && (
          <div className="stat-card__trend" style={{ color: getTrendColor() }}>
            <span className="trend-icon">{getTrendIcon()}</span>
            <span className="trend-value">{trendValue}</span>
          </div>
        )}
      </div>
      
      <div className="stat-card__content">
        <div className="stat-card__value">{value}</div>
        <div className="stat-card__title">{title}</div>
      </div>
    </div>
  );
}

export default StatCard;