import React, { useState, useEffect } from 'react';
import adminService from '../../services/adminService';
import DataTable from '../../components/admin/DataTable/DataTable';
import StatCard from '../../components/admin/StatCard/StatCard';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import Button from '../../components/common/Button/Button';
import './CustomerAnalyticsPage.css';

function CustomerAnalyticsPage() {
  const [analyticsData, setAnalyticsData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [dateRange, setDateRange] = useState('30d');

  useEffect(() => {
    loadAnalytics();
  }, [dateRange]);

  const loadAnalytics = async () => {
    try {
      setLoading(true);
      setError('');
      const result = await adminService.getCustomerAnalytics(dateRange);
      if (result.success) {
        console.log('Analytics data received:', result.data);
        setAnalyticsData(result.data);
      } else {
        setError('Failed to load analytics data. Please try again.');
      }
    } catch (err) {
      setError('Failed to load analytics data. Please try again.');
      console.error('Analytics loading error:', err);
    } finally {
      setLoading(false);
    }
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('en-IN', {
      style: 'currency',
      currency: 'INR'
    }).format(amount);
  };

  const formatPercentage = (value) => {
    return `${value.toFixed(1)}%`;
  };

  const topCustomersColumns = [
    {
      key: 'name',
      title: 'Customer Name',
      width: '200px'
    },
    {
      key: 'orders',
      title: 'Total Orders',
      align: 'center',
      width: '120px'
    },
    {
      key: 'totalSpent',
      title: 'Total Spent',
      type: 'currency',
      align: 'right',
      width: '120px'
    },
    {
      key: 'averageOrder',
      title: 'Avg Order Value',
      render: (_, customer) => formatCurrency(customer.totalSpent / customer.orders),
      align: 'right',
      width: '140px'
    },
    {
      key: 'lastOrder',
      title: 'Last Order',
      render: (_, customer) => {
        if (customer.lastOrderDate) {
          const daysDiff = Math.floor((new Date() - new Date(customer.lastOrderDate)) / (1000 * 60 * 60 * 24));
          return `${daysDiff} days ago`;
        }
        return 'N/A';
      },
      width: '120px'
    }
  ];

  if (loading) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-loading">
            <LoadingSpinner size="large" />
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-error">
            <h1>Error Loading Analytics</h1>
            <p>{error}</p>
            <Button onClick={loadAnalytics} variant="primary">
              Try Again
            </Button>
          </div>
        </div>
      </div>
    );
  }

  if (!analyticsData || !analyticsData.totalCustomers) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-error">
            <h1>No Analytics Data</h1>
            <p>No analytics data available.</p>
            <Button onClick={loadAnalytics} variant="primary">
              Load Data
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="customer-analytics-page">
      <div className="container">
        <div className="analytics-header">
          <div className="analytics-title-section">
            <h1>Customer Analytics</h1>
            <p>Track customer activity and analyze ordering patterns</p>
          </div>
          
          <div className="analytics-controls">
            <div className="date-range-selector">
              {[
                { value: '7d', label: '7 Days' },
                { value: '30d', label: '30 Days' },
                { value: '90d', label: '90 Days' },
                { value: '1y', label: '1 Year' }
              ].map(range => (
                <button
                  key={range.value}
                  className={`range-btn ${dateRange === range.value ? 'active' : ''}`}
                  onClick={() => setDateRange(range.value)}
                >
                  {range.label}
                </button>
              ))}
            </div>
            
            <Button
              variant="outline"
              onClick={loadAnalytics}
              disabled={loading}
            >
               Refresh
            </Button>
          </div>
        </div>

        <div className="analytics-stats">
          <StatCard
            title="Total Customers"
            value={(analyticsData.totalCustomers || 0).toLocaleString()}
            icon=""
            color="primary"
          />
          
          <StatCard
            title="New Customers"
            value={analyticsData.newCustomers || 0}
            icon=""
            color="success"
            trend="up"
            trendValue="+15%"
          />
          
          <StatCard
            title="Returning Customers"
            value={(analyticsData.returningCustomers || 0).toLocaleString()}
            icon=""
            color="info"
          />
          
          <StatCard
            title="Retention Rate"
            value={formatPercentage(analyticsData.customerRetentionRate || 0)}
            icon=""
            color="warning"
            trend="up"
            trendValue="+2.3%"
          />
          
          <StatCard
            title="Avg Orders/Customer"
            value={(analyticsData.averageOrdersPerCustomer || 0).toFixed(1)}
            icon=""
            color="secondary"
          />
        </div>

        <div className="analytics-content">
          <div className="analytics-main">
            <div className="analytics-section">
              <DataTable
                title="Top Customers"
                data={analyticsData.topCustomers || []}
                columns={topCustomersColumns}
                pagination={false}
                searchable={false}
                actions={[
                  {
                    label: 'Export Data',
                    variant: 'outline',
                    onClick: () => console.log('Export customer data')
                  }
                ]}
              />
            </div>

            <div className="analytics-section">
              <div className="chart-container">
                <h3>Customer Growth Trend</h3>
                <div className="simple-chart">
                  <div className="chart-bars">
                    {(analyticsData.customerGrowth?.data || []).length > 0 ? (
                      (analyticsData.customerGrowth.data || []).map((value, index) => (
                        <div key={index} className="chart-bar">
                          <div 
                            className="bar-fill"
                            style={{ 
                              height: `${(value / Math.max(...(analyticsData.customerGrowth?.data || [1]))) * 100}%` 
                            }}
                          ></div>
                          <span className="bar-label">{analyticsData.customerGrowth?.labels?.[index] || `Period ${index + 1}`}</span>
                          <span className="bar-value">{value}</span>
                        </div>
                      ))
                    ) : (
                      <div className="no-chart-data">
                        <p>No growth data available</p>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="analytics-sidebar">
            <div className="analytics-section">
              <div className="insights-card">
                <h3> Key Insights</h3>
                <div className="insights-list">
                  <div className="insight-item">
                    <div className="insight-icon"></div>
                    <div className="insight-content">
                      <h4>Customer Loyalty</h4>
                      <p>
                        {formatPercentage(analyticsData.customerRetentionRate || 0)} of customers 
                        return for repeat orders, indicating strong customer satisfaction.
                      </p>
                    </div>
                  </div>
                  
                  <div className="insight-item">
                    <div className="insight-icon"></div>
                    <div className="insight-content">
                      <h4>Growth Trend</h4>
                      <p>
                        New customer acquisition has increased by 15% compared to the previous period.
                      </p>
                    </div>
                  </div>
                  
                  <div className="insight-item">
                    <div className="insight-icon"></div>
                    <div className="insight-content">
                      <h4>Average Spending</h4>
                      <p>
                        Top customers spend an average of {formatCurrency(
                          (analyticsData.topCustomers || []).length > 0 
                            ? (analyticsData.topCustomers.reduce((sum, c) => sum + (c.totalSpent || 0), 0) / analyticsData.topCustomers.length)
                            : 0
                        )} per customer.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div className="analytics-section">
              <div className="frequency-card">
                <h3> Order Frequency</h3>
                <div className="frequency-stats">
                  <div className="frequency-item">
                    <span className="frequency-label">Daily Average</span>
                    <span className="frequency-value">{analyticsData.orderFrequency?.daily || 0}</span>
                  </div>
                  <div className="frequency-item">
                    <span className="frequency-label">Weekly Average</span>
                    <span className="frequency-value">{analyticsData.orderFrequency?.weekly || 0}</span>
                  </div>
                  <div className="frequency-item">
                    <span className="frequency-label">Monthly Average</span>
                    <span className="frequency-value">{analyticsData.orderFrequency?.monthly || 0}</span>
                  </div>
                </div>
              </div>
            </div>

            <div className="analytics-section">
              <div className="recommendations-card">
                <h3> Recommendations</h3>
                <div className="recommendations-list">
                  <div className="recommendation-item">
                    <div className="recommendation-icon"></div>
                    <div className="recommendation-text">
                      Implement a loyalty program to increase customer retention rate beyond {formatPercentage(analyticsData.customerRetentionRate || 0)}.
                    </div>
                  </div>
                  
                  <div className="recommendation-item">
                    <div className="recommendation-icon"></div>
                    <div className="recommendation-text">
                      Send targeted promotions to customers who haven't ordered in the last 30 days.
                    </div>
                  </div>
                  
                  <div className="recommendation-item">
                    <div className="recommendation-icon"></div>
                    <div className="recommendation-text">
                      Focus on upselling to increase the average order value from ${(analyticsData.averageOrdersPerCustomer || 0).toFixed(2)}.
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CustomerAnalyticsPage;