import React from 'react';
import { useCart } from '../../../contexts/CartContext';
import Button from '../Button/Button';
import './MenuCard.css';

function MenuCard({ item, showAddToCart = true, quantity = 1, onQuantityChange }) {
  const { addToCart } = useCart();

  const handleAddToCart = () => {
    addToCart(item, quantity);
  };

  const handleQuantityChange = (newQuantity) => {
    if (onQuantityChange) {
      onQuantityChange(newQuantity);
    }
  };

  return (
    <div className={`menu-card ${!item.isAvailable ? 'menu-card--unavailable' : ''}`}>
      <div className="menu-card__image">
        <img 
          src={item.image || `https://via.placeholder.com/300x200/8B4513/FFFFFF?text=${encodeURIComponent(item.name)}`} 
          alt={item.name}
          onError={(e) => {
            e.target.src = `https://via.placeholder.com/300x200/8B4513/FFFFFF?text=${encodeURIComponent(item.name)}`;
          }}
        />
        {!item.isAvailable && (
          <div className="menu-card__unavailable-overlay">
            <span>Currently Unavailable</span>
          </div>
        )}
        <div className="menu-card__category">{item.category}</div>
      </div>
      
      <div className="menu-card__content">
        <div className="menu-card__header">
          <h3 className="menu-card__name">{item.name}</h3>
          <span className="menu-card__price">{item.price.toFixed(2)}</span>
        </div>
        
        <p className="menu-card__description">{item.description}</p>
        
        <div className="menu-card__details">
          <div className="menu-card__prep-time">
            <span className="icon"></span>
            <span>{item.preparationTime} min</span>
          </div>
          
          {item.nutritionalInfo && (
            <div className="menu-card__nutrition">
              <span className="icon"></span>
              <span>{item.nutritionalInfo.calories} cal</span>
              {item.nutritionalInfo.caffeine !== '0mg' && (
                <>
                  <span className="separator"></span>
                  <span> {item.nutritionalInfo.caffeine}</span>
                </>
              )}
            </div>
          )}
        </div>
        
        {item.allergens && item.allergens.length > 0 && (
          <div className="menu-card__allergens">
            <span className="menu-card__allergens-label">Contains:</span>
            <span className="menu-card__allergens-list">
              {item.allergens.join(', ')}
            </span>
          </div>
        )}
        
        {showAddToCart && (
          <div className="menu-card__actions">
            {onQuantityChange && (
              <div className="quantity-selector">
                <button 
                  className="quantity-btn"
                  onClick={() => handleQuantityChange(Math.max(1, quantity - 1))}
                  disabled={quantity <= 1}
                >
                  -
                </button>
                <span className="quantity-display">{quantity}</span>
                <button 
                  className="quantity-btn"
                  onClick={() => handleQuantityChange(quantity + 1)}
                >
                  +
                </button>
              </div>
            )}
            
            <Button
              onClick={handleAddToCart}
              disabled={!item.isAvailable}
              variant="primary"
              className="add-to-cart-btn"
            >
              {!item.isAvailable ? 'Unavailable' : 'Add to Cart'}
            </Button>
          </div>
        )}
      </div>
    </div>
  );
}

export default MenuCard;