import React, { useState } from 'react';
import Button from '../../common/Button/Button';
import Input from '../../common/Input/Input';
import './DataTable.css';

function DataTable({ 
  data = [], 
  columns = [], 
  title,
  searchable = true,
  sortable = true,
  pagination = true,
  pageSize = 10,
  actions = [],
  onRowClick,
  loading = false,
  emptyMessage = "No data available"
}) {
  const [searchTerm, setSearchTerm] = useState('');
  const [sortConfig, setSortConfig] = useState({ key: null, direction: 'asc' });
  const [currentPage, setCurrentPage] = useState(1);

  // Helper function to get nested object values
  const getNestedValue = (obj, path) => {
    return path.split('.').reduce((current, key) => current?.[key], obj);
  };

  // Filter data based on search term
  const filteredData = data.filter(row => {
    if (!searchTerm) return true;
    return columns.some(column => {
      const value = getNestedValue(row, column.key);
      return String(value).toLowerCase().includes(searchTerm.toLowerCase());
    });
  });

  // Sort data
  const sortedData = [...filteredData].sort((a, b) => {
    if (!sortConfig.key) return 0;
    
    const aValue = getNestedValue(a, sortConfig.key);
    const bValue = getNestedValue(b, sortConfig.key);
    
    if (aValue < bValue) return sortConfig.direction === 'asc' ? -1 : 1;
    if (aValue > bValue) return sortConfig.direction === 'asc' ? 1 : -1;
    return 0;
  });

  // Paginate data
  const totalPages = Math.ceil(sortedData.length / pageSize);
  const startIndex = (currentPage - 1) * pageSize;
  const paginatedData = pagination ? sortedData.slice(startIndex, startIndex + pageSize) : sortedData;

  const handleSort = (key) => {
    if (!sortable) return;
    
    setSortConfig(prevConfig => ({
      key,
      direction: prevConfig.key === key && prevConfig.direction === 'asc' ? 'desc' : 'asc'
    }));
  };

  const renderCellContent = (row, column) => {
    const value = getNestedValue(row, column.key);
    
    if (column.render) {
      return column.render(value, row);
    }
    
    if (column.type === 'currency') {
      return new Intl.NumberFormat('en-IN', {
        style: 'currency',
        currency: 'INR'
      }).format(Number(value));
    }
    
    if (column.type === 'date') {
      return new Date(value).toLocaleDateString();
    }
    
    if (column.type === 'datetime') {
      return new Date(value).toLocaleString();
    }
    
    if (column.type === 'status') {
      return (
        <span className={`status-badge status-badge--${String(value).toLowerCase().replace(/\s+/g, '-')}`}>
          {value}
        </span>
      );
    }
    
    return value;
  };

  const renderMobileCard = (row, index) => {
    // Find the primary column (usually the first non-action column)
    const primaryColumn = columns.find(col => col.key !== 'actions' && col.primary !== false) || columns[0];
    const actionsColumn = columns.find(col => col.key === 'actions');
    
    return (
      <div 
        key={row.id || index} 
        className={`mobile-card ${onRowClick ? 'clickable' : ''}`}
        onClick={() => onRowClick && onRowClick(row)}
      >
        <div className="mobile-card-header">
          <div>
            <h4 className="mobile-card-title">
              {renderCellContent(row, primaryColumn)}
            </h4>
            {columns.length > 1 && (
              <p className="mobile-card-subtitle">
                {columns[1] && renderCellContent(row, columns[1])}
              </p>
            )}
          </div>
        </div>
        
        <div className="mobile-card-body">
          {columns
            .filter(col => col.key !== 'actions' && col.key !== primaryColumn.key && col.mobileHide !== true)
            .slice(0, 6) // Limit to 6 fields to avoid overcrowding
            .map(column => (
              <div 
                key={column.key} 
                className={`mobile-card-field ${column.mobileFullWidth ? 'full-width' : ''}`}
              >
                <div className="mobile-card-label">{column.title}</div>
                <div className={`mobile-card-value ${column.type || ''}`}>
                  {renderCellContent(row, column)}
                </div>
              </div>
            ))}
        </div>
        
        {actionsColumn && (
          <div className="mobile-card-actions">
            <div className="mobile-actions-wrapper">
              {actionsColumn.render(null, row)}
            </div>
          </div>
        )}
      </div>
    );
  };

  const getSortIcon = (columnKey) => {
    if (sortConfig.key !== columnKey) return '';
    return sortConfig.direction === 'asc' ? '' : '';
  };

  if (loading) {
    return (
      <div className="data-table-container">
        <div className="data-table-loading">
          <div className="loading-spinner"></div>
          <p>Loading data...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="data-table-container">
      {title && (
        <div className="data-table-header">
          <h3 className="data-table-title">{title}</h3>
          {actions.length > 0 && (
            <div className="data-table-actions">
              {actions.map((action, index) => (
                <Button
                  key={index}
                  variant={action.variant || 'outline'}
                  size="small"
                  onClick={action.onClick}
                  className={action.className}
                >
                  {action.icon && <span className="action-icon">{action.icon}</span>}
                  {action.label}
                </Button>
              ))}
            </div>
          )}
        </div>
      )}

      {searchable && (
        <div className="data-table-search">
          <Input
            type="text"
            placeholder="Search..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="search-input"
          />
        </div>
      )}

      <div className="data-table-wrapper">
        {/* Desktop Table */}
        <table className="data-table">
          <thead>
            <tr>
              {columns.map(column => (
                <th
                  key={column.key}
                  className={`${sortable && column.sortable !== false ? 'sortable' : ''} ${column.align ? `align-${column.align}` : ''}`}
                  onClick={() => column.sortable !== false && handleSort(column.key)}
                  style={{ width: column.width }}
                >
                  <div className="th-content">
                    <span>{column.title}</span>
                    {sortable && column.sortable !== false && (
                      <span className="sort-icon">{getSortIcon(column.key)}</span>
                    )}
                  </div>
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {paginatedData.length === 0 ? (
              <tr>
                <td colSpan={columns.length} className="empty-row">
                  {emptyMessage}
                </td>
              </tr>
            ) : (
              paginatedData.map((row, index) => (
                <tr
                  key={row.id || index}
                  className={onRowClick ? 'clickable' : ''}
                  onClick={() => onRowClick && onRowClick(row)}
                >
                  {columns.map(column => (
                    <td
                      key={column.key}
                      className={column.align ? `align-${column.align}` : ''}
                    >
                      {renderCellContent(row, column)}
                    </td>
                  ))}
                </tr>
              ))
            )}
          </tbody>
        </table>

        {/* Mobile Card Layout */}
        <div className="data-table-mobile">
          {paginatedData.length === 0 ? (
            <div className="mobile-card">
              <div className="mobile-card-body">
                <div className="mobile-card-field full-width">
                  <div className="mobile-card-value" style={{ textAlign: 'center', fontStyle: 'italic', color: 'var(--text-muted)' }}>
                    {emptyMessage}
                  </div>
                </div>
              </div>
            </div>
          ) : (
            paginatedData.map((row, index) => renderMobileCard(row, index))
          )}
        </div>
      </div>

      {pagination && totalPages > 1 && (
        <div className="data-table-pagination">
          <div className="pagination-info">
            Showing {startIndex + 1} to {Math.min(startIndex + pageSize, sortedData.length)} of {sortedData.length} entries
          </div>
          <div className="pagination-controls">
            <Button
              variant="outline"
              size="small"
              onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
              disabled={currentPage === 1}
            >
              Previous
            </Button>
            
            {Array.from({ length: totalPages }, (_, i) => i + 1)
              .filter(page => {
                const distance = Math.abs(page - currentPage);
                return distance <= 2 || page === 1 || page === totalPages;
              })
              .map((page, index, array) => {
                const prevPage = array[index - 1];
                const showEllipsis = prevPage && page - prevPage > 1;
                
                return (
                  <React.Fragment key={page}>
                    {showEllipsis && <span className="pagination-ellipsis">...</span>}
                    <Button
                      variant={currentPage === page ? 'primary' : 'outline'}
                      size="small"
                      onClick={() => setCurrentPage(page)}
                      className="pagination-page"
                    >
                      {page}
                    </Button>
                  </React.Fragment>
                );
              })}
            
            <Button
              variant="outline"
              size="small"
              onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
              disabled={currentPage === totalPages}
            >
              Next
            </Button>
          </div>
        </div>
      )}
    </div>
  );
}

export default DataTable;