import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import Button from '../../components/common/Button/Button';
import Input from '../../components/common/Input/Input';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import './Auth.css';

function CustomerLogin() {
  const [formData, setFormData] = useState({
    mobile: '',
    name: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  
  const { customerLogin, loading: authLoading } = useAuth();
  const navigate = useNavigate();

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    // Validate mobile number format
    if (!/^[6-9]\d{9}$/.test(formData.mobile)) {
      setError('Please enter a valid 10-digit mobile number starting with 6-9');
      setLoading(false);
      return;
    }

    // Validate name
    if (!formData.name.trim() || formData.name.trim().length < 2) {
      setError('Please enter a valid name (at least 2 characters)');
      setLoading(false);
      return;
    }

    try {
      if (!customerLogin) {
        setError('Authentication service not available');
        setLoading(false);
        return;
      }

      const result = await customerLogin(formData.mobile, formData.name.trim());
      if (result.success) {
        navigate('/customer/menu');
      } else {
        setError(result.message || 'Login failed');
      }
    } catch (err) {
      console.error('Customer login error:', err);
      setError('An error occurred during login');
    } finally {
      setLoading(false);
    }
  };

  if (authLoading) {
    return (
      <div className="login-container">
        <div className="login-form">
          <LoadingSpinner />
          <p>Loading...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="login-container">
      <div className="login-form">
        <h1>Customer Login - The Sanctum Cafe</h1>
        
        <div className="demo-credentials">
          <h3>Customer Access:</h3>
          <p><strong>Mobile:</strong> 9876543210</p>
          <p><strong>Name:</strong> John Smith</p>
          <p className="login-note">
            <em>Enter your mobile number and name to login</em>
          </p>
        </div>

        {error && <div className="error-message">{error}</div>}
        
        <form onSubmit={handleSubmit}>
          <Input
            type="tel"
            name="mobile"
            placeholder="Mobile Number (10 digits)"
            value={formData.mobile}
            onChange={handleChange}
            pattern="[6-9][0-9]{9}"
            title="Please enter a valid 10-digit mobile number starting with 6-9"
            required
          />
          
          <Input
            type="text"
            name="name"
            placeholder="Your Full Name"
            value={formData.name}
            onChange={handleChange}
            required
          />
          
          <Button type="submit" disabled={loading}>
            {loading ? <LoadingSpinner /> : 'Login as Customer'}
          </Button>
        </form>
        
        <p>
          Don't have an account? <Link to="/register">Register here</Link>
        </p>
        <p>
          Need different access? <Link to="/">Go to Home</Link>
        </p>
      </div>
    </div>
  );
}

export default CustomerLogin;