import React, { useState, useEffect } from 'react';
import adminService from '../../services/adminService';
import DataTable from '../../components/admin/DataTable/DataTable';
import StatCard from '../../components/admin/StatCard/StatCard';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import Button from '../../components/common/Button/Button';
import Modal from '../../components/common/Modal/Modal';
import Input from '../../components/common/Input/Input';
import './MenuItemsManagementPage.css';

function MenuItemsManagementPage() {
  const [menuItems, setMenuItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [showAddModal, setShowAddModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [selectedItem, setSelectedItem] = useState(null);
  const [categoryFilter, setCategoryFilter] = useState('all');
  const [availabilityFilter, setAvailabilityFilter] = useState('all');
  const [submitting, setSubmitting] = useState(false);

  const [formData, setFormData] = useState({
    name: '',
    description: '',
    price: '',
    category: '',
    image: '',
    preparationTime: '',
    ingredients: '',
    mainIngredients: [{ name: '', quantity: '', unit: '' }],
    allergens: '',
    calories: '',
    caffeine: '',
    isAvailable: true
  });

  useEffect(() => {
    loadMenuItems();
  }, []);

  const loadMenuItems = async () => {
    try {
      setLoading(true);
      setError('');
      console.log('Loading menu items...');
      const result = await adminService.getMenuItems();
      console.log('Menu items result:', result);
      
      if (result.success) {
        const items = result.data.items || result.data;
        console.log('Setting menu items:', items);
        setMenuItems(items);
      } else {
        setError('Failed to load menu items. Please try again.');
      }
    } catch (err) {
      const errorMessage = err.response?.data?.message || err.message || 'Failed to load menu items. Please try again.';
      setError(errorMessage);
      console.error('Menu items loading error:', err);
      console.error('Error response:', err.response?.data);
    } finally {
      setLoading(false);
    }
  };

  const handleFormChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData({
      ...formData,
      [name]: type === 'checkbox' ? checked : value
    });
  };

  const resetForm = () => {
    setFormData({
      name: '',
      description: '',
      price: '',
      category: '',
      image: '',
      preparationTime: '',
      ingredients: '',
      mainIngredients: [{ name: '', quantity: '', unit: '' }],
      allergens: '',
      calories: '',
      caffeine: '',
      isAvailable: true
    });
  };

  const handleAddItem = async (e) => {
    e.preventDefault();
    try {
      setSubmitting(true);
      setError('');
      const itemData = {
        ...formData,
        price: Number(formData.price),
        preparationTime: Number(formData.preparationTime),
        ingredients: formData.ingredients.split(',').map(i => i.trim()).filter(i => i),
        mainIngredients: formData.mainIngredients.filter(ing => ing.name && ing.quantity && ing.unit).map(ing => ({
          ...ing,
          quantity: Number(ing.quantity)
        })),
        allergens: formData.allergens.split(',').map(a => a.trim()).filter(a => a),
        nutritionalInfo: {
          calories: Number(formData.calories) || 0,
          caffeine: formData.caffeine || '0mg'
        }
      };
      
      const result = await adminService.addMenuItem(itemData);
      
      if (result.success) {
        setMenuItems([...menuItems, result.data]);
        setShowAddModal(false);
        resetForm();
        // Show success message
        alert('Menu item added successfully!');
      }
    } catch (err) {
      console.error('Add item error:', err);
      setError('Failed to add menu item. Please try again.');
    } finally {
      setSubmitting(false);
    }
  };

  const handleEditItem = async (e) => {
    e.preventDefault();
    try {
      setSubmitting(true);
      setError('');
      const itemData = {
        ...formData,
        price: Number(formData.price),
        preparationTime: Number(formData.preparationTime),
        ingredients: formData.ingredients.split(',').map(i => i.trim()).filter(i => i),
        mainIngredients: formData.mainIngredients.filter(ing => ing.name && ing.quantity && ing.unit).map(ing => ({
          ...ing,
          quantity: Number(ing.quantity)
        })),
        allergens: formData.allergens.split(',').map(a => a.trim()).filter(a => a),
        nutritionalInfo: {
          calories: Number(formData.calories) || 0,
          caffeine: formData.caffeine || '0mg'
        }
      };
      
      console.log('Form data preparationTime:', formData.preparationTime);
      console.log('Converted preparationTime:', Number(formData.preparationTime));
      console.log('Item data being sent:', itemData);
      
      const result = await adminService.updateMenuItem(selectedItem._id || selectedItem.id, itemData);
      
      if (result.success) {
        console.log('Update result:', result.data);
        console.log('Selected item:', selectedItem);
        setMenuItems(menuItems.map(item => {
          if ((item._id || item.id) === (selectedItem._id || selectedItem.id)) {
            // Merge the updated data with the original item to preserve all properties
            const updatedItem = { ...item, ...result.data };
            console.log('Updated item:', updatedItem);
            return updatedItem;
          }
          return item;
        }));
        setShowEditModal(false);
        setSelectedItem(null);
        resetForm();
        alert('Menu item updated successfully!');
      }
    } catch (err) {
      console.error('Edit item error:', err);
      setError('Failed to update menu item. Please try again.');
    } finally {
      setSubmitting(false);
    }
  };

  const handleDeleteItem = async (itemId) => {
    if (window.confirm('Are you sure you want to delete this menu item?')) {
      try {
        setError('');
        const result = await adminService.deleteMenuItem(itemId);
        if (result.success) {
          setMenuItems(menuItems.filter(item => (item._id || item.id) !== itemId));
          alert('Menu item deleted successfully!');
        }
      } catch (err) {
        console.error('Delete item error:', err);
        setError('Failed to delete menu item. Please try again.');
      }
    }
  };

  const handleToggleAvailability = async (itemId, isAvailable) => {
    try {
      setError('');
      const result = await adminService.toggleMenuItemAvailability(itemId);
      if (result.success) {
        setMenuItems(menuItems.map(item => 
          (item._id || item.id) === itemId ? { ...item, isAvailable: !isAvailable } : item
        ));
        alert(`Menu item ${!isAvailable ? 'enabled' : 'disabled'} successfully!`);
      }
    } catch (err) {
      console.error('Toggle availability error:', err);
      setError('Failed to toggle menu item availability. Please try again.');
    }
  };

  const openEditModal = (item) => {
    console.log('Opening edit modal for item:', item);
    console.log('Item preparation time:', item?.preparationTime);
    setSelectedItem(item);
    const formDataToSet = {
      name: item?.name || '',
      description: item?.description || '',
      price: item?.price?.toString() || '',
      category: item?.category || '',
      image: item?.image || '',
      preparationTime: item?.preparationTime?.toString() || '',
      ingredients: Array.isArray(item?.ingredients) ? item.ingredients.join(', ') : '',
      mainIngredients: Array.isArray(item?.mainIngredients) && item.mainIngredients.length > 0 
        ? item.mainIngredients 
        : [{ name: '', quantity: '', unit: '' }],
      allergens: Array.isArray(item?.allergens) ? item.allergens.join(', ') : '',
      calories: item?.nutritionalInfo?.calories?.toString() || '',
      caffeine: item?.nutritionalInfo?.caffeine || '',
      isAvailable: item?.isAvailable ?? true
    };
    console.log('Form data being set:', formDataToSet);
    console.log('Preparation time in form data:', formDataToSet.preparationTime);
    setFormData(formDataToSet);
    setShowEditModal(true);
  };

  const getFilteredMenuItems = () => {
    return menuItems.filter(item => {
      const categoryMatch = categoryFilter === 'all' || item.category === categoryFilter;
      const availabilityMatch = availabilityFilter === 'all' || 
        (availabilityFilter === 'available' && item.isAvailable) ||
        (availabilityFilter === 'unavailable' && !item.isAvailable);
      return categoryMatch && availabilityMatch;
    });
  };

  const getMenuStats = () => {
    const totalItems = menuItems.length;
    const availableItems = menuItems.filter(item => item.isAvailable).length;
    const unavailableItems = totalItems - availableItems;
    const categories = [...new Set(menuItems.map(item => item.category))].length;
    
    return { totalItems, availableItems, unavailableItems, categories };
  };

  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  const handleMainIngredientChange = (index, field, value) => {
    setFormData(prev => ({
      ...prev,
      mainIngredients: prev.mainIngredients.map((ingredient, i) => 
        i === index ? { ...ingredient, [field]: value } : ingredient
      )
    }));
  };

  const addMainIngredient = () => {
    setFormData(prev => ({
      ...prev,
      mainIngredients: [...prev.mainIngredients, { name: '', quantity: '', unit: '' }]
    }));
  };

  const removeMainIngredient = (index) => {
    if (formData.mainIngredients.length > 1) {
      setFormData(prev => ({
        ...prev,
        mainIngredients: prev.mainIngredients.filter((_, i) => i !== index)
      }));
    }
  };

  const categories = [...new Set(menuItems.map(item => item.category))];

  const menuItemsColumns = [
    {
      key: 'image',
      title: 'Image',
      sortable: false,
      render: (image, item) => {
        const itemName = item?.name || 'Unknown';
        const fallbackChar = itemName.charAt(0).toUpperCase() || 'U';
        return (
          <div className="menu-item-image">
            <img 
              src={image || `https://via.placeholder.com/60x60/8B4513/FFFFFF?text=${encodeURIComponent(fallbackChar)}`}
              alt={itemName}
              onError={(e) => {
                e.target.src = `https://via.placeholder.com/60x60/8B4513/FFFFFF?text=${encodeURIComponent(fallbackChar)}`;
              }}
            />
          </div>
        );
      },
      width: '80px'
    },
    {
      key: 'name',
      title: 'Item Name',
      width: '200px'
    },
    {
      key: 'category',
      title: 'Category',
      width: '120px'
    },
    {
      key: 'price',
      title: 'Price',
      type: 'currency',
      align: 'right',
      width: '100px'
    },
    {
      key: 'preparationTime',
      title: 'Prep Time',
      render: (time) => `${time} min`,
      align: 'center',
      width: '100px'
    },
    {
      key: 'isAvailable',
      title: 'Status',
      render: (isAvailable) => (
        <span className={`availability-badge availability-badge--${isAvailable ? 'available' : 'unavailable'}`}>
          {isAvailable ? 'Available' : 'Unavailable'}
        </span>
      ),
      width: '120px'
    },
    {
      key: 'actions',
      title: 'Actions',
      sortable: false,
      render: (_, item) => (
        <div className="menu-item-actions">
          <Button
            variant="outline"
            size="small"
            onClick={() => openEditModal(item)}
          >
            Edit
          </Button>
          <Button
            variant={item.isAvailable ? 'warning' : 'success'}
            size="small"
            onClick={() => handleToggleAvailability(item._id || item.id, item.isAvailable)}
          >
            {item.isAvailable ? 'Disable' : 'Enable'}
          </Button>
          <Button
            variant="outline"
            size="small"
            onClick={() => handleDeleteItem(item._id || item.id)}
            className="delete-btn"
          >
            Delete
          </Button>
        </div>
      ),
      width: '250px'
    }
  ];

  const stats = getMenuStats();
  const filteredMenuItems = getFilteredMenuItems();

  if (loading) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-loading">
            <LoadingSpinner size="large" />
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-error">
            <h1>Error Loading Menu Items</h1>
            <p>{error}</p>
            <Button onClick={loadMenuItems} variant="primary">
              Try Again
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="menu-items-management-page">
      <div className="container">
        <div className="menu-items-header">
          <div className="menu-items-title-section">
            <h1>Menu Items Management</h1>
            <p>Add, edit, delete, and manage availability of menu items</p>
          </div>
          
          <Button
            variant="primary"
            onClick={() => setShowAddModal(true)}
          >
             Add New Item
          </Button>
        </div>

        <div className="menu-items-stats">
          <StatCard
            title="Total Items"
            value={stats.totalItems}
            icon=""
            color="primary"
          />
          
          <StatCard
            title="Available Items"
            value={stats.availableItems}
            icon=""
            color="success"
            onClick={() => setAvailabilityFilter('available')}
          />
          
          <StatCard
            title="Unavailable Items"
            value={stats.unavailableItems}
            icon=""
            color="error"
            onClick={() => setAvailabilityFilter('unavailable')}
          />
          
          <StatCard
            title="Categories"
            value={stats.categories}
            icon=""
            color="info"
          />
        </div>

        <div className="menu-items-filters">
          <div className="filter-group">
            <label>Category:</label>
            <select
              value={categoryFilter}
              onChange={(e) => setCategoryFilter(e.target.value)}
              className="filter-select"
            >
              <option value="all">All Categories</option>
              {categories.map(category => (
                <option key={category} value={category}>{category}</option>
              ))}
            </select>
          </div>
          
          <div className="filter-group">
            <label>Availability:</label>
            <select
              value={availabilityFilter}
              onChange={(e) => setAvailabilityFilter(e.target.value)}
              className="filter-select"
            >
              <option value="all">All Items</option>
              <option value="available">Available</option>
              <option value="unavailable">Unavailable</option>
            </select>
          </div>
          
          <Button
            variant="outline"
            onClick={loadMenuItems}
            disabled={loading}
          >
             Refresh
          </Button>
        </div>

        <DataTable
          data={filteredMenuItems}
          columns={menuItemsColumns}
          title={`Menu Items (${filteredMenuItems.length})`}
          searchable={true}
          sortable={true}
          pagination={true}
          pageSize={15}
          emptyMessage="No menu items found"
        />

        {/* Add Item Modal */}
        <Modal
          isOpen={showAddModal}
          onClose={() => {
            setShowAddModal(false);
            resetForm();
          }}
          title="Add New Menu Item"
          size="large"
        >
          <form onSubmit={handleAddItem} className="menu-item-form">
            <div className="form-grid">
              <Input
                label="Item Name"
                name="name"
                value={formData.name}
                onChange={handleFormChange}
                required
                className="full-width"
              />
              
              <Input
                label="Description"
                name="description"
                type="textarea"
                value={formData.description}
                onChange={handleFormChange}
                required
                className="full-width"
              />
              
              <Input
                label="Price ()"
                name="price"
                type="number"
                step="0.01"
                value={formData.price}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Category"
                name="category"
                value={formData.category}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Preparation Time (minutes)"
                name="preparationTime"
                type="number"
                value={formData.preparationTime}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Image URL (optional)"
                name="image"
                value={formData.image}
                onChange={handleFormChange}
              />
              
              <Input
                label="Ingredients (comma separated)"
                name="ingredients"
                value={formData.ingredients}
                onChange={handleFormChange}
                placeholder="e.g., Espresso beans, Milk, Sugar"
                className="full-width"
              />

              {/* Main Ingredients Section */}
              <div className="main-ingredients-section">
                <label className="form-label">
                  Main Ingredients (for inventory tracking)
                  <span className="help-text">These ingredients will be automatically deducted from inventory when orders are delivered</span>
                </label>
                {formData.mainIngredients.map((ingredient, index) => (
                  <div key={index} className="main-ingredient-row">
                    <Input
                      label="Ingredient Name"
                      value={ingredient.name}
                      onChange={(e) => handleMainIngredientChange(index, 'name', e.target.value)}
                      placeholder="e.g., Burger Patty"
                      className="ingredient-name"
                    />
                    <Input
                      label="Quantity"
                      type="number"
                      step="0.01"
                      value={ingredient.quantity}
                      onChange={(e) => handleMainIngredientChange(index, 'quantity', e.target.value)}
                      placeholder="1"
                      className="ingredient-quantity"
                    />
                    <Input
                      label="Unit"
                      value={ingredient.unit}
                      onChange={(e) => handleMainIngredientChange(index, 'unit', e.target.value)}
                      placeholder="pieces, kg, liters"
                      className="ingredient-unit"
                    />
                    {formData.mainIngredients.length > 1 && (
                      <Button
                        type="button"
                        variant="danger"
                        size="small"
                        onClick={() => removeMainIngredient(index)}
                        className="remove-ingredient-btn"
                      >
                        Remove
                      </Button>
                    )}
                  </div>
                ))}
                <Button
                  type="button"
                  variant="secondary"
                  size="small"
                  onClick={addMainIngredient}
                  className="add-ingredient-btn"
                >
                  + Add Main Ingredient
                </Button>
              </div>
              
              <Input
                label="Allergens (comma separated)"
                name="allergens"
                value={formData.allergens}
                onChange={handleFormChange}
                placeholder="e.g., Dairy, Gluten, Nuts"
                className="full-width"
              />
              
              <Input
                label="Calories"
                name="calories"
                type="number"
                value={formData.calories}
                onChange={handleFormChange}
              />
              
              <Input
                label="Caffeine Content"
                name="caffeine"
                value={formData.caffeine}
                onChange={handleFormChange}
                placeholder="e.g., 63mg"
              />
              
              <div className="checkbox-group full-width">
                <label className="checkbox-label">
                  <input
                    type="checkbox"
                    name="isAvailable"
                    checked={formData.isAvailable}
                    onChange={handleFormChange}
                  />
                  <span>Available for ordering</span>
                </label>
              </div>
            </div>
            
            <div className="form-actions">
              <Button
                type="button"
                variant="outline"
                onClick={() => {
                  setShowAddModal(false);
                  resetForm();
                }}
              >
                Cancel
              </Button>
              <Button
                type="submit"
                variant="primary"
                disabled={submitting}
              >
                {submitting ? 'Adding...' : 'Add Item'}
              </Button>
            </div>
          </form>
        </Modal>

        {/* Edit Item Modal */}
        <Modal
          isOpen={showEditModal}
          onClose={() => {
            setShowEditModal(false);
            setSelectedItem(null);
            resetForm();
          }}
          title={`Edit ${selectedItem?.name}`}
          size="large"
        >
          <form onSubmit={handleEditItem} className="menu-item-form">
            <div className="form-grid">
              <Input
                label="Item Name"
                name="name"
                value={formData.name}
                onChange={handleFormChange}
                required
                className="full-width"
              />
              
              <Input
                label="Description"
                name="description"
                type="textarea"
                value={formData.description}
                onChange={handleFormChange}
                required
                className="full-width"
              />
              
              <Input
                label="Price ()"
                name="price"
                type="number"
                step="0.01"
                value={formData.price}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Category"
                name="category"
                value={formData.category}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Preparation Time (minutes)"
                name="preparationTime"
                type="number"
                value={formData.preparationTime}
                onChange={handleFormChange}
                required
              />
              
              <Input
                label="Image URL (optional)"
                name="image"
                value={formData.image}
                onChange={handleFormChange}
              />
              
              <Input
                label="Ingredients (comma separated)"
                name="ingredients"
                value={formData.ingredients}
                onChange={handleFormChange}
                className="full-width"
              />

              {/* Main Ingredients Section */}
              <div className="main-ingredients-section">
                <label className="form-label">
                  Main Ingredients (for inventory tracking)
                  <span className="help-text">These ingredients will be automatically deducted from inventory when orders are delivered</span>
                </label>
                {formData.mainIngredients.map((ingredient, index) => (
                  <div key={index} className="main-ingredient-row">
                    <Input
                      label="Ingredient Name"
                      value={ingredient.name}
                      onChange={(e) => handleMainIngredientChange(index, 'name', e.target.value)}
                      placeholder="e.g., Burger Patty"
                      className="ingredient-name"
                    />
                    <Input
                      label="Quantity"
                      type="number"
                      step="0.01"
                      value={ingredient.quantity}
                      onChange={(e) => handleMainIngredientChange(index, 'quantity', e.target.value)}
                      placeholder="1"
                      className="ingredient-quantity"
                    />
                    <Input
                      label="Unit"
                      value={ingredient.unit}
                      onChange={(e) => handleMainIngredientChange(index, 'unit', e.target.value)}
                      placeholder="pieces, kg, liters"
                      className="ingredient-unit"
                    />
                    {formData.mainIngredients.length > 1 && (
                      <Button
                        type="button"
                        variant="danger"
                        size="small"
                        onClick={() => removeMainIngredient(index)}
                        className="remove-ingredient-btn"
                      >
                        Remove
                      </Button>
                    )}
                  </div>
                ))}
                <Button
                  type="button"
                  variant="secondary"
                  size="small"
                  onClick={addMainIngredient}
                  className="add-ingredient-btn"
                >
                  + Add Main Ingredient
                </Button>
              </div>
              
              <Input
                label="Allergens (comma separated)"
                name="allergens"
                value={formData.allergens}
                onChange={handleFormChange}
                className="full-width"
              />
              
              <Input
                label="Calories"
                name="calories"
                type="number"
                value={formData.calories}
                onChange={handleFormChange}
              />
              
              <Input
                label="Caffeine Content"
                name="caffeine"
                value={formData.caffeine}
                onChange={handleFormChange}
              />
              
              <div className="checkbox-group full-width">
                <label className="checkbox-label">
                  <input
                    type="checkbox"
                    name="isAvailable"
                    checked={formData.isAvailable}
                    onChange={handleFormChange}
                  />
                  <span>Available for ordering</span>
                </label>
              </div>
            </div>
            
            <div className="form-actions">
              <Button
                type="button"
                variant="outline"
                onClick={() => {
                  setShowEditModal(false);
                  setSelectedItem(null);
                  resetForm();
                }}
              >
                Cancel
              </Button>
              <Button
                type="submit"
                variant="primary"
                disabled={submitting}
              >
                {submitting ? 'Updating...' : 'Update Item'}
              </Button>
            </div>
          </form>
        </Modal>
      </div>
    </div>
  );
}

export default MenuItemsManagementPage;