import React, { useState, useEffect } from 'react';
import adminService from '../../services/adminService';
import DataTable from '../../components/admin/DataTable/DataTable';
import StatCard from '../../components/admin/StatCard/StatCard';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import Button from '../../components/common/Button/Button';
import Modal from '../../components/common/Modal/Modal';
import Input from '../../components/common/Input/Input';
import './EmployeeManagementPage.css';

function EmployeeManagementPage() {
  const [employees, setEmployees] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [showAddModal, setShowAddModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [selectedEmployee, setSelectedEmployee] = useState(null);
  const [departmentFilter, setDepartmentFilter] = useState('all');
  const [statusFilter, setStatusFilter] = useState('all');
  const [submitting, setSubmitting] = useState(false);

  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    role: '',
    department: '',
    phone: '',
    address: '',
    salary: '',
    status: 'Active',
    permissions: []
  });

  const roles = ['employee', 'delivery'];
  const roleDisplayNames = {
    'employee': 'Employee',
    'delivery': 'Delivery Driver'
  };
  const departments = ['Kitchen', 'Delivery', 'Management', 'Front of House'];
  const availablePermissions = [
    'view_orders',
    'update_order_status',
    'view_assigned_orders',
    'update_delivery_status',
    'view_all',
    'manage_inventory',
    'manage_employees',
    'manage_menu',
    'view_analytics'
  ];

  useEffect(() => {
    loadEmployees();
  }, []);

  const loadEmployees = async () => {
    try {
      setLoading(true);
      setError('');
      const result = await adminService.getEmployees();
      if (result.success) {
        setEmployees(result.data.employees || result.data);
      }
    } catch (err) {
      setError('Failed to load employees. Please try again.');
      console.error('Employees loading error:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleFormChange = (e) => {
    const { name, value, type, checked } = e.target;

    if (name === 'permissions') {
      const updatedPermissions = checked
        ? [...formData.permissions, value]
        : formData.permissions.filter(p => p !== value);

      setFormData({
        ...formData,
        permissions: updatedPermissions
      });
    } else {
      setFormData({
        ...formData,
        [name]: type === 'checkbox' ? checked : value
      });
    }
  };


  const resetForm = () => {
    setFormData({
      name: '',
      email: '',
      password: '',
      role: '',
      department: '',
      phone: '',
      address: '',
      salary: '',
      status: 'Active',
      permissions: []
    });
  };

  const handleAddEmployee = async (e) => {
    e.preventDefault();
    try {
      setSubmitting(true);
      setError('');
      const employeeData = {
        name: formData.name,
        email: formData.email,
        password: formData.password || 'employee123', // Default password
        role: formData.role,
        department: formData.department,
        phone: formData.phone || '',
        address: formData.address || '',
        isActive: formData.status === 'Active'
      };

      const result = await adminService.addEmployee(employeeData);

      if (result.success) {
        setEmployees([...employees, result.data.employee || result.data]);
        setShowAddModal(false);
        resetForm();
        alert('Employee added successfully!');
      }
    } catch (err) {
      console.error('Add employee error:', err);
      setError('Failed to add employee. Please try again.');
    } finally {
      setSubmitting(false);
    }
  };

  const handleEditEmployee = async (e) => {
    e.preventDefault();
    try {
      setSubmitting(true);
      setError('');
      const employeeData = {
        name: formData.name,
        email: formData.email,
        role: formData.role,
        department: formData.department,
        phone: formData.phone || '',
        address: formData.address || '',
        isActive: formData.status === 'Active'
      };

      const result = await adminService.updateEmployee(selectedEmployee._id || selectedEmployee.id, employeeData);

      if (result.success) {
        setEmployees(employees.map(emp =>
          (emp._id || emp.id) === (selectedEmployee._id || selectedEmployee.id) ? result.data.employee || result.data : emp
        ));
        setShowEditModal(false);
        setSelectedEmployee(null);
        resetForm();
        alert('Employee updated successfully!');
      }
    } catch (err) {
      console.error('Edit employee error:', err);
      setError('Failed to update employee. Please try again.');
    } finally {
      setSubmitting(false);
    }
  };

  const handleDeleteEmployee = async (employeeId) => {
    if (window.confirm('Are you sure you want to deactivate this employee?')) {
      try {
        setError('');
        const result = await adminService.deleteEmployee(employeeId);
        if (result.success) {
          setEmployees(employees.map(emp =>
            (emp._id || emp.id) === employeeId ? { ...emp, isActive: false } : emp
          ));
          alert('Employee deactivated successfully!');
        }
      } catch (err) {
        console.error('Delete employee error:', err);
        setError('Failed to deactivate employee. Please try again.');
      }
    }
  };

  const openEditModal = (employee) => {
    setSelectedEmployee(employee);
    setFormData({
      name: employee.name || '',
      email: employee.email || '',
      role: employee.role || '',
      department: employee.department || '',
      phone: employee.phone || '',
      address: typeof employee.address === 'string' ? employee.address : '',
      salary: employee.salary ? employee.salary.toString() : '',
      status: employee.isActive ? 'Active' : 'Inactive',
      permissions: employee.permissions || []
    });
    setShowEditModal(true);
  };

  const getFilteredEmployees = () => {
    const filtered = employees.filter(employee => {
      const departmentMatch = departmentFilter === 'all' || employee.department === departmentFilter;
      const statusMatch = statusFilter === 'all' ||
        (statusFilter === 'Active' && employee.isActive) ||
        (statusFilter === 'Inactive' && !employee.isActive);
      return departmentMatch && statusMatch;
    });
    console.log('Filtered Employees:', filtered);
    return filtered;
  };

  const getEmployeeStats = () => {
    const totalEmployees = employees.length;
    const activeEmployees = employees.filter(emp => emp.isActive).length;
    const inactiveEmployees = totalEmployees - activeEmployees;
    const roles = [...new Set(employees.map(emp => emp.role))].length;

    return { totalEmployees, activeEmployees, inactiveEmployees, departments: roles };
  };

  const employeesColumns = [
    {
      key: 'name',
      title: 'Name',
      width: '200px'
    },
    {
      key: 'email',
      title: 'Email',
      width: '200px'
    },
    {
      key: 'role',
      title: 'Role',
      width: '150px',
      render: (value) => roleDisplayNames[value] || value
    },
    {
      key: 'department',
      title: 'Department',
      width: '150px'
    },
    {
      key: 'phone',
      title: 'Phone',
      width: '150px'
    },
    {
      key: 'createdAt',
      title: 'Created Date',
      type: 'date',
      width: '120px'
    },
    {
      key: 'isActive',
      title: 'Status',
      type: 'status',
      width: '100px',
      render: (value) => value ? 'Active' : 'Inactive'
    },
    {
      key: 'actions',
      title: 'Actions',
      sortable: false,
      render: (_, employee) => (
        <div className="employee-actions">
          <Button
            variant="outline"
            size="small"
            onClick={() => openEditModal(employee)}
          >
            Edit
          </Button>
          <Button
            variant="outline"
            size="small"
            onClick={() => handleDeleteEmployee(employee._id || employee.id)}
            className="delete-btn"
          >
            Delete
          </Button>
        </div>
      ),
      width: '150px'
    }
  ];

  const stats = getEmployeeStats();
  const filteredEmployees = getFilteredEmployees();

  if (loading) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-loading">
            <LoadingSpinner size="large" />
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-error">
            <h1>Error Loading Employees</h1>
            <p>{error}</p>
            <Button onClick={loadEmployees} variant="primary">
              Try Again
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="employee-management-page">
      <div className="container">
        <div className="employee-header">
          <div className="employee-title-section">
            <h1>Employee Management</h1>
            <p>Add new employees and assign roles/permissions</p>
          </div>

          <Button
            variant="primary"
            onClick={() => setShowAddModal(true)}
          >
             Add New Employee
          </Button>
        </div>

        <div className="employee-stats">
          <StatCard
            title="Total Employees"
            value={stats.totalEmployees}
            icon=""
            color="primary"
          />

          <StatCard
            title="Active Employees"
            value={stats.activeEmployees}
            icon=""
            color="success"
            onClick={() => setStatusFilter('Active')}
          />

          <StatCard
            title="Inactive Employees"
            value={stats.inactiveEmployees}
            icon=""
            color="error"
            onClick={() => setStatusFilter('Inactive')}
          />

          <StatCard
            title="Departments"
            value={stats.departments}
            icon=""
            color="info"
          />
        </div>

        <div className="employee-filters">
          <div className="filter-group">
            <label>Department:</label>
            <select
              value={departmentFilter}
              onChange={(e) => setDepartmentFilter(e.target.value)}
              className="filter-select"
            >
              <option value="all">All Departments</option>
              {departments.map(dept => (
                <option key={dept} value={dept}>{dept}</option>
              ))}
            </select>
          </div>

          <div className="filter-group">
            <label>Status:</label>
            <select
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              className="filter-select"
            >
              <option value="all">All Statuses</option>
              <option value="Active">Active</option>
              <option value="Inactive">Inactive</option>
            </select>
          </div>

          <Button
            variant="outline"
            onClick={loadEmployees}
            disabled={loading}
          >
             Refresh
          </Button>
        </div>

        <DataTable
          data={getFilteredEmployees()}
          columns={employeesColumns}
          title={`Employees (${getFilteredEmployees().length})`}
          searchable={true}
          sortable={true}
          pagination={true}
          pageSize={15}
          emptyMessage="No employees found"
        />

        {/* Add Employee Modal */}
        <Modal
          isOpen={showAddModal}
          onClose={() => {
            setShowAddModal(false);
            resetForm();
          }}
          title="Add New Employee"
          size="large"
        >
          <form onSubmit={handleAddEmployee} className="employee-form">
            <div className="form-grid">
              <Input
                label="Full Name"
                name="name"
                value={formData.name}
                onChange={handleFormChange}
                required
              />

              <Input
                label="Email"
                name="email"
                type="email"
                value={formData.email}
                onChange={handleFormChange}
                required
              />

              <div className="input-group">
                <label>Role</label>
                <select
                  name="role"
                  value={formData.role}
                  onChange={handleFormChange}
                  required
                  className="form-select"
                >
                  <option value="">Select Role</option>
                  {roles.map(role => (
                    <option key={role} value={role}>{roleDisplayNames[role]}</option>
                  ))}
                </select>
              </div>

              <div className="input-group">
                <label>Department</label>
                <select
                  name="department"
                  value={formData.department}
                  onChange={handleFormChange}
                  required
                  className="form-select"
                >
                  <option value="">Select Department</option>
                  {departments.map(dept => (
                    <option key={dept} value={dept}>{dept}</option>
                  ))}
                </select>
              </div>

              <Input
                label="Phone"
                name="phone"
                value={formData.phone}
                onChange={handleFormChange}
              />

              <Input
                label="Salary ($)"
                name="salary"
                type="number"
                value={formData.salary}
                onChange={handleFormChange}
              />

              <Input
                label="Address"
                name="address"
                type="textarea"
                value={formData.address}
                onChange={handleFormChange}
                className="full-width"
              />

              <div className="permissions-section full-width">
                <label>Permissions</label>
                <div className="permissions-grid">
                  {availablePermissions.map(permission => (
                    <label key={permission} className="permission-checkbox">
                      <input
                        type="checkbox"
                        name="permissions"
                        value={permission}
                        checked={formData.permissions.includes(permission)}
                        onChange={handleFormChange}
                      />
                      <span>{permission.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase())}</span>
                    </label>
                  ))}
                </div>
              </div>
            </div>

            <div className="form-actions">
              <Button
                type="button"
                variant="outline"
                onClick={() => {
                  setShowAddModal(false);
                  resetForm();
                }}
              >
                Cancel
              </Button>
              <Button
                type="submit"
                variant="primary"
                disabled={submitting}
              >
                {submitting ? 'Adding...' : 'Add Employee'}
              </Button>
            </div>
          </form>
        </Modal>

        {/* Edit Employee Modal */}
        <Modal
          isOpen={showEditModal}
          onClose={() => {
            setShowEditModal(false);
            setSelectedEmployee(null);
            resetForm();
          }}
          title={`Edit ${selectedEmployee?.name}`}
          size="large"
        >
          <form onSubmit={handleEditEmployee} className="employee-form">
            <div className="form-grid">
              <Input
                label="Full Name"
                name="name"
                value={formData.name}
                onChange={handleFormChange}
                required
              />

              <Input
                label="Email"
                name="email"
                type="email"
                value={formData.email}
                onChange={handleFormChange}
                required
              />

              <div className="input-group">
                <label>Role</label>
                <select
                  name="role"
                  value={formData.role}
                  onChange={handleFormChange}
                  required
                  className="form-select"
                >
                  <option value="">Select Role</option>
                  {roles.map(role => (
                    <option key={role} value={role}>{roleDisplayNames[role]}</option>
                  ))}
                </select>
              </div>

              <div className="input-group">
                <label>Department</label>
                <select
                  name="department"
                  value={formData.department}
                  onChange={handleFormChange}
                  required
                  className="form-select"
                >
                  <option value="">Select Department</option>
                  {departments.map(dept => (
                    <option key={dept} value={dept}>{dept}</option>
                  ))}
                </select>
              </div>

              <Input
                label="Phone"
                name="phone"
                value={formData.phone}
                onChange={handleFormChange}
              />

              <Input
                label="Salary ($)"
                name="salary"
                type="number"
                value={formData.salary}
                onChange={handleFormChange}
              />

              <div className="input-group">
                <label>Status</label>
                <select
                  name="status"
                  value={formData.status}
                  onChange={handleFormChange}
                  required
                  className="form-select"
                >
                  <option value="Active">Active</option>
                  <option value="Inactive">Inactive</option>
                </select>
              </div>

              <Input
                label="Address"
                name="address"
                type="textarea"
                value={formData.address}
                onChange={handleFormChange}
                className="full-width"
              />

              <div className="permissions-section full-width">
                <label>Permissions</label>
                <div className="permissions-grid">
                  {availablePermissions.map(permission => (
                    <label key={permission} className="permission-checkbox">
                      <input
                        type="checkbox"
                        name="permissions"
                        value={permission}
                        checked={formData.permissions.includes(permission)}
                        onChange={handleFormChange}
                      />
                      <span>{permission.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase())}</span>
                    </label>
                  ))}
                </div>
              </div>
            </div>

            <div className="form-actions">
              <Button
                type="button"
                variant="outline"
                onClick={() => {
                  setShowEditModal(false);
                  setSelectedEmployee(null);
                  resetForm();
                }}
              >
                Cancel
              </Button>
              <Button
                type="submit"
                variant="primary"
                disabled={submitting}
              >
                {submitting ? 'Updating...' : 'Update Employee'}
              </Button>
            </div>
          </form>
        </Modal>
      </div>
    </div>
  );
}

export default EmployeeManagementPage;