import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import StatCard from '../../components/admin/StatCard/StatCard';
import DataTable from '../../components/admin/DataTable/DataTable';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import Button from '../../components/common/Button/Button';
import { useAuth } from '../../contexts/AuthContext';
import './EmployeeDashboardPage.css';

function EmployeeDashboardPage() {
  const { user } = useAuth();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [dashboardData, setDashboardData] = useState({
    stats: {},
    recentOrders: [],
    lowStockItems: []
  });

  useEffect(() => {
    // Simulate API call to fetch dashboard data
    const fetchDashboardData = async () => {
      setLoading(true);
      try {
        // Mock data - in real app, this would come from API
        const mockData = {
          stats: {
            todayOrders: 45,
            pendingOrders: 8,
            completedOrders: 37,
            totalRevenue: 1250.75,
            averageOrderValue: 27.79,
            popularItem: 'Cappuccino'
          },
          recentOrders: [
            {
              id: 'ORD-001',
              customerName: 'John Doe',
              items: 'Cappuccino, Croissant',
              total: 12.50,
              status: 'Cooking in Progress',
              orderTime: new Date(Date.now() - 15 * 60000).toISOString(),
              type: 'Online'
            },
            {
              id: 'ORD-002',
              customerName: 'Jane Smith',
              items: 'Latte, Sandwich',
              total: 18.75,
              status: 'Order Placed',
              orderTime: new Date(Date.now() - 25 * 60000).toISOString(),
              type: 'In-Cafe'
            },
            {
              id: 'ORD-003',
              customerName: 'Mike Johnson',
              items: 'Espresso, Pastry',
              total: 9.25,
              status: 'On the Way to Delivery',
              orderTime: new Date(Date.now() - 45 * 60000).toISOString(),
              type: 'Online'
            }
          ],
          lowStockItems: [
            {
              id: 'ING-001',
              name: 'Coffee Beans (Arabica)',
              currentStock: 2,
              minStock: 5,
              unit: 'kg',
              status: 'Low'
            },
            {
              id: 'ING-002',
              name: 'Milk',
              currentStock: 3,
              minStock: 10,
              unit: 'liters',
              status: 'Low'
            }
          ]
        };

        setTimeout(() => {
          setDashboardData(mockData);
          setLoading(false);
        }, 1000);
      } catch (error) {
        console.error('Error fetching dashboard data:', error);
        setLoading(false);
      }
    };

    fetchDashboardData();
  }, []);

  const orderColumns = [
    {
      key: 'id',
      title: 'Order ID',
      width: '120px'
    },
    {
      key: 'customerName',
      title: 'Customer',
      width: '150px'
    },
    {
      key: 'items',
      title: 'Items',
      width: '200px'
    },
    {
      key: 'total',
      title: 'Total',
      type: 'currency',
      width: '100px',
      align: 'right'
    },
    {
      key: 'status',
      title: 'Status',
      type: 'status',
      width: '150px'
    },
    {
      key: 'type',
      title: 'Type',
      width: '100px',
      render: (value) => (
        <span className={`order-type order-type--${value.toLowerCase().replace('-', '')}`}>
          {value}
        </span>
      )
    },
    {
      key: 'orderTime',
      title: 'Time',
      type: 'datetime',
      width: '150px'
    }
  ];

  const stockColumns = [
    {
      key: 'name',
      title: 'Ingredient',
      width: '200px'
    },
    {
      key: 'currentStock',
      title: 'Current Stock',
      width: '120px',
      align: 'center',
      render: (value, row) => `${value} ${row.unit}`
    },
    {
      key: 'minStock',
      title: 'Min Required',
      width: '120px',
      align: 'center',
      render: (value, row) => `${value} ${row.unit}`
    },
    {
      key: 'status',
      title: 'Status',
      type: 'status',
      width: '100px'
    }
  ];

  if (loading) {
    return (
      <div className="employee-dashboard">
        <LoadingSpinner />
      </div>
    );
  }

  return (
    <div className="employee-dashboard">
      <div className="dashboard-header">
        <div className="dashboard-title-section">
          <h1>Employee Dashboard</h1>
          <p className="dashboard-subtitle">
            Welcome back, {user?.username}! Here's your overview of today's operations.
          </p>
        </div>
        <div className="dashboard-actions">
          <Button
            variant="primary"
            onClick={() => navigate('/employee/take-order')}
            className="take-order-btn"
          >
             Take New Order
          </Button>
          <Button
            variant="outline"
            onClick={() => navigate('/employee/orders')}
          >
             View All Orders
          </Button>
        </div>
      </div>

      {/* Statistics Cards */}
      <div className="dashboard-stats">
        <StatCard
          title="Today's Orders"
          value={dashboardData.stats.todayOrders}
          icon=""
          color="primary"
          trend="up"
          trendValue="+12%"
        />
        <StatCard
          title="Pending Orders"
          value={dashboardData.stats.pendingOrders}
          icon=""
          color="warning"
        />
        <StatCard
          title="Completed Orders"
          value={dashboardData.stats.completedOrders}
          icon=""
          color="success"
          trend="up"
          trendValue="+8%"
        />
        <StatCard
          title="Today's Revenue"
          value={`${dashboardData.stats.totalRevenue.toFixed(2)}`}
          icon=""
          color="info"
          trend="up"
          trendValue="+15%"
        />
      </div>

      {/* Recent Orders */}
      <div className="dashboard-section">
        <DataTable
          title="Recent Orders"
          data={dashboardData.recentOrders}
          columns={orderColumns}
          pageSize={5}
          searchable={false}
          emptyMessage="No recent orders found"
        />
      </div>

      {/* Low Stock Alert */}
      {dashboardData.lowStockItems.length > 0 && (
        <div className="dashboard-section">
          <div className="alert-section">
            <div className="alert-header">
              <h3> Low Stock Alert</h3>
              <p>The following items are running low and need attention:</p>
            </div>
            <DataTable
              data={dashboardData.lowStockItems}
              columns={stockColumns}
              pagination={false}
              searchable={false}
              emptyMessage="All items are well stocked"
            />
          </div>
        </div>
      )}

      {/* Quick Stats */}
      <div className="dashboard-section">
        <div className="quick-stats">
          <div className="quick-stat-item">
            <span className="stat-label">Average Order Value:</span>
            <span className="stat-value">${dashboardData.stats.averageOrderValue}</span>
          </div>
          <div className="quick-stat-item">
            <span className="stat-label">Most Popular Item:</span>
            <span className="stat-value">{dashboardData.stats.popularItem}</span>
          </div>
        </div>
      </div>
    </div>
  );
}

export default EmployeeDashboardPage;