import React, { useState, useEffect } from 'react';
import inventoryService from '../../../services/inventoryService';
import './InventoryAlerts.css';

function InventoryAlerts() {
  const [lowStockItems, setLowStockItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    loadLowStockAlerts();
  }, []);

  const loadLowStockAlerts = async () => {
    try {
      setLoading(true);
      setError('');
      const result = await inventoryService.getLowStockAlerts();
      
      if (result.success) {
        setLowStockItems(result.data.lowStockItems || []);
      } else {
        setError(result.message || 'Failed to load inventory alerts');
      }
    } catch (err) {
      console.error('Error loading inventory alerts:', err);
      setError('Failed to load inventory alerts');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="inventory-alerts loading">
        <div className="loading-spinner">Loading inventory alerts...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="inventory-alerts error">
        <div className="error-message">
          <span className="error-icon"></span>
          {error}
        </div>
      </div>
    );
  }

  if (lowStockItems.length === 0) {
    return (
      <div className="inventory-alerts success">
        <div className="success-message">
          <span className="success-icon"></span>
          All inventory items are well stocked!
        </div>
      </div>
    );
  }

  return (
    <div className="inventory-alerts">
      <div className="alerts-header">
        <h3>
          <span className="warning-icon"></span>
          Low Stock Alerts ({lowStockItems.length})
        </h3>
        <button 
          className="refresh-btn"
          onClick={loadLowStockAlerts}
          title="Refresh alerts"
        >
          
        </button>
      </div>
      
      <div className="alerts-list">
        {lowStockItems.map((item, index) => (
          <div 
            key={index} 
            className={`alert-item ${item.stockStatus}`}
          >
            <div className="item-info">
              <span className="item-name">{item.name}</span>
              <span className="item-category">({item.category})</span>
            </div>
            <div className="stock-info">
              <span className="current-stock">
                {item.currentStock} {item.unit}
              </span>
              <span className="min-stock">
                Min: {item.minStock} {item.unit}
              </span>
            </div>
            <div className={`status-badge ${item.stockStatus}`}>
              {item.stockStatus === 'critical' ? 'Critical' : 'Low'}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default InventoryAlerts;