import { jsPDF } from 'jspdf';
import autoTable from 'jspdf-autotable';

// Utility: Draw emoji as canvas image and return Data URL
const drawEmojiOnly = async (emoji, fontSize = 32) => {
  const canvas = document.createElement('canvas');
  const ctx = canvas.getContext('2d');

  ctx.font = `${fontSize}px "Segoe UI Emoji", "Noto Color Emoji", sans-serif`;
  const { width } = ctx.measureText(emoji);
  canvas.width = width;
  canvas.height = fontSize * 1.2;

  ctx.font = `${fontSize}px "Segoe UI Emoji", "Noto Color Emoji", sans-serif`;
  ctx.textBaseline = 'top';
  ctx.fillStyle = '#000';
  ctx.fillText(emoji, 0, 0);

  return canvas.toDataURL('image/png');
};

const formatPrice = (price) => `Rs.${Number(price).toFixed(2)}`;
const currencyFormat = (amount) => `Rs. ${Number(amount).toFixed(2)}`;

export const generateInvoice = async (order) => {
  const {
    id,
    customerName,
    customerPhone,
    items,
    orderType,
    tableNumber,
    paymentMethod,
    total,
    timestamp,
    deliveryAddress,
    paymentStatus,
    status,
    type,
    subtotal,
    deliveryFee,
    tax
  } = order;

  const doc = new jsPDF({ unit: 'pt' });
  let currentY = 60;

  doc.setFont('helvetica', 'bold');
  doc.setFontSize(22);
  doc.text('The Sanctum Cafe', 300, currentY, { align: 'center' });
  currentY += 50;

  const drawLabelWithEmoji = async (emoji, label) => {
    const imgData = await drawEmojiOnly(emoji);
    const imgSize = 16;
    doc.addImage(imgData, 'PNG', 40, currentY, imgSize, imgSize);
    doc.setFont('helvetica', 'normal');
    doc.setFontSize(12);
    doc.text(label, 40 + imgSize + 6, currentY + 12);
    currentY += 24;
  };

  const formattedDate = new Date(timestamp).toLocaleString();
  const deliveryType = type === 'online' ? 'Delivery' : 'Pickup';

  await drawLabelWithEmoji('', `Invoice #${id}`);
  await drawLabelWithEmoji('', `Date: ${formattedDate}`);
  await drawLabelWithEmoji('', `Customer: ${customerName}`);
  if (customerPhone) await drawLabelWithEmoji('', `Phone: ${customerPhone}`);
  await drawLabelWithEmoji('', `Order Type: ${deliveryType}`);
  await drawLabelWithEmoji('', `Status: ${status}`);
  if (orderType === 'dine in' && tableNumber)
    await drawLabelWithEmoji('', `Table No: ${tableNumber}`);
  await drawLabelWithEmoji('', `Payment: ${paymentMethod}`);

  currentY += 30;

  const tableBody = items.map(item => [
    item.name.length > 25 ? item.name.slice(0, 25) + '...' : item.name,
    item.quantity.toString(),
    formatPrice(item.price),
    formatPrice(item.price * item.quantity)
  ]);

  autoTable(doc, {
    startY: currentY,
    head: [['Item', 'Qty', 'Price', 'Total']],
    body: tableBody,
    theme: 'grid',
    styles: {
      font: 'helvetica',
      fontSize: 10,
      cellPadding: 6
    },
    headStyles: {
      fillColor: [41, 128, 185],
      textColor: [255, 255, 255],
      fontStyle: 'bold'
    },
    columnStyles: {
      0: { cellWidth: 200 },
      1: { halign: 'center' },
      2: { halign: 'right' },
      3: { halign: 'right' }
    },
    didDrawPage: async (data) => {
      let y = data.cursor.y + 20;
    //   doc.setFont('helvetica', 'bold');
    //   doc.setFontSize(12);
    //   doc.text(`Subtotal: ${formatPrice(subtotal || total)}`, 400, y);
    //   if (deliveryFee && deliveryFee > 0) {
    //     y += 14;
    //     doc.text(`Delivery Fee: ${formatPrice(deliveryFee)}`, 400, y);
    //   }
    //   if (tax && tax > 0) {
    //     y += 14;
    //     doc.text(`Tax: ${formatPrice(tax)}`, 400, y);
    //   }
      y += 20;
      doc.setFont('helvetica', 'bold');
      doc.text(`TOTAL: ${formatPrice(total)}`, 400, y);

      y += 25;
      doc.setFont('helvetica', 'normal');
      doc.setFontSize(10);
      doc.text(`Payment Method: ${paymentMethod || 'Cash on Delivery'}`, 40, y);
      doc.text(`Payment Status: ${paymentStatus || 'Pending'}`, 40, y + 14);

      if (deliveryAddress) {
        y += 30;
        doc.setFontSize(11);
        doc.text('Delivery Address:', 40, y);
        y += 14;
        const addressLines = doc.splitTextToSize(deliveryAddress, 500);
        addressLines.forEach((line) => {
          doc.text(line, 40, y);
          y += 12;
        });
      }

      if (tableNumber) {
        y += 18;
        doc.setFontSize(11);
        doc.text('Pickup Table:', 40, y);
        y += 12;
        doc.setFontSize(10);
        doc.text(`Table #${tableNumber}`, 40, y);
      }

      y += 30;
      const emoji = await drawEmojiOnly('');
      doc.addImage(emoji, 'PNG', 240, y, 16, 16);
      doc.setFont('helvetica', 'italic');
      doc.setFontSize(10);
      doc.text('Thank you for choosing The Sanctum Cafe!', 260, y + 12);
      doc.text('Visit us again soon!', 260, y + 24);
    }
  });

  return doc;
};

export const downloadInvoice = async (order) => {
  const doc = await generateInvoice(order);
  const fileName = `invoice-${order.id}-${new Date().toISOString().split('T')[0]}.pdf`;
  doc.save(fileName);
};
