import React, { useState, useEffect } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import orderService from '../../services/orderService';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import DeliveryNavigation from '../../components/delivery/DeliveryNavigation/DeliveryNavigation';
import './DeliveryDashboard.css';

function DeliveryDashboard() {
  const { user } = useAuth();
  const [stats, setStats] = useState(null);
  const [recentOrders, setRecentOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchDashboardData = async () => {
      try {
        setError(null);
        
        // Fetch delivery person stats
        const statsData = await orderService.getDeliveryPersonStats(user.id);
        setStats(statsData);

        // Fetch recent assigned orders
        const ordersData = await orderService.getAssignedOrders(user.id);
        setRecentOrders(ordersData.slice(0, 5)); // Show only recent 5 orders
        
      } catch (err) {
        // For development, we'll still show sample data even if there's an error
        console.warn('API not available, using sample data:', err);
        
        // Set sample stats
        setStats({
          totalDeliveries: 127,
          todayDeliveries: 8,
          pendingOrders: 2,
          todayEarnings: 45.75,
          avgDeliveryTime: 22,
          customerRating: 4.8,
          onTimeRate: 94
        });

        // Set sample recent orders
        setRecentOrders([
          {
            _id: '507f1f77bcf86cd799439011',
            customerName: 'John Smith',
            deliveryAddress: '123 Main Street, Downtown',
            totalAmount: 24.99,
            status: 'Cooking in Progress'
          },
          {
            _id: '507f1f77bcf86cd799439012',
            customerName: 'Sarah Johnson',
            deliveryAddress: '456 Oak Avenue, Apartment 3B',
            totalAmount: 18.75,
            status: 'On the Way to Delivery'
          }
        ]);
      } finally {
        setLoading(false);
      }
    };

    if (user && user.role === 'delivery') {
      fetchDashboardData();
    }
  }, [user]);

  if (loading) {
    return (
      <>
        <DeliveryNavigation />
        <div className="delivery-dashboard">
          <div className="loading-container">
            <LoadingSpinner />
            <p>Loading your dashboard...</p>
          </div>
        </div>
      </>
    );
  }

  if (error) {
    return (
      <>
        <DeliveryNavigation />
        <div className="delivery-dashboard">
          <div className="error-container">
            <h2>Error Loading Dashboard</h2>
            <p>{error}</p>
          </div>
        </div>
      </>
    );
  }

  return (
    <>
      <DeliveryNavigation />
      <div className="delivery-dashboard">
      <div className="dashboard-header">
        <h1>Welcome back, {user.username}!</h1>
        <p className="dashboard-subtitle">Here's your delivery overview for today</p>
      </div>

      <div className="stats-grid">
        <div className="stat-card total-deliveries">
          <div className="stat-icon"></div>
          <div className="stat-content">
            <h3>Total Deliveries</h3>
            <span className="stat-number">{stats?.totalDeliveries || 0}</span>
            <span className="stat-period">All Time</span>
          </div>
        </div>

        <div className="stat-card today-deliveries">
          <div className="stat-icon"></div>
          <div className="stat-content">
            <h3>Today's Deliveries</h3>
            <span className="stat-number">{stats?.todayDeliveries || 0}</span>
            <span className="stat-period">Today</span>
          </div>
        </div>

        <div className="stat-card pending-orders">
          <div className="stat-icon"></div>
          <div className="stat-content">
            <h3>Pending Orders</h3>
            <span className="stat-number">{stats?.pendingOrders || 0}</span>
            <span className="stat-period">Active</span>
          </div>
        </div>

        <div className="stat-card earnings">
          <div className="stat-icon"></div>
          <div className="stat-content">
            <h3>Today's Earnings</h3>
            <span className="stat-number">${stats?.todayEarnings?.toFixed(2) || '0.00'}</span>
            <span className="stat-period">Today</span>
          </div>
        </div>
      </div>

      <div className="dashboard-content">
        <div className="recent-orders-section">
          <h2>Recent Orders</h2>
          {recentOrders.length === 0 ? (
            <div className="no-orders">
              <p>No recent orders assigned to you.</p>
            </div>
          ) : (
            <div className="orders-list">
              {recentOrders.map(order => (
                <div key={order._id} className="order-summary">
                  <div className="order-summary-header">
                    <span className="order-id">#{order._id.slice(-6)}</span>
                    <span 
                      className="order-status"
                      style={{ 
                        backgroundColor: getStatusColor(order.status),
                        color: 'white',
                        padding: '2px 8px',
                        borderRadius: '12px',
                        fontSize: '12px'
                      }}
                    >
                      {order.status}
                    </span>
                  </div>
                  <div className="order-summary-details">
                    <p><strong>{order.customerName}</strong></p>
                    <p className="address">{order.deliveryAddress}</p>
                    <p className="amount">{order.totalAmount?.toFixed(2) || '0.00'}</p>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        <div className="quick-actions-section">
          <h2>Quick Actions</h2>
          <div className="quick-actions">
            <button 
              className="action-btn view-orders"
              onClick={() => window.location.href = '/delivery/orders'}
            >
              <span className="action-icon"></span>
              <span>View All Orders</span>
            </button>
            
            <button 
              className="action-btn emergency"
              onClick={() => window.location.href = 'tel:+1234567890'}
            >
              <span className="action-icon"></span>
              <span>Emergency Contact</span>
            </button>
            
            <button 
              className="action-btn profile"
              onClick={() => window.location.href = '/profile'}
            >
              <span className="action-icon"></span>
              <span>My Profile</span>
            </button>
          </div>
        </div>
      </div>

      <div className="performance-section">
        <h2>Performance Overview</h2>
        <div className="performance-metrics">
          <div className="metric">
            <span className="metric-label">Average Delivery Time</span>
            <span className="metric-value">{stats?.avgDeliveryTime || 'N/A'} min</span>
          </div>
          <div className="metric">
            <span className="metric-label">Customer Rating</span>
            <span className="metric-value">
              {stats?.customerRating ? `${stats.customerRating}/5 ` : 'N/A'}
            </span>
          </div>
          <div className="metric">
            <span className="metric-label">On-Time Delivery Rate</span>
            <span className="metric-value">{stats?.onTimeRate || 'N/A'}%</span>
          </div>
        </div>
      </div>
    </div>
    </>
  );
}

// Helper function to get status color
function getStatusColor(status) {
  switch (status) {
    case 'Order Placed':
      return '#f39c12';
    case 'Cooking in Progress':
      return '#e67e22';
    case 'On the Way to Delivery':
      return '#3498db';
    case 'Delivered':
      return '#27ae60';
    default:
      return '#95a5a6';
  }
}

export default DeliveryDashboard;