import React, { createContext, useContext, useState, useEffect } from 'react';
import { useAuth } from './AuthContext';
import notificationService from '../services/notificationService';

const NotificationContext = createContext(null);

export const NotificationProvider = ({ children }) => {
  const { user } = useAuth();
  const [isSubscribed, setIsSubscribed] = useState(false);
  const [notificationPermission, setNotificationPermission] = useState('default');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    // Check initial notification permission
    if ('Notification' in window) {
      setNotificationPermission(Notification.permission);
    }

    // Check if user is already subscribed
    if (user) {
      const subscription = notificationService.getStoredSubscription(user.id);
      setIsSubscribed(!!subscription);
    }
  }, [user]);

  // Subscribe to push notifications
  const subscribeToPushNotifications = async () => {
    if (!user) {
      throw new Error('User not authenticated');
    }

    try {
      setLoading(true);
      const subscription = await notificationService.subscribeToPushNotifications(
        user.id, 
        user.role
      );
      
      if (subscription) {
        setIsSubscribed(true);
        setNotificationPermission('granted');
        return subscription;
      }
      
      return null;
    } catch (error) {
      console.error('Failed to subscribe to push notifications:', error);
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Unsubscribe from push notifications
  const unsubscribeFromPushNotifications = async () => {
    if (!user) return false;

    try {
      setLoading(true);
      const success = await notificationService.unsubscribeFromPushNotifications(user.id);
      
      if (success) {
        setIsSubscribed(false);
      }
      
      return success;
    } catch (error) {
      console.error('Failed to unsubscribe from push notifications:', error);
      return false;
    } finally {
      setLoading(false);
    }
  };

  // Send order notification (for when new orders are placed)
  const sendOrderNotification = async (orderData) => {
    try {
      await notificationService.sendOrderNotification(orderData);
      
      // Also show local notification if user is admin/employee
      if (user && (user.role === 'admin' || user.role === 'employee')) {
        notificationService.showLocalNotification(
          ` New Order #${orderData.id}`,
          {
            body: `${orderData.customerName} placed an order for $${orderData.total}`,
            tag: `order-${orderData.id}`,
            requireInteraction: true
          }
        );
      }
    } catch (error) {
      console.error('Failed to send order notification:', error);
    }
  };

  // Send status update notification
  const sendStatusUpdateNotification = async (orderId, customerId, status, message) => {
    try {
      await notificationService.sendStatusUpdateNotification(orderId, customerId, status, message);
    } catch (error) {
      console.error('Failed to send status update notification:', error);
    }
  };

  // Show local notification
  const showLocalNotification = (title, options) => {
    return notificationService.showLocalNotification(title, options);
  };

  // Request notification permission
  const requestNotificationPermission = async () => {
    try {
      const granted = await notificationService.requestPermission();
      setNotificationPermission(granted ? 'granted' : 'denied');
      return granted;
    } catch (error) {
      console.error('Failed to request notification permission:', error);
      return false;
    }
  };

  const value = {
    // State
    isSubscribed,
    notificationPermission,
    loading,
    isSupported: notificationService.isNotificationSupported(),
    
    // Actions
    subscribeToPushNotifications,
    unsubscribeFromPushNotifications,
    sendOrderNotification,
    sendStatusUpdateNotification,
    showLocalNotification,
    requestNotificationPermission
  };

  return (
    <NotificationContext.Provider value={value}>
      {children}
    </NotificationContext.Provider>
  );
};

export const useNotifications = () => {
  const context = useContext(NotificationContext);
  if (!context) {
    throw new Error('useNotifications must be used within a NotificationProvider');
  }
  return context;
};

export default NotificationContext;