import React from 'react';
import { useSettings } from '../../../contexts/SettingsContext';
import './Footer.css';

function Footer() {
  const { settings, formatBusinessHours, getBusinessInfo } = useSettings();
  
  const businessInfo = getBusinessInfo();
  const formattedHours = formatBusinessHours();
  
  // Debug logging
  React.useEffect(() => {
    console.log(' Footer - Business info updated:', businessInfo);
    console.log(' Footer - Full settings:', settings);
  }, [businessInfo, settings]);
  
  // Group consecutive days with same hours for better display
  const groupedHours = () => {
    const hours = settings.business?.openingHours || {};
    const daysOrder = ['monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday', 'sunday'];
    const dayNames = {
      monday: 'Mon', tuesday: 'Tue', wednesday: 'Wed', thursday: 'Thu',
      friday: 'Fri', saturday: 'Sat', sunday: 'Sun'
    };
    
    const formatTime = (time) => {
      const [hour, minute] = time.split(':');
      const hourNum = parseInt(hour);
      const ampm = hourNum >= 12 ? 'PM' : 'AM';
      const displayHour = hourNum === 0 ? 12 : hourNum > 12 ? hourNum - 12 : hourNum;
      return `${displayHour}:${minute} ${ampm}`;
    };
    
    const groups = [];
    let currentGroup = null;
    
    daysOrder.forEach(day => {
      const dayHours = hours[day];
      const hoursString = dayHours?.closed ? 'Closed' : 
        `${formatTime(dayHours?.open || '07:00')} - ${formatTime(dayHours?.close || '19:00')}`;
      
      if (currentGroup && currentGroup.hours === hoursString) {
        currentGroup.days.push(dayNames[day]);
      } else {
        if (currentGroup) groups.push(currentGroup);
        currentGroup = {
          days: [dayNames[day]],
          hours: hoursString
        };
      }
    });
    
    if (currentGroup) groups.push(currentGroup);
    
    return groups.map(group => {
      const dayRange = group.days.length > 1 && group.days.length > 2 ? 
        `${group.days[0]} - ${group.days[group.days.length - 1]}` :
        group.days.join(', ');
      return `${dayRange}: ${group.hours}`;
    });
  };

  return (
    <footer className="app-footer">
      <div className="footer-content">
        <div className="footer-section">
          <h3>Contact Us</h3>
          <ul>
            <li> {businessInfo.address || '123 Coffee Street, Brew City'}</li>
            <li> {businessInfo.phone || '(555) 123-CAFE'}</li>
            <li> {businessInfo.email || 'hello@sanctumcafe.com'}</li>
          </ul>
        </div>
        
        <div className="footer-section">
          <h3>Hours</h3>
          <ul>
            {groupedHours().map((hourLine, index) => (
              <li key={index}>{hourLine}</li>
            ))}
          </ul>
        </div>
        
        <div className="footer-section">
          <h3>Quick Links</h3>
          <ul>
            <li><a href="/about">About Us</a></li>
            <li><a href="/menu">Our Menu</a></li>
            <li><a href="/careers">Careers</a></li>
            <li><a href="/contact">Contact</a></li>
          </ul>
        </div>
      </div>
      
      <div className="footer-bottom">
        <div className="social-links">
          <a href="#" aria-label="Facebook"></a>
          <a href="#" aria-label="Instagram"></a>
          <a href="#" aria-label="Twitter"></a>
        </div>
        <p>&copy; {new Date().getFullYear()} {businessInfo.name || 'The Sanctum Cafe'}. All rights reserved.</p>
      </div>
    </footer>
  );
}

export default Footer;