import React, { useState, useEffect } from 'react';
import { useAuth } from '../../../contexts/AuthContext';
import { useNotifications } from '../../../contexts/NotificationContext';
import './NotificationBanner.css';

const NotificationBanner = () => {
  const { user } = useAuth();
  const { isSubscribed, notificationPermission, subscribeToPushNotifications } = useNotifications();
  const [showBanner, setShowBanner] = useState(false);
  const [dismissed, setDismissed] = useState(false);

  useEffect(() => {
    // Show banner for admin/employee users who aren't subscribed to notifications
    if (user && 
        (user.role === 'admin' || user.role === 'employee') && 
        !isSubscribed && 
        notificationPermission !== 'denied' &&
        !dismissed) {
      setShowBanner(true);
    } else {
      setShowBanner(false);
    }
  }, [user, isSubscribed, notificationPermission, dismissed]);

  const handleSubscribe = async () => {
    try {
      await subscribeToPushNotifications();
      setShowBanner(false);
    } catch (error) {
      console.error('Failed to subscribe to notifications:', error);
    }
  };

  const handleDismiss = () => {
    setDismissed(true);
    setShowBanner(false);
    // Remember dismissal for this session
    sessionStorage.setItem('notification-banner-dismissed', 'true');
  };

  // Check if banner was dismissed this session
  useEffect(() => {
    const wasDismissed = sessionStorage.getItem('notification-banner-dismissed');
    if (wasDismissed) {
      setDismissed(true);
    }
  }, []);

  if (!showBanner) return null;

  return (
    <div className="notification-banner">
      <div className="notification-banner-content">
        <div className="notification-banner-icon"></div>
        <div className="notification-banner-text">
          <strong>Stay Updated!</strong>
          <p>Enable notifications to get instant alerts when new orders are placed.</p>
        </div>
        <div className="notification-banner-actions">
          <button 
            className="notification-banner-btn primary"
            onClick={handleSubscribe}
          >
            Enable Notifications
          </button>
          <button 
            className="notification-banner-btn secondary"
            onClick={handleDismiss}
          >
            Maybe Later
          </button>
        </div>
      </div>
    </div>
  );
};

export default NotificationBanner;