import React, { useState, useEffect } from 'react';
import { useSettings } from '../../contexts/SettingsContext';
import { usePWA } from '../../contexts/PWAContext';
import { useNotifications } from '../../contexts/NotificationContext';
import { useAuth } from '../../contexts/AuthContext';
import Button from '../../components/common/Button/Button';
import Input from '../../components/common/Input/Input';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import PWAGuide from '../../components/common/PWAGuide/PWAGuide';
import adminService from '../../services/adminService';
import './SettingsPage.css';

function SettingsPage() {
  const { user } = useAuth();
  const { settings: contextSettings, updateSettings, loading: contextLoading } = useSettings();
  const {
    isOnline,
    isInstalled,
    hasServiceWorker,
    cacheInfo,
    offlineOrders,
    clearCache,
    updateCacheInfo,
    subscribeToPushNotifications,
    showNotification
  } = usePWA();
  const {
    isSubscribed,
    notificationPermission,
    loading: notificationLoading,
    isSupported: notificationSupported,
    subscribeToPushNotifications: subscribeToNotifications,
    unsubscribeFromPushNotifications,
    requestNotificationPermission
  } = useNotifications();
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);
  const [activeTab, setActiveTab] = useState('business');
  const [showPWAGuide, setShowPWAGuide] = useState(false);
  const [isClearing, setIsClearing] = useState(false);
  const [pushSubscription, setPushSubscription] = useState(null);
  const [reportFromDate, setReportFromDate] = useState('');
  const [reportToDate, setReportToDate] = useState('');
  const [downloadingReport, setDownloadingReport] = useState(false);
  const [saveMessage, setSaveMessage] = useState('');
  const [saveMessageType, setSaveMessageType] = useState(''); // 'success' or 'error'
  const [settings, setSettings] = useState({
    business: {
      name: 'Sanctum Cafe',
      address: '123 Coffee Street, Brew City, BC 12345',
      phone: '(555) 123-CAFE',
      email: 'info@sanctumcafe.com',
      website: 'https://sanctumcafe.com',
      description: 'A cozy neighborhood cafe serving premium coffee and fresh food.',
      openingHours: {
        monday: { open: '07:00', close: '19:00', closed: false },
        tuesday: { open: '07:00', close: '19:00', closed: false },
        wednesday: { open: '07:00', close: '19:00', closed: false },
        thursday: { open: '07:00', close: '19:00', closed: false },
        friday: { open: '07:00', close: '20:00', closed: false },
        saturday: { open: '08:00', close: '20:00', closed: false },
        sunday: { open: '08:00', close: '18:00', closed: false }
      }
    },
    delivery: {
      enabled: true,
      freeDeliveryThreshold: 25.00,
      deliveryFee: 3.99,
      maxDeliveryDistance: 10,
      estimatedDeliveryTime: 30,
      deliveryZones: [
        { name: 'Downtown', radius: 3, fee: 2.99 },
        { name: 'Midtown', radius: 6, fee: 3.99 },
        { name: 'Suburbs', radius: 10, fee: 5.99 }
      ]
    },
    payment: {
      acceptCash: true,
      acceptCard: true,
      acceptDigitalWallet: true,
      taxRate: 8.5,
      tipSuggestions: [15, 18, 20, 25],
      paymentGateways: {
        stripe: { enabled: true, publicKey: 'pk_test_...' },
        paypal: { enabled: false, clientId: '' },
        square: { enabled: false, applicationId: '' }
      }
    },
    notifications: {
      emailNotifications: true,
      smsNotifications: false,
      pushNotifications: true,
      orderNotifications: true,
      inventoryAlerts: true,
      customerFeedback: true
    },
    system: {
      timezone: 'Asia/Kolkata',
      dateFormat: 'DD/MM/YYYY',
      currency: 'INR',
      language: 'en',
      autoBackup: true,
      backupFrequency: 'daily',
      maintenanceMode: false,
      maintenanceMessage: 'Sorry, we are not delivering currently. Please try again later!'
    }
  });

  // Update local settings when context settings change
  useEffect(() => {
    if (contextSettings) {
      setSettings(contextSettings);
    }
  }, [contextSettings]);

  const handleInputChange = (section, field, value) => {
    setSettings(prev => ({
      ...prev,
      [section]: {
        ...prev[section],
        [field]: value
      }
    }));
  };

  const handleNestedInputChange = (section, parentField, field, value) => {
    setSettings(prev => ({
      ...prev,
      [section]: {
        ...prev[section],
        [parentField]: {
          ...prev[section][parentField],
          [field]: value
        }
      }
    }));
  };

  const handleHoursChange = (day, field, value) => {
    setSettings(prev => ({
      ...prev,
      business: {
        ...prev.business,
        openingHours: {
          ...prev.business.openingHours,
          [day]: {
            ...prev.business.openingHours[day],
            [field]: value
          }
        }
      }
    }));
  };

  const handleSaveSettings = async (section) => {
    try {
      setSaving(true);
      setSaveMessage('');
      
      console.log(` Saving ${section} settings:`, settings[section]);
      const result = await updateSettings(section, settings[section]);
      
      if (result.success) {
        setSaveMessage(`${section.charAt(0).toUpperCase() + section.slice(1)} settings saved successfully!`);
        setSaveMessageType('success');
        console.log(` ${section} settings saved successfully`);
        
        // Clear message after 3 seconds
        setTimeout(() => {
          setSaveMessage('');
          setSaveMessageType('');
        }, 3000);
      } else {
        setSaveMessage(`Error saving ${section} settings: ${result.message}`);
        setSaveMessageType('error');
        console.error('Error saving settings:', result.message);
        
        // Clear error message after 5 seconds
        setTimeout(() => {
          setSaveMessage('');
          setSaveMessageType('');
        }, 5000);
      }
    } catch (error) {
      console.error('Error saving settings:', error);
      setSaveMessage(`Error saving ${section} settings: ${error.message}`);
      setSaveMessageType('error');
      
      // Clear error message after 5 seconds
      setTimeout(() => {
        setSaveMessage('');
        setSaveMessageType('');
      }, 5000);
    } finally {
      setSaving(false);
    }
  };

  const handleDownloadReport = async () => {
    if (!reportFromDate || !reportToDate) {
      alert('Please select both from and to dates');
      return;
    }

    if (new Date(reportFromDate) > new Date(reportToDate)) {
      alert('From date cannot be later than to date');
      return;
    }

    try {
      setDownloadingReport(true);
      await adminService.downloadOrdersReport(reportFromDate, reportToDate);
    } catch (error) {
      console.error('Error downloading report:', error);
      alert('Failed to download report. Please try again.');
    } finally {
      setDownloadingReport(false);
    }
  };

  const tabs = [
    { id: 'business', label: 'Business Info', icon: '' },
    { id: 'delivery', label: 'Delivery', icon: '' },
    { id: 'payment', label: 'Payment', icon: '' },
    { id: 'notifications', label: 'Notifications', icon: '' },
    { id: 'system', label: 'System', icon: '' },
    { id: 'reports', label: 'Reports', icon: '' },
    { id: 'pwa', label: 'PWA', icon: '' }
  ];

  const renderBusinessSettings = () => (
    <div className="settings-section">
      <h3>Business Information</h3>
      
      {saveMessage && (
        <div className={`save-message ${saveMessageType}`}>
          {saveMessage}
        </div>
      )}
      
      <div className="form-grid">
        <Input
          label="Business Name"
          value={settings.business.name}
          onChange={(e) => handleInputChange('business', 'name', e.target.value)}
        />
        
        <Input
          label="Phone Number"
          value={settings.business.phone}
          onChange={(e) => handleInputChange('business', 'phone', e.target.value)}
        />
        
        <Input
          label="Email Address"
          type="email"
          value={settings.business.email}
          onChange={(e) => handleInputChange('business', 'email', e.target.value)}
        />
        
        <Input
          label="Website"
          value={settings.business.website}
          onChange={(e) => handleInputChange('business', 'website', e.target.value)}
        />
        
        <Input
          label="Address"
          type="textarea"
          value={settings.business.address}
          onChange={(e) => handleInputChange('business', 'address', e.target.value)}
          className="full-width"
        />
        
        <Input
          label="Description"
          type="textarea"
          value={settings.business.description}
          onChange={(e) => handleInputChange('business', 'description', e.target.value)}
          className="full-width"
        />
      </div>

      <div className="opening-hours-section">
        <h4>Opening Hours</h4>
        <div className="hours-grid">
          {Object.entries(settings.business.openingHours).map(([day, hours]) => (
            <div key={day} className="hours-row">
              <div className="day-label">
                {day.charAt(0).toUpperCase() + day.slice(1)}
              </div>
              <div className="hours-controls">
                <label className="closed-checkbox">
                  <input
                    type="checkbox"
                    checked={hours.closed}
                    onChange={(e) => handleHoursChange(day, 'closed', e.target.checked)}
                  />
                  <span>Closed</span>
                </label>
                {!hours.closed && (
                  <>
                    <input
                      type="time"
                      value={hours.open}
                      onChange={(e) => handleHoursChange(day, 'open', e.target.value)}
                      className="time-input"
                    />
                    <span>to</span>
                    <input
                      type="time"
                      value={hours.close}
                      onChange={(e) => handleHoursChange(day, 'close', e.target.value)}
                      className="time-input"
                    />
                  </>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>

      <div className="section-actions">
        <Button
          variant="primary"
          onClick={() => handleSaveSettings('business')}
          disabled={saving}
        >
          {saving ? 'Saving...' : 'Save Business Settings'}
        </Button>
      </div>
    </div>
  );

  const renderDeliverySettings = () => (
    <div className="settings-section">
      <h3>Delivery Configuration</h3>
      
      <div className="form-grid">
        <div className="checkbox-group">
          <label className="checkbox-label">
            <input
              type="checkbox"
              checked={settings.delivery.enabled}
              onChange={(e) => handleInputChange('delivery', 'enabled', e.target.checked)}
            />
            <span>Enable Delivery Service</span>
          </label>
        </div>
        
        {settings.delivery.enabled && (
          <>
            <Input
              label="Free Delivery Threshold ($)"
              type="number"
              step="0.01"
              value={settings.delivery.freeDeliveryThreshold}
              onChange={(e) => handleInputChange('delivery', 'freeDeliveryThreshold', parseFloat(e.target.value))}
            />
            
            <Input
              label="Standard Delivery Fee ($)"
              type="number"
              step="0.01"
              value={settings.delivery.deliveryFee}
              onChange={(e) => handleInputChange('delivery', 'deliveryFee', parseFloat(e.target.value))}
            />
            
            <Input
              label="Max Delivery Distance (miles)"
              type="number"
              value={settings.delivery.maxDeliveryDistance}
              onChange={(e) => handleInputChange('delivery', 'maxDeliveryDistance', parseInt(e.target.value))}
            />
            
            <Input
              label="Estimated Delivery Time (minutes)"
              type="number"
              value={settings.delivery.estimatedDeliveryTime}
              onChange={(e) => handleInputChange('delivery', 'estimatedDeliveryTime', parseInt(e.target.value))}
            />
          </>
        )}
      </div>

      <div className="section-actions">
        <Button
          variant="primary"
          onClick={() => handleSaveSettings('delivery')}
          disabled={saving}
        >
          {saving ? 'Saving...' : 'Save Delivery Settings'}
        </Button>
      </div>
    </div>
  );

  const renderPaymentSettings = () => (
    <div className="settings-section">
      <h3>Payment Configuration</h3>
      
      <div className="form-grid">
        <div className="payment-methods">
          <h4>Accepted Payment Methods</h4>
          <div className="checkbox-group">
            <label className="checkbox-label">
              <input
                type="checkbox"
                checked={settings.payment.acceptCash}
                onChange={(e) => handleInputChange('payment', 'acceptCash', e.target.checked)}
              />
              <span> Cash</span>
            </label>
            
            <label className="checkbox-label">
              <input
                type="checkbox"
                checked={settings.payment.acceptCard}
                onChange={(e) => handleInputChange('payment', 'acceptCard', e.target.checked)}
              />
              <span> Credit/Debit Cards</span>
            </label>
            
            <label className="checkbox-label">
              <input
                type="checkbox"
                checked={settings.payment.acceptDigitalWallet}
                onChange={(e) => handleInputChange('payment', 'acceptDigitalWallet', e.target.checked)}
              />
              <span> Digital Wallets</span>
            </label>
          </div>
        </div>
        
        <Input
          label="Tax Rate (%)"
          type="number"
          step="0.1"
          value={settings.payment.taxRate}
          onChange={(e) => handleInputChange('payment', 'taxRate', parseFloat(e.target.value))}
        />
      </div>

      <div className="section-actions">
        <Button
          variant="primary"
          onClick={() => handleSaveSettings('payment')}
          disabled={saving}
        >
          {saving ? 'Saving...' : 'Save Payment Settings'}
        </Button>
      </div>
    </div>
  );

  const renderNotificationSettings = () => (
    <div className="settings-section">
      <h3>Notification Preferences</h3>
      
      <div className="notification-options">
        <div className="checkbox-group">
          <label className="checkbox-label">
            <input
              type="checkbox"
              checked={settings.notifications.emailNotifications}
              onChange={(e) => handleInputChange('notifications', 'emailNotifications', e.target.checked)}
            />
            <span> Email Notifications</span>
          </label>
          
          <label className="checkbox-label">
            <input
              type="checkbox"
              checked={settings.notifications.smsNotifications}
              onChange={(e) => handleInputChange('notifications', 'smsNotifications', e.target.checked)}
            />
            <span> SMS Notifications</span>
          </label>
          
          <label className="checkbox-label">
            <input
              type="checkbox"
              checked={settings.notifications.pushNotifications}
              onChange={(e) => handleInputChange('notifications', 'pushNotifications', e.target.checked)}
            />
            <span> Push Notifications</span>
          </label>
          
          <label className="checkbox-label">
            <input
              type="checkbox"
              checked={settings.notifications.orderNotifications}
              onChange={(e) => handleInputChange('notifications', 'orderNotifications', e.target.checked)}
            />
            <span> Order Notifications</span>
          </label>
          
          <label className="checkbox-label">
            <input
              type="checkbox"
              checked={settings.notifications.inventoryAlerts}
              onChange={(e) => handleInputChange('notifications', 'inventoryAlerts', e.target.checked)}
            />
            <span> Inventory Alerts</span>
          </label>
          
          <label className="checkbox-label">
            <input
              type="checkbox"
              checked={settings.notifications.customerFeedback}
              onChange={(e) => handleInputChange('notifications', 'customerFeedback', e.target.checked)}
            />
            <span> Customer Feedback</span>
          </label>
        </div>
      </div>

      <div className="section-actions">
        <Button
          variant="primary"
          onClick={() => handleSaveSettings('notifications')}
          disabled={saving}
        >
          {saving ? 'Saving...' : 'Save Notification Settings'}
        </Button>
      </div>
    </div>
  );

  const renderSystemSettings = () => (
    <div className="settings-section">
      <h3>System Configuration</h3>
      
      <div className="form-grid">
        <div className="input-group">
          <label>Timezone</label>
          <select
            value={settings.system.timezone}
            onChange={(e) => handleInputChange('system', 'timezone', e.target.value)}
            className="form-select"
          >
            <option value="America/New_York">Eastern Time</option>
            <option value="America/Chicago">Central Time</option>
            <option value="America/Denver">Mountain Time</option>
            <option value="America/Los_Angeles">Pacific Time</option>
          </select>
        </div>
        
        <div className="input-group">
          <label>Date Format</label>
          <select
            value={settings.system.dateFormat}
            onChange={(e) => handleInputChange('system', 'dateFormat', e.target.value)}
            className="form-select"
          >
            <option value="MM/DD/YYYY">MM/DD/YYYY</option>
            <option value="DD/MM/YYYY">DD/MM/YYYY</option>
            <option value="YYYY-MM-DD">YYYY-MM-DD</option>
          </select>
        </div>
        
        <div className="input-group">
          <label>Currency</label>
          <select
            value={settings.system.currency}
            onChange={(e) => handleInputChange('system', 'currency', e.target.value)}
            className="form-select"
          >
            <option value="USD">USD ($)</option>
            <option value="EUR">EUR ()</option>
            <option value="GBP">GBP ()</option>
            <option value="INR">INR ()</option>
          </select>
        </div>
        
        <div className="checkbox-group">
          <label className="checkbox-label">
            <input
              type="checkbox"
              checked={settings.system.autoBackup}
              onChange={(e) => handleInputChange('system', 'autoBackup', e.target.checked)}
            />
            <span>Enable Automatic Backups</span>
          </label>
          
          <label className="checkbox-label">
            <input
              type="checkbox"
              checked={settings.system.maintenanceMode}
              onChange={(e) => handleInputChange('system', 'maintenanceMode', e.target.checked)}
            />
            <span> Maintenance Mode</span>
          </label>
        </div>
        
        {settings.system.maintenanceMode && (
          <div className="maintenance-message-section">
            <Input
              label="Maintenance Message"
              type="textarea"
              value={settings.system.maintenanceMessage}
              onChange={(e) => handleInputChange('system', 'maintenanceMessage', e.target.value)}
              placeholder="Enter the message to display when maintenance mode is enabled"
              className="full-width"
            />
            <p className="help-text">
              This message will be displayed to customers instead of the menu when maintenance mode is enabled.
            </p>
          </div>
        )}
      </div>

      <div className="section-actions">
        <Button
          variant="primary"
          onClick={() => handleSaveSettings('system')}
          disabled={saving}
        >
          {saving ? 'Saving...' : 'Save System Settings'}
        </Button>
      </div>
    </div>
  );

  const renderReportsSettings = () => (
    <div className="settings-section">
      <h3>Download Reports</h3>
      <p>Generate and download Excel reports for orders within a specific date range</p>
      
      <div className="reports-section">
        <div className="report-card">
          <h4> Orders Report</h4>
          <p>Download a comprehensive Excel report containing all order details, customer information, and summary statistics for the selected date range.</p>
          
          <div className="report-form">
            <div className="date-inputs">
              <div className="input-group">
                <label htmlFor="fromDate">From Date:</label>
                <input
                  id="fromDate"
                  type="date"
                  value={reportFromDate}
                  onChange={(e) => setReportFromDate(e.target.value)}
                  className="form-input"
                  max={new Date().toISOString().split('T')[0]}
                />
              </div>
              
              <div className="input-group">
                <label htmlFor="toDate">To Date:</label>
                <input
                  id="toDate"
                  type="date"
                  value={reportToDate}
                  onChange={(e) => setReportToDate(e.target.value)}
                  className="form-input"
                  max={new Date().toISOString().split('T')[0]}
                  min={reportFromDate}
                />
              </div>
            </div>
            
            <div className="report-info">
              <h5>Report will include:</h5>
              <ul>
                <li> Order ID, Customer details (Name, Phone, Email)</li>
                <li> Order type (Delivery, Take Away, Dine In)</li>
                <li> Items ordered with quantities</li>
                <li> Pricing details (Subtotal, Tax, Delivery Fee, Total)</li>
                <li> Order status and payment method</li>
                <li> Table number (for dine-in orders)</li>
                <li> Delivery address (for delivery orders)</li>
                <li> Order date and time</li>
                <li> Summary statistics (Total orders count, Total amount)</li>
                <li> Status breakdown</li>
              </ul>
            </div>
            
            <div className="report-actions">
              <Button
                variant="primary"
                onClick={handleDownloadReport}
                disabled={downloadingReport || !reportFromDate || !reportToDate}
                className="download-report-btn"
              >
                {downloadingReport ? (
                  <>
                    <span className="loading-spinner"></span>
                    Generating Report...
                  </>
                ) : (
                  <>
                     Download Excel Report
                  </>
                )}
              </Button>
              
              {reportFromDate && reportToDate && (
                <div className="date-range-info">
                  <small>
                    Report period: {new Date(reportFromDate).toLocaleDateString()} - {new Date(reportToDate).toLocaleDateString()}
                  </small>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  const renderPWASettings = () => {

    const handleClearCache = async () => {
      setIsClearing(true);
      const success = await clearCache();
      if (success) {
        showNotification('Cache cleared successfully', 'success');
        await updateCacheInfo();
      } else {
        showNotification('Failed to clear cache', 'error');
      }
      setIsClearing(false);
    };



    const formatBytes = (bytes) => {
      if (bytes === 0) return '0 Bytes';
      const k = 1024;
      const sizes = ['Bytes', 'KB', 'MB', 'GB'];
      const i = Math.floor(Math.log(bytes) / Math.log(k));
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
    };

    return (
      <div className="settings-section">
        <h3>Progressive Web App Settings</h3>
        <p>Manage PWA features and monitor app performance</p>

        {/* App Status */}
        <div className="pwa-status-grid">
          <div className="pwa-status-card">
            <h4> App Status</h4>
            <div className="status-items">
              <div className="status-item">
                <span>Connection:</span>
                <span className={`status-badge ${isOnline ? 'online' : 'offline'}`}>
                  {isOnline ? ' Online' : ' Offline'}
                </span>
              </div>
              <div className="status-item">
                <span>Installation:</span>
                <span className={`status-badge ${isInstalled ? 'installed' : 'browser'}`}>
                  {isInstalled ? ' Installed' : ' Browser'}
                </span>
              </div>
              <div className="status-item">
                <span>Service Worker:</span>
                <span className={`status-badge ${hasServiceWorker ? 'active' : 'inactive'}`}>
                  {hasServiceWorker ? ' Active' : ' Inactive'}
                </span>
              </div>
            </div>
          </div>

          {/* Cache Management */}
          <div className="pwa-status-card">
            <h4> Cache Management</h4>
            {cacheInfo ? (
              <div className="cache-info">
                <div className="cache-stat">
                  <span>Used: {formatBytes(cacheInfo.used)}</span>
                  <span>Available: {formatBytes(cacheInfo.available)}</span>
                </div>
                <div className="cache-progress">
                  <div 
                    className="cache-progress-bar"
                    style={{ width: `${cacheInfo.percentage}%` }}
                  ></div>
                </div>
                <span className="cache-percentage">{cacheInfo.percentage}% used</span>
              </div>
            ) : (
              <p>Cache information not available</p>
            )}
            <div className="cache-actions">
              <Button
                variant="outline"
                size="small"
                onClick={handleClearCache}
                disabled={isClearing}
              >
                {isClearing ? ' Clearing...' : ' Clear Cache'}
              </Button>
              <Button
                variant="outline"
                size="small"
                onClick={updateCacheInfo}
              >
                 Refresh
              </Button>
            </div>
          </div>

          {/* Offline Orders */}
          <div className="pwa-status-card">
            <h4> Offline Orders</h4>
            {offlineOrders && offlineOrders.length > 0 ? (
              <div className="offline-orders">
                <p className="orders-count">
                  <strong>{offlineOrders.length}</strong> orders pending sync
                </p>
                <div className="orders-preview">
                  {offlineOrders.slice(0, 2).map((order, index) => (
                    <div key={index} className="order-preview">
                      <span>{new Date(order.timestamp).toLocaleDateString()}</span>
                      <span>{order.items?.length || 0} items</span>
                    </div>
                  ))}
                </div>
              </div>
            ) : (
              <p>No offline orders pending</p>
            )}
          </div>

          {/* Push Notifications */}
          <div className="pwa-status-card">
            <h4> Push Notifications</h4>
            <p>Enable push notifications for new orders and status updates.</p>
            
            {!notificationSupported ? (
              <div className="notification-not-supported">
                <span className="status-badge inactive"> Not Supported</span>
                <p>Your browser doesn't support push notifications.</p>
              </div>
            ) : (
              <div className="notification-controls">
                <div className="notification-status">
                  <span>Permission: </span>
                  <span className={`status-badge ${notificationPermission === 'granted' ? 'active' : 'inactive'}`}>
                    {notificationPermission === 'granted' ? ' Granted' : 
                     notificationPermission === 'denied' ? ' Denied' : ' Default'}
                  </span>
                </div>
                
                <div className="notification-subscription">
                  <span>Subscription: </span>
                  <span className={`status-badge ${isSubscribed ? 'active' : 'inactive'}`}>
                    {isSubscribed ? ' Subscribed' : ' Not Subscribed'}
                  </span>
                </div>

                <div className="notification-actions">
                  {notificationPermission !== 'granted' && (
                    <Button
                      variant="outline"
                      size="small"
                      onClick={requestNotificationPermission}
                      disabled={notificationLoading}
                    >
                       Request Permission
                    </Button>
                  )}
                  
                  {notificationPermission === 'granted' && !isSubscribed && (
                    <Button
                      variant="primary"
                      size="small"
                      onClick={subscribeToNotifications}
                      disabled={notificationLoading}
                    >
                      {notificationLoading ? ' Subscribing...' : ' Subscribe to Notifications'}
                    </Button>
                  )}
                  
                  {isSubscribed && (
                    <Button
                      variant="outline"
                      size="small"
                      onClick={unsubscribeFromPushNotifications}
                      disabled={notificationLoading}
                    >
                      {notificationLoading ? ' Unsubscribing...' : ' Unsubscribe'}
                    </Button>
                  )}
                </div>

                {user && (user.role === 'admin' || user.role === 'employee') && isSubscribed && (
                  <div className="notification-info">
                    <p className="notification-note">
                       You will receive notifications for new orders and status updates.
                    </p>
                  </div>
                )}
              </div>
            )}
          </div>
        </div>

        <div className="section-actions">
          <Button
            variant="primary"
            onClick={() => setShowPWAGuide(true)}
          >
             Installation Guide
          </Button>
          <Button
            variant="outline"
            onClick={() => window.open('/offline.html', '_blank')}
          >
             Test Offline Page
          </Button>
          <Button
            variant="outline"
            onClick={() => {
              if ('serviceWorker' in navigator) {
                navigator.serviceWorker.getRegistrations().then(registrations => {
                  console.log('Service Worker Registrations:', registrations);
                  showNotification(`Found ${registrations.length} service worker(s)`, 'info');
                });
              }
            }}
          >
             Debug SW
          </Button>
        </div>
      </div>
    );
  };

  const renderTabContent = () => {
    switch (activeTab) {
      case 'business':
        return renderBusinessSettings();
      case 'delivery':
        return renderDeliverySettings();
      case 'payment':
        return renderPaymentSettings();
      case 'notifications':
        return renderNotificationSettings();
      case 'system':
        return renderSystemSettings();
      case 'reports':
        return renderReportsSettings();
      case 'pwa':
        return renderPWASettings();
      default:
        return renderBusinessSettings();
    }
  };

  if (loading) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-loading">
            <LoadingSpinner size="large" />
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="settings-page">
      <div className="container">
        <div className="settings-header">
          <h1>Settings</h1>
          <p>Configure your cafe's business settings and preferences</p>
        </div>

        <div className="settings-content">
          <div className="settings-tabs">
            {tabs.map(tab => (
              <button
                key={tab.id}
                className={`tab-button ${activeTab === tab.id ? 'active' : ''}`}
                onClick={() => setActiveTab(tab.id)}
              >
                <span className="tab-icon">{tab.icon}</span>
                <span className="tab-label">{tab.label}</span>
              </button>
            ))}
          </div>

          <div className="settings-panel">
            {renderTabContent()}
          </div>
        </div>
      </div>
      
      {showPWAGuide && (
        <PWAGuide onClose={() => setShowPWAGuide(false)} />
      )}
    </div>
  );
}

export default SettingsPage;