import React, { createContext, useState, useContext, useEffect } from 'react';
import authService from '../services/authService';

const AuthContext = createContext(null);

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [token, setToken] = useState(null);
  const [loading, setLoading] = useState(true);

  // Auto-refresh token before it expires
  useEffect(() => {
    if (!token) return;

    const checkTokenExpiration = () => {
      if (authService.isTokenExpired(token)) {
        console.log('Auth: Token expired, attempting refresh...');
        authService.refreshToken()
          .then((newToken) => {
            if (newToken) {
              setToken(newToken);
              console.log('Auth: Token refreshed successfully');
            } else {
              console.log('Auth: Token refresh failed, logging out');
              logout();
            }
          })
          .catch((error) => {
            console.error('Auth: Token refresh error:', error);
            logout();
          });
      }
    };

    // Check token expiration every 5 minutes
    const interval = setInterval(checkTokenExpiration, 5 * 60 * 1000);
    
    // Also check immediately
    checkTokenExpiration();

    return () => clearInterval(interval);
  }, [token]);

  // Initialize auth state from localStorage
  useEffect(() => {
    const initializeAuth = async () => {
      const storedToken = authService.getToken();
      const storedUser = authService.getCurrentUser();
      
      if (storedToken && storedUser) {
        // First, check if token is expired locally (no network request)
        if (authService.isTokenExpired(storedToken)) {
          console.log('Auth: Token expired locally, clearing storage');
          authService.logout();
          setUser(null);
          setToken(null);
        } else {
          try {
            // Token seems valid locally, verify with server
            const isValid = await authService.verifyToken(storedToken);
            if (isValid) {
              setToken(storedToken);
              setUser(storedUser);
              console.log('Auth: User session restored and verified');
            } else {
              // Server says token is invalid
              console.log('Auth: Server rejected token, clearing storage');
              authService.logout();
              setUser(null);
              setToken(null);
            }
          } catch (error) {
            // Network error or server down - use graceful degradation
            if (error.response?.status === 401) {
              // Definitely unauthorized, clear storage
              console.log('Auth: Token unauthorized (401), clearing storage');
              authService.logout();
              setUser(null);
              setToken(null);
            } else {
              // Network/server error - restore session anyway for better UX
              console.warn('Auth: Server verification failed (network issue), restoring session locally:', error.message);
              setToken(storedToken);
              setUser(storedUser);
            }
          }
        }
      }
      setLoading(false);
    };

    initializeAuth();
  }, []);

  const login = async (identifier, password) => {
    try {
      const response = await authService.login(identifier, password);
      setUser(response.user);
      setToken(response.token);
      return { success: true, user: response.user, token: response.token };
    } catch (error) {
      console.error('Login error:', error);
      return { 
        success: false, 
        message: error.response?.data?.message || 'Login failed' 
      };
    }
  };

  const customerLogin = async (mobile, name) => {
    try {
      const response = await authService.customerLogin(mobile, name);
      setUser(response.user);
      setToken(response.token);
      return { success: true, user: response.user, token: response.token };
    } catch (error) {
      console.error('Customer login error:', error);
      return { 
        success: false, 
        message: error.response?.data?.message || 'Login failed' 
      };
    }
  };

  const logout = () => {
    authService.logout();
    setUser(null);
    setToken(null);
  };

  const register = async (userData) => {
    try {
      const response = await authService.register(userData);
      return { success: true, message: response.message };
    } catch (error) {
      console.error('Registration error:', error);
      return { 
        success: false, 
        message: error.response?.data?.message || 'Registration failed' 
      };
    }
  };

  return (
    <AuthContext.Provider value={{ user, token, login, customerLogin, logout, register, loading }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  return useContext(AuthContext);
};