import React from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import Button from '../components/common/Button/Button';
import './HomePage.css';

function HomePage() {
  const { user } = useAuth();

  return (
    <div className="home-page">
      <div className="container">
        <div className="hero-section">
        <h1>Welcome to The Sanctum Cafe</h1>
        <p className="hero-subtitle">Your perfect coffee experience awaits</p>
        
        {!user ? (
          <div className="auth-actions">
            <p>Please login to access your portal</p>
            <div className="button-group">
              <Link to="/login">
                <Button variant="primary" size="large">Login</Button>
              </Link>
              <Link to="/register">
                <Button variant="secondary" size="large">Register</Button>
              </Link>
            </div>
          </div>
        ) : (
          <div className="user-portal">
            <h2>Welcome back, {user.username}!</h2>
            <p>Access your portal:</p>
            
            <div className="portal-links">
              {user.role === 'customer' && (
                <div className="portal-section">
                  <h3>Customer Portal</h3>
                  <div className="button-group">
                    <Link to="/customer/menu">
                      <Button variant="primary">Browse Menu</Button>
                    </Link>
                    <Link to="/customer/cart">
                      <Button variant="secondary">View Cart</Button>
                    </Link>
                    <Link to="/customer/orders">
                      <Button variant="secondary">Track Orders</Button>
                    </Link>
                  </div>
                  
                  <h3>In-Cafe Ordering</h3>
                  <div className="button-group">
                    <Link to="/in-cafe/menu">
                      <Button variant="primary">In-Cafe Menu</Button>
                    </Link>
                  </div>
                </div>
              )}
              
              {user.role === 'delivery' && (
                <div className="portal-section">
                  <h3>Delivery Portal</h3>
                  <Link to="/delivery/orders">
                    <Button variant="primary">View Assigned Orders</Button>
                  </Link>
                </div>
              )}
              
              {user.role === 'admin' && (
                <div className="portal-section">
                  <h3>Admin Panel</h3>
                  <div className="button-group">
                    <Link to="/admin/dashboard">
                      <Button variant="primary">Dashboard</Button>
                    </Link>
                    <Link to="/admin/orders">
                      <Button variant="secondary">Manage Orders</Button>
                    </Link>
                    <Link to="/admin/inventory">
                      <Button variant="secondary">Inventory</Button>
                    </Link>
                    <Link to="/admin/menu-items">
                      <Button variant="secondary">Menu Items</Button>
                    </Link>
                  </div>
                </div>
              )}
              
              {user.role === 'employee' && (
                <div className="portal-section">
                  <h3>Employee Portal</h3>
                  <div className="button-group">
                    <Link to="/employee/dashboard">
                      <Button variant="primary">Dashboard</Button>
                    </Link>
                    <Link to="/employee/orders">
                      <Button variant="secondary">View Orders</Button>
                    </Link>
                  </div>
                </div>
              )}
            </div>
          </div>
        )}
        </div>
        
        <div className="features-section">
          <h2>Our Services</h2>
          <div className="features-grid">
            <div className="feature-card">
              <h3>Online Ordering</h3>
              <p>Order your favorite coffee and food items for delivery</p>
            </div>
            <div className="feature-card">
              <h3>In-Cafe Ordering</h3>
              <p>Skip the queue with our digital in-cafe ordering system</p>
            </div>
            <div className="feature-card">
              <h3>Real-time Tracking</h3>
              <p>Track your order status from preparation to delivery</p>
            </div>
            <div className="feature-card">
              <h3>Quality Ingredients</h3>
              <p>Fresh, high-quality ingredients in every item</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default HomePage;