import React, { createContext, useContext, useState, useEffect } from 'react';
import pwaService from '../services/pwaService';

const PWAContext = createContext(null);

export const PWAProvider = ({ children }) => {
  const [isOnline, setIsOnline] = useState(navigator.onLine);
  const [isInstalled, setIsInstalled] = useState(false);
  const [canInstall, setCanInstall] = useState(false);
  const [hasServiceWorker, setHasServiceWorker] = useState(false);
  const [cacheInfo, setCacheInfo] = useState(null);
  const [offlineOrders, setOfflineOrders] = useState([]);

  useEffect(() => {
    // Initialize PWA service
    initializePWA();
    
    // Setup network listeners
    const handleOnline = () => setIsOnline(true);
    const handleOffline = () => setIsOnline(false);
    
    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);
    
    // Check for install prompt
    const handleBeforeInstallPrompt = (e) => {
      e.preventDefault();
      setCanInstall(true);
    };
    
    const handleAppInstalled = () => {
      setIsInstalled(true);
      setCanInstall(false);
    };
    
    window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
    window.addEventListener('appinstalled', handleAppInstalled);
    
    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
      window.removeEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
      window.removeEventListener('appinstalled', handleAppInstalled);
    };
  }, []);

  const initializePWA = async () => {
    try {
      // Wait for PWA service to initialize
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      const appInfo = pwaService.getAppInfo();
      setIsOnline(appInfo.isOnline);
      setIsInstalled(appInfo.isInstalled);
      setCanInstall(appInfo.canInstall);
      setHasServiceWorker(appInfo.hasServiceWorker);
      
      // Load cache info
      await updateCacheInfo();
      
      // Load offline orders
      await loadOfflineOrders();
    } catch (error) {
      console.error('PWA: Initialization failed:', error);
    }
  };

  const updateCacheInfo = async () => {
    try {
      const info = await pwaService.getCacheSize();
      setCacheInfo(info);
    } catch (error) {
      console.error('PWA: Failed to get cache info:', error);
    }
  };

  const loadOfflineOrders = async () => {
    try {
      const orders = await pwaService.getOfflineOrders();
      setOfflineOrders(orders);
    } catch (error) {
      console.error('PWA: Failed to load offline orders:', error);
    }
  };

  const installApp = async () => {
    try {
      const success = await pwaService.promptInstall();
      if (success) {
        setCanInstall(false);
        // Installation success will be handled by the appinstalled event
      }
      return success;
    } catch (error) {
      console.error('PWA: Install failed:', error);
      return false;
    }
  };

  const clearCache = async () => {
    try {
      await pwaService.clearCache();
      await updateCacheInfo();
      return true;
    } catch (error) {
      console.error('PWA: Clear cache failed:', error);
      return false;
    }
  };

  const saveOfflineOrder = async (order) => {
    try {
      await pwaService.saveOfflineOrder(order);
      await loadOfflineOrders();
      return true;
    } catch (error) {
      console.error('PWA: Save offline order failed:', error);
      return false;
    }
  };

  const subscribeToPushNotifications = async () => {
    try {
      const subscription = await pwaService.subscribeToPushNotifications();
      return subscription;
    } catch (error) {
      console.error('PWA: Push notification subscription failed:', error);
      return null;
    }
  };

  const showNotification = (message, type = 'info') => {
    pwaService.showNotification(message, type);
  };

  const getNetworkStatus = () => {
    return {
      isOnline,
      effectiveType: navigator.connection?.effectiveType || 'unknown',
      downlink: navigator.connection?.downlink || 0,
      rtt: navigator.connection?.rtt || 0
    };
  };

  const value = {
    // State
    isOnline,
    isInstalled,
    canInstall,
    hasServiceWorker,
    cacheInfo,
    offlineOrders,
    
    // Actions
    installApp,
    clearCache,
    saveOfflineOrder,
    subscribeToPushNotifications,
    showNotification,
    updateCacheInfo,
    loadOfflineOrders,
    getNetworkStatus,
    
    // Utils
    pwaService
  };

  return (
    <PWAContext.Provider value={value}>
      {children}
    </PWAContext.Provider>
  );
};

export const usePWA = () => {
  const context = useContext(PWAContext);
  if (!context) {
    throw new Error('usePWA must be used within a PWAProvider');
  }
  return context;
};

export default PWAContext;