import React, { useState } from 'react';
import { usePWA } from '../../../contexts/PWAContext';
import Button from '../Button/Button';
import './PWAStatus.css';

function PWAStatus() {
  const {
    isOnline,
    isInstalled,
    canInstall,
    hasServiceWorker,
    cacheInfo,
    offlineOrders,
    installApp,
    clearCache,
    updateCacheInfo,
    getNetworkStatus
  } = usePWA();

  const [showDetails, setShowDetails] = useState(false);
  const [isClearing, setIsClearing] = useState(false);

  const handleInstall = async () => {
    const success = await installApp();
    if (success) {
      console.log('App installation initiated');
    }
  };

  const handleClearCache = async () => {
    setIsClearing(true);
    const success = await clearCache();
    if (success) {
      console.log('Cache cleared successfully');
    }
    setIsClearing(false);
  };

  const networkStatus = getNetworkStatus();

  const formatBytes = (bytes) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  return (
    <div className="pwa-status">
      <div className="pwa-status-header">
        <div className="connection-indicator">
          <span className={`status-dot ${isOnline ? 'online' : 'offline'}`}></span>
          <span className="status-text">
            {isOnline ? 'Online' : 'Offline'}
          </span>
        </div>
        
        <button 
          className="details-toggle"
          onClick={() => setShowDetails(!showDetails)}
          aria-label="Toggle PWA details"
        >
          {showDetails ? '' : ''}
        </button>
      </div>

      {showDetails && (
        <div className="pwa-details">
          <div className="pwa-info-grid">
            <div className="pwa-info-item">
              <span className="info-label">App Status:</span>
              <span className={`info-value ${isInstalled ? 'installed' : 'browser'}`}>
                {isInstalled ? ' Installed' : ' Browser'}
              </span>
            </div>

            <div className="pwa-info-item">
              <span className="info-label">Service Worker:</span>
              <span className={`info-value ${hasServiceWorker ? 'active' : 'inactive'}`}>
                {hasServiceWorker ? ' Active' : ' Inactive'}
              </span>
            </div>

            {!isOnline && offlineOrders.length > 0 && (
              <div className="pwa-info-item">
                <span className="info-label">Offline Orders:</span>
                <span className="info-value warning">
                  {offlineOrders.length} pending
                </span>
              </div>
            )}

            {networkStatus.effectiveType !== 'unknown' && (
              <div className="pwa-info-item">
                <span className="info-label">Connection:</span>
                <span className="info-value">
                  {networkStatus.effectiveType.toUpperCase()}
                </span>
              </div>
            )}

            {cacheInfo && (
              <div className="pwa-info-item">
                <span className="info-label">Cache Size:</span>
                <span className="info-value">
                  {formatBytes(cacheInfo.used)} / {formatBytes(cacheInfo.available)}
                  <span className="cache-percentage">
                    ({cacheInfo.percentage}%)
                  </span>
                </span>
              </div>
            )}
          </div>

          <div className="pwa-actions">
            {canInstall && !isInstalled && (
              <Button
                variant="primary"
                size="small"
                onClick={handleInstall}
                className="pwa-action-btn"
              >
                 Install App
              </Button>
            )}

            <Button
              variant="outline"
              size="small"
              onClick={handleClearCache}
              disabled={isClearing}
              className="pwa-action-btn"
            >
              {isClearing ? ' Clearing...' : ' Clear Cache'}
            </Button>

            <Button
              variant="outline"
              size="small"
              onClick={updateCacheInfo}
              className="pwa-action-btn"
            >
               Refresh Info
            </Button>
          </div>

          {!isOnline && (
            <div className="offline-notice">
              <div className="offline-notice-content">
                <span className="offline-icon"></span>
                <div className="offline-text">
                  <strong>You're currently offline</strong>
                  <p>Some features may be limited. Your data will sync when you're back online.</p>
                </div>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
}

export default PWAStatus;