import api from './api';

const menuService = {
  getAllMenuItems: async (filters = {}) => {
    try {
      const queryParams = new URLSearchParams();
      
      // Ensure we only get available items for customers (unless explicitly overridden)
      if (filters.isAvailable === undefined) {
        filters.isAvailable = true;
      }
      
      // Add filters to query params
      Object.keys(filters).forEach(key => {
        if (filters[key] !== undefined && filters[key] !== null && filters[key] !== '') {
          queryParams.append(key, filters[key]);
        }
      });

      console.log('Fetching menu items with filters:', filters);
      const response = await api.get(`/menu?${queryParams.toString()}`);
      console.log('Menu items response:', response.data);
      
      // Transform menu items to ensure they have the correct structure
      const transformedItems = response.data.items.map(item => ({
        ...item,
        id: item.id || item._id, // Ensure id field exists
        price: typeof item.price === 'number' ? item.price : parseFloat(item.price) || 0,
        name: item.name || 'Unknown Item',
        isAvailable: item.isAvailable !== false
      }));
      
      return { success: true, data: transformedItems, pagination: response.data.pagination };
    } catch (error) {
      console.error('Error fetching menu items:', error.response?.data || error.message);
      throw error;
    }
  },

  getMenuItemById: async (id) => {
    try {
      const response = await api.get(`/menu/${id}`);
      return { success: true, data: response.data };
    } catch (error) {
      console.error(`Error fetching menu item ${id}:`, error.response?.data || error.message);
      throw error;
    }
  },

  getMenuItemsByCategory: async (category) => {
    try {
      const filters = category ? { category } : {};
      return await menuService.getAllMenuItems(filters);
    } catch (error) {
      console.error(`Error fetching menu items by category ${category}:`, error.response?.data || error.message);
      throw error;
    }
  },

  getCategories: async () => {
    try {
      const response = await api.get('/menu/categories/list');
      return { success: true, data: response.data.categories };
    } catch (error) {
      console.error('Error fetching categories:', error.response?.data || error.message);
      throw error;
    }
  },

  createMenuItem: async (itemData) => {
    try {
      const response = await api.post('/menu', itemData);
      return response.data;
    } catch (error) {
      console.error('Error creating menu item:', error.response?.data || error.message);
      throw error;
    }
  },

  updateMenuItem: async (id, itemData) => {
    try {
      const response = await api.put(`/menu/${id}`, itemData);
      return response.data;
    } catch (error) {
      console.error(`Error updating menu item ${id}:`, error.response?.data || error.message);
      throw error;
    }
  },

  deleteMenuItem: async (id) => {
    try {
      const response = await api.delete(`/menu/${id}`);
      return response.data;
    } catch (error) {
      console.error(`Error deleting menu item ${id}:`, error.response?.data || error.message);
      throw error;
    }
  },

  toggleMenuItemAvailability: async (id) => {
    try {
      const response = await api.patch(`/menu/${id}/availability`);
      return response.data;
    } catch (error) {
      console.error(`Error toggling availability for menu item ${id}:`, error.response?.data || error.message);
      throw error;
    }
  },

  // Admin-specific method to get all menu items (including unavailable ones)
  getAllMenuItemsForAdmin: async (filters = {}) => {
    try {
      const queryParams = new URLSearchParams();
      
      // Add filters to query params
      Object.keys(filters).forEach(key => {
        if (filters[key] !== undefined && filters[key] !== null && filters[key] !== '') {
          queryParams.append(key, filters[key]);
        }
      });

      console.log('Fetching admin menu items with filters:', filters);
      const response = await api.get(`/admin/menu?${queryParams.toString()}`);
      console.log('Admin menu items response:', response.data);
      
      return { success: true, data: response.data.items, pagination: response.data.pagination };
    } catch (error) {
      console.error('Error fetching admin menu items:', error.response?.data || error.message);
      throw error;
    }
  },
};

export default menuService;