import React, { useState, useEffect } from 'react';
import { usePWA } from '../contexts/PWAContext';
import Button from '../components/common/Button/Button';
import './PWATestPage.css';

function PWATestPage() {
  const {
    isOnline,
    isInstalled,
    hasServiceWorker,
    cacheInfo,
    offlineOrders,
    installApp,
    clearCache,
    updateCacheInfo,
    subscribeToPushNotifications,
    showNotification,
    getNetworkStatus
  } = usePWA();

  const [testResults, setTestResults] = useState({});
  const [isRunningTests, setIsRunningTests] = useState(false);

  useEffect(() => {
    updateCacheInfo();
  }, [updateCacheInfo]);

  const runPWATests = async () => {
    setIsRunningTests(true);
    const results = {};

    // Test 1: Service Worker Registration
    try {
      if ('serviceWorker' in navigator) {
        const registrations = await navigator.serviceWorker.getRegistrations();
        results.serviceWorker = {
          supported: true,
          registered: registrations.length > 0,
          count: registrations.length,
          status: 'pass'
        };
      } else {
        results.serviceWorker = {
          supported: false,
          status: 'fail',
          message: 'Service Workers not supported'
        };
      }
    } catch (error) {
      results.serviceWorker = {
        status: 'fail',
        message: error.message
      };
    }

    // Test 2: Manifest File
    try {
      const response = await fetch('/manifest.json');
      const manifest = await response.json();
      results.manifest = {
        status: response.ok ? 'pass' : 'fail',
        name: manifest.name,
        shortName: manifest.short_name,
        icons: manifest.icons?.length || 0,
        startUrl: manifest.start_url,
        display: manifest.display
      };
    } catch (error) {
      results.manifest = {
        status: 'fail',
        message: error.message
      };
    }

    // Test 3: Cache API
    try {
      if ('caches' in window) {
        const cacheNames = await caches.keys();
        results.cache = {
          supported: true,
          caches: cacheNames.length,
          names: cacheNames,
          status: 'pass'
        };
      } else {
        results.cache = {
          supported: false,
          status: 'fail',
          message: 'Cache API not supported'
        };
      }
    } catch (error) {
      results.cache = {
        status: 'fail',
        message: error.message
      };
    }

    // Test 4: Push Notifications
    try {
      if ('Notification' in window && 'serviceWorker' in navigator && 'PushManager' in window) {
        results.pushNotifications = {
          supported: true,
          permission: Notification.permission,
          status: 'pass'
        };
      } else {
        results.pushNotifications = {
          supported: false,
          status: 'fail',
          message: 'Push notifications not supported'
        };
      }
    } catch (error) {
      results.pushNotifications = {
        status: 'fail',
        message: error.message
      };
    }

    // Test 5: Install Prompt
    results.installPrompt = {
      canInstall: window.deferredPrompt !== null,
      isInstalled: isInstalled,
      status: isInstalled ? 'pass' : 'info'
    };

    // Test 6: Network Status
    const networkStatus = getNetworkStatus();
    results.network = {
      isOnline,
      effectiveType: networkStatus.effectiveType,
      downlink: networkStatus.downlink,
      rtt: networkStatus.rtt,
      status: 'pass'
    };

    setTestResults(results);
    setIsRunningTests(false);
    showNotification('PWA tests completed', 'success');
  };

  const formatBytes = (bytes) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'pass': return '';
      case 'fail': return '';
      case 'info': return '';
      default: return '';
    }
  };

  return (
    <div className="pwa-test-page">
      <div className="container">
        <div className="test-header">
          <h1>PWA Test Dashboard</h1>
          <p>Test and verify Progressive Web App functionality</p>
        </div>

        <div className="test-actions">
          <Button
            variant="primary"
            onClick={runPWATests}
            disabled={isRunningTests}
          >
            {isRunningTests ? ' Running Tests...' : ' Run PWA Tests'}
          </Button>
          <Button
            variant="outline"
            onClick={() => showNotification('Test notification', 'info')}
          >
             Test Notification
          </Button>
          <Button
            variant="outline"
            onClick={updateCacheInfo}
          >
             Refresh Cache Info
          </Button>
        </div>

        <div className="test-grid">
          {/* Current Status */}
          <div className="test-card">
            <h3> Current Status</h3>
            <div className="status-list">
              <div className="status-item">
                <span>Connection:</span>
                <span className={`status-badge ${isOnline ? 'online' : 'offline'}`}>
                  {isOnline ? ' Online' : ' Offline'}
                </span>
              </div>
              <div className="status-item">
                <span>Installation:</span>
                <span className={`status-badge ${isInstalled ? 'installed' : 'browser'}`}>
                  {isInstalled ? ' Installed' : ' Browser'}
                </span>
              </div>
              <div className="status-item">
                <span>Service Worker:</span>
                <span className={`status-badge ${hasServiceWorker ? 'active' : 'inactive'}`}>
                  {hasServiceWorker ? ' Active' : ' Inactive'}
                </span>
              </div>
            </div>
          </div>

          {/* Cache Information */}
          <div className="test-card">
            <h3> Cache Information</h3>
            {cacheInfo ? (
              <div className="cache-details">
                <div className="cache-stat">
                  <span>Used:</span>
                  <span>{formatBytes(cacheInfo.used)}</span>
                </div>
                <div className="cache-stat">
                  <span>Available:</span>
                  <span>{formatBytes(cacheInfo.available)}</span>
                </div>
                <div className="cache-stat">
                  <span>Usage:</span>
                  <span>{cacheInfo.percentage}%</span>
                </div>
                <div className="cache-progress">
                  <div 
                    className="cache-progress-bar"
                    style={{ width: `${cacheInfo.percentage}%` }}
                  ></div>
                </div>
              </div>
            ) : (
              <p>Cache information not available</p>
            )}
          </div>

          {/* Offline Orders */}
          <div className="test-card">
            <h3> Offline Orders</h3>
            {offlineOrders && offlineOrders.length > 0 ? (
              <div>
                <p><strong>{offlineOrders.length}</strong> orders pending sync</p>
                <div className="orders-list">
                  {offlineOrders.slice(0, 3).map((order, index) => (
                    <div key={index} className="order-item">
                      <span>{new Date(order.timestamp).toLocaleDateString()}</span>
                      <span>{order.items?.length || 0} items</span>
                    </div>
                  ))}
                </div>
              </div>
            ) : (
              <p>No offline orders pending</p>
            )}
          </div>

          {/* Test Results */}
          {Object.keys(testResults).length > 0 && (
            <div className="test-card full-width">
              <h3> Test Results</h3>
              <div className="test-results">
                {Object.entries(testResults).map(([testName, result]) => (
                  <div key={testName} className="test-result">
                    <div className="test-result-header">
                      <span className="test-icon">{getStatusIcon(result.status)}</span>
                      <span className="test-name">{testName.replace(/([A-Z])/g, ' $1').replace(/^./, str => str.toUpperCase())}</span>
                      <span className={`test-status ${result.status}`}>{result.status.toUpperCase()}</span>
                    </div>
                    <div className="test-result-details">
                      {result.message && <p className="test-message">{result.message}</p>}
                      {result.supported !== undefined && (
                        <p>Supported: {result.supported ? 'Yes' : 'No'}</p>
                      )}
                      {result.registered !== undefined && (
                        <p>Registered: {result.registered ? 'Yes' : 'No'}</p>
                      )}
                      {result.count !== undefined && (
                        <p>Count: {result.count}</p>
                      )}
                      {result.permission && (
                        <p>Permission: {result.permission}</p>
                      )}
                      {result.names && (
                        <p>Cache Names: {result.names.join(', ')}</p>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>

        <div className="test-actions-bottom">
          <Button
            variant="outline"
            onClick={() => clearCache()}
          >
             Clear Cache
          </Button>
          {!isInstalled && (
            <Button
              variant="primary"
              onClick={installApp}
            >
               Install App
            </Button>
          )}
          <Button
            variant="outline"
            onClick={() => window.open('/offline.html', '_blank')}
          >
             Test Offline Page
          </Button>
        </div>
      </div>
    </div>
  );
}

export default PWATestPage;