import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useAuth } from '../../../contexts/AuthContext';
import './DeliveryNavigation.css';

function DeliveryNavigation() {
  const location = useLocation();
  const { user, logout } = useAuth();

  const isActive = (path) => {
    return location.pathname === path;
  };

  return (
    <nav className="delivery-navigation">
      <div className="nav-container">
        <div className="nav-brand">
          <h2> Delivery Portal</h2>
          <span className="user-info">Welcome, {user?.username}</span>
        </div>
        
        <div className="nav-links">
          <Link 
            to="/delivery/dashboard" 
            className={`nav-link ${isActive('/delivery/dashboard') ? 'active' : ''}`}
          >
            <span className="nav-icon"></span>
            Dashboard
          </Link>
          
          <Link 
            to="/delivery/orders" 
            className={`nav-link ${isActive('/delivery/orders') ? 'active' : ''}`}
          >
            <span className="nav-icon"></span>
            My Orders
          </Link>
        </div>

        <div className="nav-actions">
          <button 
            onClick={logout}
            className="logout-btn"
            title="Logout"
          >
            <span className="nav-icon"></span>
            Logout
          </button>
        </div>
      </div>
    </nav>
  );
}

export default DeliveryNavigation;