import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import menuService from '../../services/menuService';
import orderService from '../../services/orderService';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import Button from '../../components/common/Button/Button';
import Input from '../../components/common/Input/Input';
import SimpleMenuCard from '../../components/common/SimpleMenuCard/SimpleMenuCard';
import Modal from '../../components/common/Modal/Modal';
import Toast from '../../components/common/Toast/Toast';
import { downloadInvoice } from '../../utils/invoiceGenerator';
import './EmployeeTakeOrderPage.css';

function EmployeeTakeOrderPage() {
  const [menuItems, setMenuItems] = useState([]);
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [searchTerm, setSearchTerm] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [cart, setCart] = useState([]);
  const [showCheckoutModal, setShowCheckoutModal] = useState(false);
  const [orderDetails, setOrderDetails] = useState({
    customerName: '',
    customerPhone: '',
    tableNumber: '',
    orderType: 'dine in',
    paymentMethod: 'cash',
    specialInstructions: ''
  });
  const [submitting, setSubmitting] = useState(false);
  const [toast, setToast] = useState({ show: false, message: '', type: 'success' });
  const [lastOrderData, setLastOrderData] = useState(null);

  const navigate = useNavigate();

  useEffect(() => {
    loadMenuData();
  }, []);

  const loadMenuData = async () => {
    try {
      setLoading(true);
      const [menuResult, categoriesResult] = await Promise.all([
        menuService.getAllMenuItems({ isAvailable: true }),
        menuService.getCategories()
      ]);

      if (menuResult.success) {
        console.log('Loaded menu items:', menuResult.data);
        setMenuItems(menuResult.data);
      }

      if (categoriesResult.success) {
        setCategories(['all', ...categoriesResult.data]);
      }
    } catch (err) {
      setError('Failed to load menu data. Please try again.');
      console.error('Menu loading error:', err);
    } finally {
      setLoading(false);
    }
  };

  const filteredMenuItems = menuItems.filter(item => {
    const matchesCategory = selectedCategory === 'all' || item.category === selectedCategory;
    const matchesSearch = item.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         item.description.toLowerCase().includes(searchTerm.toLowerCase());
    return matchesCategory && matchesSearch;
  });

  const addToCart = (item, quantity = 1) => {
    // Ensure item has required fields
    if (!item.id || !item.name || item.price === undefined) {
      showToast('Invalid item. Please refresh the page and try again.', 'error');
      return;
    }
    
    setCart(prevCart => {
      const existingItemIndex = prevCart.findIndex(cartItem => cartItem.id === item.id);
      if (existingItemIndex > -1) {
        const newCart = [...prevCart];
        newCart[existingItemIndex].quantity += quantity;
        return newCart;
      } else {
        return [...prevCart, { ...item, quantity }];
      }
    });
  };

  const removeFromCart = (itemId) => {
    setCart(prevCart => prevCart.filter(item => item.id !== itemId));
  };

  const updateCartQuantity = (itemId, newQuantity) => {
    if (newQuantity <= 0) {
      removeFromCart(itemId);
      return;
    }
    setCart(prevCart =>
      prevCart.map(item =>
        item.id === itemId ? { ...item, quantity: newQuantity } : item
      )
    );
  };

  const getCartTotal = () => {
    return cart.reduce((total, item) => total + (item.price * item.quantity), 0);
  };

  const getCartItemsCount = () => {
    return cart.reduce((total, item) => total + item.quantity, 0);
  };

  const handleCheckout = () => {
    if (cart.length === 0) {
      alert('Please add items to cart before checkout');
      return;
    }
    setShowCheckoutModal(true);
  };

  const showToast = (message, type = 'success') => {
    setToast({ show: true, message, type });
  };

  const hideToast = () => {
    setToast({ show: false, message: '', type: 'success' });
  };

  const handleOrderSubmit = async (e) => {
    e.preventDefault();
    
    if (!orderDetails.customerName.trim()) {
      showToast('Please enter customer name', 'error');
      return;
    }

    if (orderDetails.orderType === 'dine in' && !orderDetails.tableNumber.trim()) {
      showToast('Please enter table number for dine in orders', 'error');
      return;
    }

    try {
      setSubmitting(true);
      
      // Validate cart items have valid IDs
      console.log('Cart before validation:', cart);
      const validCartItems = cart.filter(item => {
        const hasId = item.id || item._id;
        const hasName = item.name;
        const hasPrice = item.price !== undefined && item.price !== null && !isNaN(parseFloat(item.price));
        console.log(`Item ${item.name}: hasId=${!!hasId}, hasName=${!!hasName}, hasPrice=${!!hasPrice}, price=${item.price}`);
        return hasId && hasName && hasPrice;
      });
      
      console.log('Valid cart items:', validCartItems);
      
      if (validCartItems.length === 0) {
        showToast('Cart contains invalid items. Please refresh and try again.', 'error');
        return;
      }

      const orderData = {
        items: validCartItems.map(item => ({
          id: item.id || item._id,
          menuItem: item.id || item._id,
          quantity: item.quantity,
          specialInstructions: ''
        })),
        orderType: orderDetails.orderType,
        tableNumber: orderDetails.orderType === 'dine in' ? orderDetails.tableNumber.trim() : null,
        paymentMethod: orderDetails.paymentMethod,
        customerName: orderDetails.customerName.trim(),
        customerPhone: orderDetails.customerPhone.trim(),
        customerEmail: '', // Add empty email to avoid validation issues
        specialInstructions: orderDetails.specialInstructions.trim()
      };

      console.log('Placing order with data:', orderData);
      const result = await orderService.placeAdminOrder(orderData);
      
      if (result.success) {
        // Prepare order data for invoice
        const invoiceData = {
          id: result.data.id,
          customerName: orderDetails.customerName,
          customerPhone: orderDetails.customerPhone,
          items: validCartItems.map(item => ({
            name: item.name,
            price: item.price,
            quantity: item.quantity
          })),
          orderType: orderDetails.orderType,
          tableNumber: orderDetails.tableNumber,
          paymentMethod: orderDetails.paymentMethod,
          total: getCartTotal(),
          timestamp: new Date().toISOString()
        };
        
        setLastOrderData(invoiceData);
        showToast(`Order placed successfully! Order ID: ${result.data.id}`, 'success');
        
        // Reset form
        setCart([]);
        setOrderDetails({
          customerName: '',
          customerPhone: '',
          tableNumber: '',
          orderType: 'dine in',
          paymentMethod: 'cash',
          specialInstructions: ''
        });
        setShowCheckoutModal(false);
        
        // Navigate to orders page after a short delay
        setTimeout(() => {
          navigate('/employee/orders');
        }, 2000);
      } else {
        showToast(`Failed to place order: ${result.error}`, 'error');
      }
    } catch (err) {
      console.error('Order submission error:', err);
      showToast('Failed to place order. Please try again.', 'error');
    } finally {
      setSubmitting(false);
    }
  };

  const handleDownloadInvoice = () => {
    if (lastOrderData) {
      downloadInvoice(lastOrderData);
      showToast('Invoice downloaded successfully!', 'success');
    }
  };

  if (loading) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-loading">
            <LoadingSpinner size="large" />
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-error">
            <h1>Error Loading Menu</h1>
            <p>{error}</p>
            <Button onClick={loadMenuData} variant="primary">
              Try Again
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="employee-take-order-page">
      <div className="container">
        <div className="take-order-header">
          <div className="take-order-title-section">
            <h1>Take Order</h1>
            <p>Quick order creation for dine in and take away customers</p>
          </div>
          
          <div className="take-order-controls">
            <Button
              variant="outline"
              onClick={() => navigate('/employee/dashboard')}
            >
               Back to Dashboard
            </Button>
            
            {lastOrderData && (
              <Button
                variant="outline"
                onClick={handleDownloadInvoice}
                className="download-invoice-btn"
              >
                 Download Last Invoice
              </Button>
            )}
            
            {cart.length > 0 && (
              <Button
                variant="primary"
                onClick={handleCheckout}
                className="checkout-btn"
              >
                Place Order ({getCartItemsCount()} items) - {getCartTotal().toFixed(2)}
              </Button>
            )}
          </div>
        </div>

        <div className="take-order-content">
          <div className="menu-section">
            <div className="menu-filters">
              <div className="search-filter">
                <Input
                  type="text"
                  placeholder="Search menu items..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="search-input"
                />
              </div>
            </div>

            {/* Category Tabs */}
            <div className="category-tabs">
              {categories.map(category => (
                <button
                  key={category}
                  className={`category-tab ${selectedCategory === category ? 'category-tab--active' : ''}`}
                  onClick={() => setSelectedCategory(category)}
                >
                  {category === 'all' ? 'All Items' : category}
                </button>
              ))}
            </div>

            {/* Simplified Menu List */}
            <div className="simple-menu-list">
              {filteredMenuItems.map(item => {
                const cartItem = cart.find(c => c.id === item.id);
                const quantity = cartItem?.quantity || 0;
                
                return (
                  <SimpleMenuCard
                    key={item.id}
                    item={item}
                    quantity={quantity}
                    onAdd={addToCart}
                    onQuantityChange={updateCartQuantity}
                  />
                );
              })}
            </div>

            {filteredMenuItems.length === 0 && (
              <div className="no-items">
                <p>No menu items found matching your criteria.</p>
              </div>
            )}
          </div>

          {cart.length > 0 && (
            <div className="cart-sidebar">
              <div className="cart-header">
                <h3>Current Order ({getCartItemsCount()} items)</h3>
                <Button
                  variant="outline"
                  size="small"
                  onClick={() => setCart([])}
                >
                  Clear All
                </Button>
              </div>
              
              <div className="cart-items">
                {cart.map(item => (
                  <div key={item.id} className="cart-item">
                    <div className="cart-item-info">
                      <h4>{item.name}</h4>
                      <p>{item.price.toFixed(2)} each</p>
                    </div>
                    <div className="cart-item-controls">
                      <div className="quantity-controls">
                        <button
                          className="quantity-btn"
                          onClick={() => updateCartQuantity(item.id, item.quantity - 1)}
                        >
                          -
                        </button>
                        <span>{item.quantity}</span>
                        <button
                          className="quantity-btn"
                          onClick={() => updateCartQuantity(item.id, item.quantity + 1)}
                        >
                          +
                        </button>
                      </div>
                      <button
                        className="remove-btn"
                        onClick={() => removeFromCart(item.id)}
                      >
                        
                      </button>
                    </div>
                    <div className="cart-item-total">
                      {(item.price * item.quantity).toFixed(2)}
                    </div>
                  </div>
                ))}
              </div>
              
              <div className="cart-total">
                <div className="total-line">
                  <strong>Total: {getCartTotal().toFixed(2)}</strong>
                </div>
                <Button
                  variant="primary"
                  onClick={handleCheckout}
                  className="place-order-btn"
                  size="large"
                >
                  Place Order
                </Button>
              </div>
            </div>
          )}
        </div>

        {/* Checkout Modal */}
        <Modal
          isOpen={showCheckoutModal}
          onClose={() => setShowCheckoutModal(false)}
          title="Complete Order"
        >
          <form onSubmit={handleOrderSubmit} className="checkout-form">
            <div className="form-group">
              <label htmlFor="customerName">Customer Name *</label>
              <Input
                id="customerName"
                type="text"
                value={orderDetails.customerName}
                onChange={(e) => setOrderDetails(prev => ({ ...prev, customerName: e.target.value }))}
                required
              />
            </div>

            <div className="form-group">
              <label htmlFor="customerPhone">Customer Phone</label>
              <Input
                id="customerPhone"
                type="tel"
                value={orderDetails.customerPhone}
                onChange={(e) => setOrderDetails(prev => ({ ...prev, customerPhone: e.target.value }))}
              />
            </div>

            <div className="form-group">
              <label htmlFor="orderType">Order Type</label>
              <select
                id="orderType"
                value={orderDetails.orderType}
                onChange={(e) => setOrderDetails(prev => ({ ...prev, orderType: e.target.value }))}
                className="form-select"
              >
                <option value="dine in">Dine In</option>
                <option value="take away">Take Away</option>
              </select>
            </div>

            {orderDetails.orderType === 'dine in' && (
              <div className="form-group">
                <label htmlFor="tableNumber">Table Number *</label>
                <Input
                  id="tableNumber"
                  type="text"
                  value={orderDetails.tableNumber}
                  onChange={(e) => setOrderDetails(prev => ({ ...prev, tableNumber: e.target.value }))}
                  required
                />
              </div>
            )}

            <div className="form-group">
              <label htmlFor="paymentMethod">Payment Method</label>
              <select
                id="paymentMethod"
                value={orderDetails.paymentMethod}
                onChange={(e) => setOrderDetails(prev => ({ ...prev, paymentMethod: e.target.value }))}
                className="form-select"
              >
                <option value="cash">Cash</option>
                <option value="card">Card</option>
                <option value="digital">Digital Payment</option>
              </select>
            </div>

            <div className="form-group">
              <label htmlFor="specialInstructions">Special Instructions</label>
              <textarea
                id="specialInstructions"
                value={orderDetails.specialInstructions}
                onChange={(e) => setOrderDetails(prev => ({ ...prev, specialInstructions: e.target.value }))}
                className="form-textarea"
                rows="3"
              />
            </div>

            <div className="order-summary">
              <h4>Order Summary</h4>
              {cart.map(item => (
                <div key={item.id} className="summary-item">
                  <span>{item.quantity}x {item.name}</span>
                  <span>{(item.price * item.quantity).toFixed(2)}</span>
                </div>
              ))}
              <div className="summary-total">
                <strong>Total: {getCartTotal().toFixed(2)}</strong>
              </div>
            </div>

            <div className="modal-actions">
              <Button
                type="button"
                variant="outline"
                onClick={() => setShowCheckoutModal(false)}
                disabled={submitting}
              >
                Cancel
              </Button>
              <Button
                type="submit"
                variant="primary"
                disabled={submitting}
              >
                {submitting ? 'Placing Order...' : 'Place Order'}
              </Button>
            </div>
          </form>
        </Modal>

        {/* Toast Notification */}
        <Toast
          message={toast.message}
          type={toast.type}
          isVisible={toast.show}
          onClose={hideToast}
        />
      </div>
    </div>
  );
}

export default EmployeeTakeOrderPage;