import React from 'react';
import { Routes, Route } from 'react-router-dom';
import './App.css';
import './styles/mobile-enhancements.css';

// Layout Components
import Header from './components/layout/Header/Header';
import Footer from './components/layout/Footer/Footer';
// import Navbar from './components/layout/Navbar/Navbar';
import PrivateRoute from './components/layout/PrivateRoute/PrivateRoute';
import PWAStatus from './components/common/PWAStatus/PWAStatus';
import NotificationBanner from './components/common/NotificationBanner/NotificationBanner';

// Import placeholder pages
import HomePage from './pages/HomePage';
import NotFoundPage from './pages/NotFoundPage';
import Login from './pages/Auth/Login';
import AdminLogin from './pages/Auth/AdminLogin';
import EmployeeLogin from './pages/Auth/EmployeeLogin';
import CustomerLogin from './pages/Auth/CustomerLogin';
import DeliveryLogin from './pages/Auth/DeliveryLogin';
import Register from './pages/Auth/Register';
import MenuPage from './pages/CustomerPortal/MenuPage';
import CartPage from './pages/CustomerPortal/CartPage';
import CheckoutPage from './pages/CustomerPortal/CheckoutPage';
import OrderTrackingPage from './pages/CustomerPortal/OrderTrackingPage';
import InCafeMenuPage from './pages/InCafeCustomerPortal/InCafeMenuPage';
import InCafeCheckoutPage from './pages/InCafeCustomerPortal/InCafeCheckoutPage';
import AssignedOrdersPage from './pages/DeliveryPersonPortal/AssignedOrdersPage';
import DeliveryDashboard from './pages/DeliveryPersonPortal/DeliveryDashboard';
import DashboardPage from './pages/AdminPanel/DashboardPage';
import OrdersManagementPage from './pages/AdminPanel/OrdersManagementPage';
import InventoryManagementPage from './pages/AdminPanel/InventoryManagementPage';
import CustomerAnalyticsPage from './pages/AdminPanel/CustomerAnalyticsPage';
import MenuItemsManagementPage from './pages/AdminPanel/MenuItemsManagementPage';
import SettingsPage from './pages/AdminPanel/SettingsPage';
import EmployeeManagementPage from './pages/AdminPanel/EmployeeManagementPage';
import TakeOrderPage from './pages/AdminPanel/TakeOrderPage';
import EmployeeDashboardPage from './pages/EmployeeAccessPortal/EmployeeDashboardPage';
import EmployeeOrdersPage from './pages/EmployeeAccessPortal/EmployeeOrdersPage';
import EmployeeTakeOrderPage from './pages/EmployeeAccessPortal/EmployeeTakeOrderPage';
import PWATestPage from './pages/PWATestPage';


function App() {
  return (
    <div className="App">
      <NotificationBanner />
      <PWAStatus />
      <Header />
      {/* <Navbar /> */}
      <main>
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/login" element={<Login />} />
          <Route path="/admin/login" element={<AdminLogin />} />
          <Route path="/employee/login" element={<EmployeeLogin />} />
          <Route path="/customer/login" element={<CustomerLogin />} />
          <Route path="/delivery/login" element={<DeliveryLogin />} />
          <Route path="/register" element={<Register />} />

          {/* Customer Portal Routes (Protected) */}
          <Route element={<PrivateRoute allowedRoles={['customer']} />}>
            <Route path="/customer/menu" element={<MenuPage />} />
            <Route path="/customer/cart" element={<CartPage />} />
            <Route path="/customer/checkout" element={<CheckoutPage />} />
            <Route path="/customer/orders" element={<OrderTrackingPage />} />
          </Route>

          {/* In-Cafe Customer Portal Routes (Protected - assuming in-cafe customers also need to be logged in) */}
          <Route element={<PrivateRoute allowedRoles={['customer']} />}>
            <Route path="/in-cafe/menu" element={<InCafeMenuPage />} />
            <Route path="/in-cafe/checkout" element={<InCafeCheckoutPage />} />
          </Route>

          {/* Delivery Person Portal Routes (Protected) */}
          <Route element={<PrivateRoute allowedRoles={['delivery']} />}>
            <Route path="/delivery/dashboard" element={<DeliveryDashboard />} />
            <Route path="/delivery/orders" element={<AssignedOrdersPage />} />
          </Route>

          {/* Admin Panel Routes (Protected) */}
          <Route element={<PrivateRoute allowedRoles={['admin']} />}>
            <Route path="/admin/dashboard" element={<DashboardPage />} />
            <Route path="/admin/orders" element={<OrdersManagementPage />} />
            <Route path="/admin/take-order" element={<TakeOrderPage />} />
            <Route path="/admin/inventory" element={<InventoryManagementPage />} />
            <Route path="/admin/customers" element={<CustomerAnalyticsPage />} />
            <Route path="/admin/menu-items" element={<MenuItemsManagementPage />} />
            <Route path="/admin/settings" element={<SettingsPage />} />
            <Route path="/admin/employees" element={<EmployeeManagementPage />} />
            <Route path="/admin/pwa-test" element={<PWATestPage />} />
          </Route>

          {/* Employee Access Portal Routes (Protected) */}
          <Route element={<PrivateRoute allowedRoles={['employee', 'admin']} />}> {/* Admin can also access employee views */}
            <Route path="/employee/dashboard" element={<EmployeeDashboardPage />} />
            <Route path="/employee/orders" element={<EmployeeOrdersPage />} />
            <Route path="/employee/take-order" element={<EmployeeTakeOrderPage />} />
          </Route>

          {/* Catch-all for 404 */}
          <Route path="*" element={<NotFoundPage />} />
        </Routes>
      </main>
      <Footer />
    </div>
  );
}

export default App;
