import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { useOrder } from '../../contexts/OrderContext';
import { useAuth } from '../../contexts/AuthContext';
import LoadingSpinner from '../../components/common/LoadingSpinner/LoadingSpinner';
import Button from '../../components/common/Button/Button';
import { downloadInvoice } from '../../utils/invoiceGeneratorCustomer'; 
import jsPDF from 'jspdf';
import './OrderTrackingPage.css';

function OrderTrackingPage() {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedOrder, setSelectedOrder] = useState(null);
  
  const { fetchOrders, currentOrder } = useOrder();
  const { user } = useAuth();
  const location = useLocation();

  useEffect(() => {
    loadOrders();
    
    // Check if we just placed an order
    if (location.state?.orderPlaced) {
      // Show success message or highlight the new order
      setTimeout(() => {
        window.history.replaceState({}, document.title);
      }, 5000);
    }
  }, [location.state]);

  const loadOrders = async () => {
    try {
      setLoading(true);
      const result = await fetchOrders(user.id, user.role);
      if (result.success) {
        setOrders(result.orders);
        // If there's a current order, select it by default
        if (currentOrder) {
          setSelectedOrder(currentOrder);
        } else if (result.orders.length > 0) {
          setSelectedOrder(result.orders[0]);
        }
      }
    } catch (err) {
      setError('Failed to load orders. Please try again.');
      console.error('Error loading orders:', err);
    } finally {
      setLoading(false);
    }
  };

  const getOrderStatusInfo = (status) => {
    const statusMap = {
      'Pending': {
        icon: '',
        color: '#6B7280',
        description: 'Your order is being processed'
      },
      'Order Placed': {
        icon: '',
        color: '#3B82F6',
        description: 'Your order has been received and is being prepared'
      },
      'Cooking in Progress': {
        icon: '',
        color: '#F59E0B',
        description: 'Our chefs are preparing your delicious order'
      },
      'Ready for Pickup': {
        icon: '',
        color: '#10B981',
        description: 'Your order is ready for pickup'
      },
      'Out for Delivery': {
        icon: '',
        color: '#8B5CF6',
        description: 'Your order is on its way to you'
      },
      'Delivered': {
        icon: '',
        color: '#059669',
        description: 'Your order has been delivered. Enjoy!'
      },
      'Completed': {
        icon: '',
        color: '#059669',
        description: 'Order completed successfully'
      }
    };
    
    return statusMap[status] || {
      icon: '',
      color: '#6B7280',
      description: 'Your order is cancelled'
    };
  };

  const formatOrderTime = (timestamp) => {
    const date = new Date(timestamp);
    return date.toLocaleString();
  };

  const calculateEstimatedTime = (order) => {
    if (order.status === 'Delivered' || order.status === 'Completed') {
      return 'Completed';
    }
    
    const orderTime = new Date(order.timestamp);
    const estimatedMinutes = order.orderType === 'delivery' ? 35 : 15;
    const estimatedTime = new Date(orderTime.getTime() + estimatedMinutes * 60000);
    
    if (new Date() > estimatedTime) {
      return 'Should be ready soon';
    }
    
    return `Est. ${estimatedTime.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}`;
  };


  if (loading) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-loading">
            <LoadingSpinner size="large" />
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="page-wrapper">
        <div className="container">
          <div className="page-error">
            <h1>Error Loading Orders</h1>
            <p>{error}</p>
            <Button onClick={loadOrders} variant="primary">
              Try Again
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="order-tracking-page">
      <div className="container">
        <div className="tracking-header">
          <div className="header-top">
            <h1>Your Orders</h1>
            <Button 
              onClick={loadOrders} 
              variant="secondary" 
              size="small"
              disabled={loading}
              className="refresh-btn"
            >
              <span className={loading ? 'refresh-icon' : ''}></span> Refresh
            </Button>
          </div>
          {location.state?.orderPlaced && (
            <div className="order-placed-notice">
              <span className="success-icon"></span>
              <div>
                <div className="notice-title">Order Placed Successfully!</div>
                <div className="notice-subtitle">
                  Order #{location.state.orderId} is being prepared
                </div>
              </div>
            </div>
          )}
        </div>

        {orders.length === 0 ? (
          <div className="no-orders">
            <div className="no-orders-icon"></div>
            <h2>No orders yet</h2>
            <p>When you place an order, you'll be able to track it here.</p>
            <Button onClick={() => window.location.href = '/customer/menu'} variant="primary">
              Browse Menu
            </Button>
          </div>
        ) : (
          <div className="tracking-content">
            <div className="orders-list">
              <h2>Recent Orders</h2>
              {orders.map(order => {
                const statusInfo = getOrderStatusInfo(order.status);
                return (
                  <div 
                    key={order.id} 
                    className={`order-card ${selectedOrder?.id === order.id ? 'selected' : ''}`}
                    onClick={() => setSelectedOrder(order)}
                  >
                    <div className="order-card-header">
                      <div className="order-info">
                        <div className="order-id">Order #{order.id}</div>
                        <div className="order-time">{formatOrderTime(order.timestamp)}</div>
                      </div>
                      <div className="order-status" style={{ color: statusInfo.color }}>
                        <span className="status-icon">{statusInfo.icon}</span>
                        <span className="status-text">{order.status}</span>
                      </div>
                    </div>
                    
                    <div className="order-summary">
                      <div className="order-items-count">
                        {order.items.length} item{order.items.length !== 1 ? 's' : ''}
                      </div>
                      <div className="order-total">{order.total.toFixed(2)}</div>
                    </div>
                    
                    <div className="order-type-badge">
                      {order.type === 'online' ? ' Delivery' : ' Pickup'}
                    </div>
                  </div>
                );
              })}
            </div>

            {selectedOrder && (
              <div className="order-details">
                <div className="order-details-header">
                  <h2>Order #{selectedOrder.id}</h2>
                  <div className="estimated-time">
                    {calculateEstimatedTime(selectedOrder)}
                  </div>
                </div>

                <div className="order-status-timeline">
                  <div className="current-status">
                    <div className="status-icon-large" style={{ color: getOrderStatusInfo(selectedOrder.status).color }}>
                      {getOrderStatusInfo(selectedOrder.status).icon}
                    </div>
                    <div className="status-info">
                      <div className="status-title">{selectedOrder.status}</div>
                      <div className="status-description">
                        {getOrderStatusInfo(selectedOrder.status).description}
                      </div>
                    </div>
                  </div>

                  <div className="status-timeline">
                   {(() => {
                      const getTimelineStatuses = (orderType) => {
                        if (orderType === 'delivery' || orderType === 'online') {
                          return ['Order Placed', 'Cooking in Progress', 'Out for Delivery', 'Delivered'];
                        } else if (orderType === 'pickup') {
                          return ['Order Placed', 'Cooking in Progress', 'Ready for Pickup', 'Completed'];
                        } else if (orderType === 'dine in') {
                          return ['Order Placed', 'Cooking in Progress', 'Ready for Pickup', 'Completed'];
                        } else {
                          return ['Order Placed', 'Cooking in Progress', 'Ready for Pickup', 'Completed'];
                        }
                      };
                      return getTimelineStatuses(selectedOrder.type);
                    })().map((status, index) => {
                      const isCompleted = ['Order Placed', 'Cooking in Progress'].includes(selectedOrder.status) ? 
                        index <= ['Order Placed', 'Cooking in Progress'].indexOf(selectedOrder.status) :
                        index <= 3;
                      const isCurrent = status === selectedOrder.status;
                      
                      return (
                        <div key={status} className={`timeline-item ${isCompleted ? 'completed' : ''} ${isCurrent ? 'current' : ''}`}>
                          <div className="timeline-dot"></div>
                          <div className="timeline-content">
                            <div className="timeline-status">{status}</div>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                </div>

                <div className="order-items-detail">
                  <h3>Order Items</h3>
                  {selectedOrder.items.map((item, index) => (
                    <div key={index} className="order-item-detail">
                      <div className="item-info">
                        <span className="item-name">{item.name}</span>
                        <span className="item-quantity">x{item.quantity}</span>
                      </div>
                      <span className="item-price">{(item.price * item.quantity).toFixed(2)}</span>
                    </div>
                  ))}
                </div>

                {selectedOrder.deliveryAddress && (
                  <div className="delivery-info-detail">
                    <h3>Delivery Address</h3>
                    <p>{selectedOrder.deliveryAddress}</p>
                  </div>
                )}

                {selectedOrder.tableNumber && (
                  <div className="pickup-info-detail">
                    <h3>Pickup Information</h3>
                    <p>Table #{selectedOrder.tableNumber}</p>
                  </div>
                )}

                <div className="order-actions">
                  <Button 
                    onClick={() => {
                      if (selectedOrder) {
                        // Ensure customer name is available for invoice
                        const orderForInvoice = {
                          ...selectedOrder,
                          customerName: selectedOrder.customerName === 'Unknown Customer' 
                            ? user.name || 'Customer' 
                            : selectedOrder.customerName
                        };
                        downloadInvoice(orderForInvoice);
                      }
                    }}
                    variant="secondary"
                    size="medium"
                    className="download-invoice-btn"
                  >
                     Download Invoice
                  </Button>
                  <Button 
                    onClick={loadOrders} 
                    variant="outline"
                    size="medium"
                    disabled={loading}
                    className="refresh-order-btn"
                  >
                    <span className={loading ? 'refresh-icon' : ''}></span> Refresh Status
                  </Button>
                </div>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}

export default OrderTrackingPage;