// Mock backend service for handling push notifications
// In a real app, this would be handled by your actual backend

class MockNotificationBackend {
  constructor() {
    this.subscriptions = new Map(); // Store user subscriptions
    this.vapidKeys = {
      publicKey: 'BEl62iUYgUivxIkv69yViEuiBIa40HI8YlOU2AwJ3_Q-1VdVeCOUHw9-8YuOdqiUaEXaEpC_colMJTpHR9-NfQ',
      privateKey: 'your-private-vapid-key-here' // In real app, keep this secret on server
    };
  }

  // Store user subscription
  async storeSubscription(userId, userRole, subscription) {
    console.log('Mock Backend: Storing subscription for user', userId, userRole);
    
    this.subscriptions.set(userId, {
      subscription,
      userRole,
      userId,
      timestamp: new Date().toISOString()
    });

    // Store in localStorage for persistence in this mock
    const allSubscriptions = JSON.parse(localStorage.getItem('mock_push_subscriptions') || '{}');
    allSubscriptions[userId] = {
      subscription,
      userRole,
      userId,
      timestamp: new Date().toISOString()
    };
    localStorage.setItem('mock_push_subscriptions', JSON.stringify(allSubscriptions));

    return { success: true, message: 'Subscription stored successfully' };
  }

  // Remove user subscription
  async removeSubscription(userId) {
    console.log('Mock Backend: Removing subscription for user', userId);
    
    this.subscriptions.delete(userId);

    // Remove from localStorage
    const allSubscriptions = JSON.parse(localStorage.getItem('mock_push_subscriptions') || '{}');
    delete allSubscriptions[userId];
    localStorage.setItem('mock_push_subscriptions', JSON.stringify(allSubscriptions));

    return { success: true, message: 'Subscription removed successfully' };
  }

  // Send notification to specific roles
  async sendNotificationToRoles(notificationData, targetRoles = ['admin', 'employee']) {
    console.log('Mock Backend: Sending notification to roles', targetRoles, notificationData);

    // Load subscriptions from localStorage
    const allSubscriptions = JSON.parse(localStorage.getItem('mock_push_subscriptions') || '{}');
    
    // Find subscriptions for target roles
    const targetSubscriptions = Object.values(allSubscriptions).filter(
      sub => targetRoles.includes(sub.userRole)
    );

    console.log(`Mock Backend: Found ${targetSubscriptions.length} subscriptions for roles:`, targetRoles);

    // In a real app, you would send push notifications via web push protocol
    // For this mock, we'll simulate by showing browser notifications
    for (const sub of targetSubscriptions) {
      try {
        await this.simulatePushNotification(sub.subscription, notificationData);
      } catch (error) {
        console.error('Mock Backend: Failed to send notification to user', sub.userId, error);
      }
    }

    return { 
      success: true, 
      message: `Notifications sent to ${targetSubscriptions.length} users`,
      sentCount: targetSubscriptions.length
    };
  }

  // Send notification to specific user
  async sendNotificationToUser(userId, notificationData) {
    console.log('Mock Backend: Sending notification to user', userId, notificationData);

    const allSubscriptions = JSON.parse(localStorage.getItem('mock_push_subscriptions') || '{}');
    const userSubscription = allSubscriptions[userId];

    if (!userSubscription) {
      console.warn('Mock Backend: No subscription found for user', userId);
      return { success: false, message: 'User not subscribed to notifications' };
    }

    try {
      await this.simulatePushNotification(userSubscription.subscription, notificationData);
      return { success: true, message: 'Notification sent successfully' };
    } catch (error) {
      console.error('Mock Backend: Failed to send notification to user', userId, error);
      return { success: false, message: 'Failed to send notification' };
    }
  }

  // Simulate push notification (in real app, this would use web-push library on server)
  async simulatePushNotification(subscription, notificationData) {
    // In a real implementation, you would use the web-push library to send
    // the notification to the subscription endpoint
    
    // For this mock, we'll trigger a local notification if the user is online
    // and has the page open, or store it for when they come back online
    
    console.log('Mock Backend: Simulating push notification', notificationData);
    
    // Try to send message to service worker if available
    if ('serviceWorker' in navigator && navigator.serviceWorker.controller) {
      navigator.serviceWorker.controller.postMessage({
        type: 'MOCK_PUSH_NOTIFICATION',
        data: notificationData
      });
    }

    // Also show a browser notification if permission is granted
    if ('Notification' in window && Notification.permission === 'granted') {
      const { title, body, type, orderData } = notificationData;
      
      let notificationTitle = title;
      let notificationBody = body;
      
      if (type === 'new_order' && orderData) {
        notificationTitle = ` New Order #${orderData.id}`;
        notificationBody = `${orderData.customerName} placed an order for $${orderData.total}`;
      } else if (type === 'order_status' && orderData) {
        notificationTitle = ` Order Update #${orderData.id}`;
        notificationBody = `Your order is now ${orderData.status}`;
      }

      new Notification(notificationTitle, {
        body: notificationBody,
        icon: '/icons/icon-192x192.png',
        badge: '/icons/icon-96x96.png',
        tag: `${type}-${orderData?.id || Date.now()}`,
        requireInteraction: true
      });
    }

    return true;
  }

  // Get all subscriptions (for admin purposes)
  async getAllSubscriptions() {
    const allSubscriptions = JSON.parse(localStorage.getItem('mock_push_subscriptions') || '{}');
    return {
      success: true,
      subscriptions: Object.values(allSubscriptions),
      count: Object.keys(allSubscriptions).length
    };
  }
}

// Create singleton instance
const mockNotificationBackend = new MockNotificationBackend();

// Make available globally for debugging
window.mockNotificationBackend = mockNotificationBackend;

export default mockNotificationBackend;