import React from 'react';
import './Input.css';

function Input({ 
  label, 
  id, 
  type = 'text', 
  value, 
  onChange, 
  placeholder, 
  className = '', 
  error,
  helpText,
  required = false,
  size = 'medium',
  ...props 
}) {
  const inputClasses = [
    'input',
    `input--${size}`,
    error && 'input--error',
    type === 'select' && 'select',
    type === 'textarea' && 'textarea',
    className
  ].filter(Boolean).join(' ');

  const groupClasses = [
    'input-group',
    label && 'has-label',
    type === 'checkbox' && 'input-group--checkbox',
    type === 'radio' && 'input-group--radio'
  ].filter(Boolean).join(' ');

  const InputElement = type === 'textarea' ? 'textarea' : 
                      type === 'select' ? 'select' : 'input';

  return (
    <div className={groupClasses}>
      {label && type !== 'checkbox' && type !== 'radio' && (
        <label htmlFor={id} className={required ? 'required' : ''}>
          {label}
        </label>
      )}
      
      <InputElement
        type={type === 'select' || type === 'textarea' ? undefined : type}
        id={id}
        value={value}
        onChange={onChange}
        placeholder={placeholder}
        className={inputClasses}
        required={required}
        {...props}
      />
      
      {label && (type === 'checkbox' || type === 'radio') && (
        <label htmlFor={id} className={required ? 'required' : ''}>
          {label}
        </label>
      )}
      
      {error && <div className="input-group__error">{error}</div>}
      {helpText && !error && <div className="input-group__help">{helpText}</div>}
    </div>
  );
}

export default Input;